module.exports=[{"Symbol":"MMM","Name":"3M Company","Sector":"Industrials","Description":"3M Company (3M) is a diversified technology company. The Company operates in six segments: industrial and transportation; healthcare; consumer and office; safety, security and protection services; display and graphics, and electro and communications businesses. 3M products are sold through a number of distribution channels, including directly to users and through wholesalers, retailers, jobbers, distributors and dealers in a range of trades in a number of countries worldwide. In April 2012, it acquired CodeRyte Inc. In September 2012, it acquired the business of Federal Signal Technologies Group (FSTech) from Federal Signal Corporation. On November 28, 2012, the Company acquired Ceradyne, Inc."},{"Symbol":"ABT","Name":"Abbott Laboratories","Sector":"Health Care","Description":"Abbott Laboratories (Abbott) is engaged in the discovery, development, manufacture, and sale of a portfolio of science-based health care products. Abbott operates in four business segments: diagnostics, medical devices, nutritionals and generic pharmaceuticals. Geographically, 30% of its revenue is generated in the United States; 30% in Western Europe, Canada, Japan and Australia, and 40% in the economies, including India, China, Russia and Brazil. In January 2013, the Company completed the separation of its research-based pharmaceuticals business, which became AbbVie, a new independent biopharmaceutical company. In August 2013, Abbott Laboratories completed its acquisition of OptiMedica Corporation. In August 2013, Abbott Laboratories completed its acquisition of IDEV Technologies."},{"Symbol":"ABBV","Name":"AbbVie","Sector":"Health Care","Description":"AbbVie Inc. (AbbVie) is a research-based pharmaceuticals company. The Company discovers, develops, and commercializes advanced therapies. AbbVie's portfolio of products include a line of adult and pediatric pharmaceuticals, which includes HUMIRA, metabolics/hormones products, virology products, endocrinology products, dyslipidemia products and other products. AbbVie products are used to treat rheumatoid arthritis, psoriasis, Crohn's disease, human immunodeficiency virus (HIV), cystic fibrosis complications, low testosterone, thyroid disease, Parkinson's disease and complications associated with chronic kidney disease, among other indications. In October 2012, AbbVie initiated a comprehensive Phase III program for hepatitis C virus (HCV) genotype one."},{"Symbol":"ACN","Name":"Accenture plc","Sector":"Information Technology","Description":"Accenture plc (Accenture), is engaged in providing management consulting, technology and outsourcing services. The Company's business is structured around five operating groups, which together consists of 19 industry groups serving clients in industries globally. The Company's segment includes Communications, Media & Technology, Financial Services, Health & Public Service, Products and Resources. In September 2013, its Accenture Federal Services completed its acquisition of ASM Research. Effective September 4, 2013, Accenture PLC acquired a 51% interest in GPIT Ltd, a provider of information technology services, from Grameenphone Ltd. In October 2013, the Company acquired PRION Group, a consulting and systems integrator that specializes in Siemens Product Lifecycle Management (PLM) Software. Effective November 1, 2013, Accenture PLC acquired an undisclosed majority interest in Vivere Brasil Servicos e Solucoes SA. Effective December 5, 2013, the Company acquired Procurian Inc."},{"Symbol":"ATVI","Name":"Activision Blizzard","Sector":"Information Technology","Description":"Activision Blizzard, Inc. publishes online, personal computer (PC), video game console, handheld, mobile, and tablet games. The company operates through three segments: Activision, Blizzard, and Distribution. The Activision segment develops and publishes interactive software products and content, as well as sells through retail channels and digital downloads. The Blizzard segment develops, hosts, and supports subscription-based massively multi-player online role-playing game; and develops, markets, and sells role-playing action and strategy games for the PC and iPad. It also maintains a proprietary online-game related service, Battle.net. The Distribution segment provides warehousing, logistical, and sales distribution services to third-party publishers of interactive entertainment software; its own publishing operations; and manufacturers of interactive entertainment hardware. The company serves retailers and distributors, including mass-market retailers, consumer electronics stores, discount warehouses, and game specialty stores through third-party distribution, licensing arrangements, and direct digital purchases. It operates primarily in the United States, Canada, the United Kingdom, France, Germany, Ireland, Italy, Sweden, Spain, the Netherlands, Australia, South Korea, and China. The company is headquartered in Santa Monica, California."},{"Symbol":"AYI","Name":"Acuity Brands Inc","Sector":"Industrials","Description":"Acuity Brands, Inc. (Acuity Brands), incorporated on September 20, 2007, is the parent company of Acuity Brands Lighting, Inc. (ABL), and other subsidiaries. Acuity Brands is a provider of lighting solutions for commercial, institutional, industrial, infrastructure, and residential applications throughout North America and select international markets. The Company's lighting solutions include devices, such as luminaires, lighting controls, power supplies, prismatic skylights, light-emitting diode (LED) lamps, and integrated lighting systems for indoor and outdoor applications utilizing a combination of light sources, including daylight, and other devices controlled by software that monitors and manages light levels while optimizing energy consumption (collectively referred to herein as lighting solutions). Effective March 14, 2013, the Company acquired eldoLED Europe BV."},{"Symbol":"ADBE","Name":"Adobe Systems Inc","Sector":"Information Technology","Description":"Adobe Systems Incorporated (Adobe) is a diversified software company. The Company offers a line of software and services used by professionals, marketers, knowledge workers, application developers, enterprises and consumers for creating, managing, delivering, measuring and engaging with content and experiences across multiple operating systems, devices and media. The Company markets and licenses its software directly to enterprise customers through its sales force and to end users through application stores and its Website at www.adobe.com. Adobe also distributes its products through a network of distributors, value-added resellers (VARs), systems integrators, independent software vendors (ISVs), retailers and original equipment manufacturers (OEMs). In May 2013, Adobe Systems Inc acquired Ideacodes LLC. In July 2013, Adobe Systems Inc announced the completion of acquisition of privately held Neolane."},{"Symbol":"AAP","Name":"Advance Auto Parts","Sector":"Consumer Discretionary","Description":"Advance Auto Parts, Inc. (Advance) is a specialty retailer of automotive aftermarket parts, accessories, batteries and maintenance items primarily operating within the United States. The Company operates in two segments: Advance Auto Parts (AAP), and Autopart International (AI). The AAP segment is comprised of its store operations, which operate under the trade names Advance Auto Parts and Advance Discount Auto Parts. The AI segment consists of the operations of Autopart International, Inc. which operates under the Autopart International trade name. The Company's stores carry a product line for cars, vans, sport utility vehicles and light trucks. The Company serves both do-it-yourself (DIY), and do-it-for-me (Commercial), customers. On December 31, 2012, the Company acquired B.W.P. Distributors, Inc."},{"Symbol":"AES","Name":"AES Corp","Sector":"Utilities","Description":"The AES Corporation (AES) owns a portfolio of electricity generation and distribution businesses on five continents in 27 countries, with total capacity of approximately 44,200 megawatts and distribution networks. It owns and operates two types of businesses. In its Generation business, it owns and/or operates power plants to generate and sell power to wholesale customers, such as utilities and other intermediaries. In its Utilities business, it owns and/or operates utilities to generate, distribute, transmit and sell electricity to end-user customers in the residential, commercial, industrial and governmental sectors within a defined service area. Its six segments include Latin America-Generation, Latin America-Utilities, North America-Generation, North America-Utilities, Europe-Generation and Asia-Generation. On November 28, 2011, it acquired DPL Inc. In September 2012, Sembcorp Industries Ltd acquired power assets in China from AES."},{"Symbol":"AET","Name":"Aetna Inc","Sector":"Health Care","Description":"Aetna Inc. (Aetna) is a diversified healthcare benefits company. The Company offers a range of traditional and consumer-directed health insurance products and related services, including medical, pharmacy, dental, behavioral health, group life and disability plans, medical management capabilities, Medicaid healthcare management services and health information exchange technology services. The Company's operations are conducted in three business segments: Health Care, Group Insurance and Large Case Pensions. Its customers include employer groups, individuals, college students, part-time and hourly workers, health plans, healthcare providers, governmental units, government-sponsored plans, labor groups and expatriates. As of December 31, 2011, the Company served approximately 36.4 million people. In May 2013, the Company announced it has completed its acquisition of Coventry Health Care Inc."},{"Symbol":"AMG","Name":"Affiliated Managers Group Inc","Sector":"Financials","Description":"Affiliated Managers Group, Inc. (AMG) is a global asset management company with equity investments in a diverse group of boutique investment management firms (Affiliates). As of December 31, 2012, the Company manages assets through its Affiliates across a broad range of asset classes and investment styles in three principal distribution channels: Institutional, Mutual Fund and High Net Worth. On June 29, 2012, it completed its investment in Veritable, LP (Veritable) through AMG Wealth Partners. Veritable manages assets for ultra-high net worth individuals and provides comprehensive, objective and tax-sensitive investment consultation through an approach customized for each client's specific needs. On June 29, 2012, it completed its investment in Yacktman Asset Management LP (Yacktman)."},{"Symbol":"AFL","Name":"AFLAC Inc","Sector":"Financials","Description":"Aflac Incorporated (Aflac) is a general business holding company and acts as a management company, overseeing the operations of its subsidiaries by providing management services. Its business is supplemental health and life insurance, through its subsidiary, American Family Life Assurance Company of Columbus, which operates in the United States and as a branch in Japan. Aflac's insurance business consists of two segments: Aflac Japan and Aflac U.S. Aflac Japan sells voluntary supplemental insurance products, including cancer plans, general medical indemnity plans, medical/sickness riders, care plans, living benefit life plans, ordinary life insurance plans and annuities. Aflac U.S. sells voluntary supplemental insurance products including loss-of-income products and products designed to protect individuals from depletion of assets (hospital indemnity, fixed-benefit dental, vision care, accident, cancer, critical illness/ critical care, and hospital intensive care plans)."},{"Symbol":"A","Name":"Agilent Technologies Inc","Sector":"Health Care","Description":"Agilent Technologies, Inc. provides bio-analytical solutions and services to the life sciences, chemical analysis, diagnostics and genomics, communications, and electronics industries worldwide. The companys Life Sciences and Diagnostics segment offers liquid chromatography systems, columns, and components; liquid chromatography mass spectrometry systems; laboratory software and informatics systems; laboratory automation and robotic systems; dissolution testing; nucleic acid solutions; nuclear magnetic resonance, magnetic resonance imaging, and X-ray diffraction systems; immunohistochemistry; in situ hybridization; hematoxylin and eosin staining; DNA mutation detection; genotyping; gene copy number determination; identification of gene rearrangements; DNA methylation and gene expression profiling; next generation sequencing target enrichment; and automated gel electrophoresis-based sample analysis systems. Its Chemical Analysis segment provides gas chromatography systems, columns, and components; gas chromatography mass spectrometry systems; inductively coupled plasma mass spectrometry instruments; atomic absorption instruments; inductively coupled plasma optical emission spectrometry instruments; molecular spectroscopy instruments; software and data systems; and vacuum pumps and measurement technologies. Agilent Technologies, Inc. sells its products through direct sales; and distributors, resellers, manufacturer's representatives, telesales, and electronic commerce. The company was founded in 1999 and is headquartered in Santa Clara, California."},{"Symbol":"APD","Name":"Air Products & Chemicals Inc","Sector":"Materials","Description":"Air Products and Chemicals, Inc. has a portfolio of products, services, and solutions that include atmospheric gases, process and specialty gases, performance materials, equipment, and services. It is a supplier of hydrogen and helium and operates in markets, such as semiconductor materials, refinery hydrogen and natural gas liquefaction. Its segments include Merchant Gases, Tonnage Gases, Electronics and Performance Materials, and Equipment and Energy. In November 2011, Fluor Corporation acquired Goar, Allison & Associates, a unit of Air Products. In February 2012, it acquired ROVI Cosmetics International GmbH. In March 2012, it acquired advanced adsorption technology assets for its Generated Gases product lines. In March 2012, it brought a new hydrogen selenide (H2Se) plant. In September 2012, it acquired an air separation unit (ASU) and integrated gases liquefier in Guiyang, China from Guizhou Kaiyang Chemical Co., Ltd."},{"Symbol":"AKAM","Name":"Akamai Technologies Inc","Sector":"Information Technology","Description":"Akamai Technologies, Inc. (Akamai) provides content delivery and cloud infrastructure services for the delivery of content and applications over the Internet. The Company's solutions range from delivery of conventional content on Websites, to tools that support the delivery and operation of cloud-based applications, to live and on-demand streaming video capabilities all designed to help its customers interact with people accessing the Internet from myriad devices and locations around the world. The Company offers five solutions designed to meet the online business needs of its customers: Terra, Aqua, Sola, Kona and Aura. In December 2012, the Company acquired Verivue, Inc. In September 2012, the Company acquired FastSoft, Inc. In March 2012, the Company acquired Cotendo, Inc. In February 2012, the Company acquired Blaze Software, Inc. Effective November 11, 2013, Akamai Technologies Inc acquired Velocius Networks."},{"Symbol":"ALK","Name":"Alaska Air Group Inc","Sector":"Industrials","Description":"Alaska Air Group, Inc. (Air Group) is the holding company for Alaska Airlines and Horizon Air. The Company has two principal subsidiaries: Alaska Airlines, Inc. (Alaska) and Horizon Air Industries, Inc. (Horizon), which provides scheduled air transportation for passengers and cargo throughout the United States, Canada and Mexico. During the year ended December 31, 2011, it provided air service to approximately 24 million passengers and flew to approximately 100 destinations. Air Group has two operating airlines: Alaska Airlines and Horizon Air. It offers frequent flyer program, the Alaska Airlines Mileage Plan. Members can earn and redeem miles on Alaska, Horizon, and a network of 15 airline partners around the globe, including Delta Air Lines and American Airlines in the United States. The Company launched mobile applications for Android and iPhone smart phones. During 2011, the Company terminated the underlying subleases and sold the remaining Q200 aircraft."},{"Symbol":"ALB","Name":"Albemarle Corp","Sector":"Materials","Description":"Albemarle Corporation (Albemarle) is a developer, manufacturer and marketer of specialty chemicals, which meet customer needs across a range of end markets, including the petroleum refining, consumer electronics, plastics/packaging, construction, automotive, lubricants, pharmaceuticals, crop protection, food-safety and custom chemistry services markets. As of December 31, 2011, the Company and its joint ventures operated 50 facilities, encompassing production, research and development facilities, and administrative and sales offices in North and South America, Europe, the Middle East, Asia, Africa and Australia. It serves approximately 3,000 customers in over 100 countries. It operates in three segments: Polymer Solutions, Catalysts and Fine Chemistry. On May 11, 2011, the Company acquired Catilin Inc. In October 2013, Albemarle Corp acquired Cambridge Chemical Co Ltd."},{"Symbol":"ALXN","Name":"Alexion Pharmaceuticals","Sector":"Health Care","Description":"Alexion Pharmaceuticals, Inc. (Alexion) is a biopharmaceutical company focused on serving patients with severe and ultra-rare disorders through the development and commercialization of life-transforming therapeutic products. Its marketed product Soliris (eculizumab) is the first and only therapeutic approved for patients with two ultra-rare and severe disorders resulting from chronic uncontrolled activation of the complement component of the immune system: paroxysmal nocturnal hemoglobinuria (PNH), an ultra-rare and life-threatening blood disorder, and atypical hemolytic uremic syndrome (aHUS), an ultra-rare and life-threatening genetic disease. On February 7, 2012, it acquired Enobia Pharma Corp. (Enobia). On February 8, 2011, it acquired patents and assets from Orphatec Pharmaceuticals GmbH (Orphatec). On January 28, 2011, it acquired Taligen Therapeutics, Inc. (Taligen). It acquired Asfotase alfa in February 2012."},{"Symbol":"ALLE","Name":"Allegion","Sector":"Industrials","Description":"Allegion plc is a global provider of security products and solutions. The Company offers a portfolio of mechanical and electronic security products across a range of brands. The Company operates in three geographic regions: Americas; Europe, Middle East, India and Africa (EMEIA), and Asia Pacific. As of November 15, 2013, the Company was selling its security products and solutions under 23 brands in 120 countries, including Schlage, Von Duprin, LCN, CISA and Interflex. It sell a range of security products and solutions for end-users in commercial, institutional and residential facilities worldwide, including into the education, healthcare, government, commercial office and single- and multi-family residential markets. In April 2014, the Company acquired Fire & Security Hardware Pty Ltd."},{"Symbol":"AGN","Name":"Allergan, Plc","Sector":"Health Care","Description":"Allergan, Inc. is a multi-specialty health care Company focused on developing and commercializing pharmaceuticals, biologics, medical devices and over-the-counter products. The Company's segments include specialty pharmaceuticals, which produces a range of pharmaceutical products, including ophthalmic products for dry eye, glaucoma, inflammation, infection, allergy and retinal disease; Botox for certain therapeutic and aesthetic indications; skin care products for acne, psoriasis, eyelash growth and other prescription and over-the-counter skin care products; and urologic products and the medical devices segment, which produces a range of medical devices, including breast implants for augmentation, revision and reconstructive surgery and tissue expanders; obesity intervention products, and facial aesthetics products. In December 2013, Allergan Inc completed the sale of its obesity intervention business to Apollo Endosurgery."},{"Symbol":"ADS","Name":"Alliance Data Systems","Sector":"Information Technology","Description":"Alliance Data Systems Corporation is a provider of transaction-based, data-driven marketing and loyalty solutions serving consumer-based businesses in a variety of industries. The Company offers a portfolio of integrated outsourced marketing solutions, including customer loyalty programs, database marketing services, marketing strategy consulting, analytics and creative services, direct marketing services and private label and co-brand retail credit card programs. It focuses on facilitating and managing interactions between its clients and their customers through all consumer marketing channels, including in-store, on-line, catalog, mail, telephone and email, and emerging channels, such as mobile and social media. Its products and services operate under three segments: LoyaltyOne, Epsilon and Private Label Services and Credit."},{"Symbol":"LNT","Name":"Alliant Energy Corp","Sector":"Utilities","Description":"Alliant Energy Corporation (Alliant Energy) operates as a regulated investor-owned public utility holding company. Alliant Energy's primary focus is to provide regulated electricity and natural gas service to approximately one million electric and approximately 415,000 natural gas customers in the Midwest through its two public utility subsidiaries. The primary first tier subsidiaries of Alliant Energy are: Interstate Power and Light Company (IPL), Wisconsin Power and Light Company (WPL), Resources and Corporate Services. IPL is a public utility engaged principally in the generation and distribution of electricity and the distribution and transportation of natural gas in selective markets in Iowa and southern Minnesota. WPL is a public utility engaged principally in the generation and distribution of electricity and the distribution and transportation of natural gas in selective markets in southern and central Wisconsin. On February 1, 2013, it sold RMT, Inc."},{"Symbol":"ALL","Name":"Allstate Corp","Sector":"Financials","Description":"The Allstate Corporation (Allstate) is a holding company for Allstate Insurance Company. The Company's business is conducted principally through Allstate Insurance Company, Allstate Life Insurance Company and their affiliates. It is engaged, principally in the United States, in the property-liability insurance, life insurance, retirement and investment product business. Allstate's primary business is the sale of private passenger auto and homeowners insurance. The Company also sells several other personal property and casualty insurance products, select commercial property and casualty coverages, life insurance, annuities, voluntary accident and health insurance and funding agreements. It conducts its business primarily in the United States. Allstate has four business segments: Allstate Protection, Allstate Financial, Discontinued Lines and Coverages and Corporate and Other. In October 2011, the Company acquired Esurance and Answer Financial from White Mountains Insurance Group."},{"Symbol":"GOOGL","Name":"Alphabet Inc Class A","Sector":"Information Technology","Description":"Google Inc. (Google) is a global technology company. The Company's business is primarily focused around key areas, such as search, advertising, operating systems and platforms, enterprise and hardware products. The Company generates revenue primarily by delivering online advertising. The Company also generates revenues from Motorola by selling hardware products. The Company provides its products and services in more than 100 languages and in more than 50 countries, regions, and territories. Effective May 16, 2014, Google Inc acquired Quest Visual Inc. Effective May 20, 2014, Google Inc acquired Enterproid Inc, doing business as Divide. In June 2014, Google Inc acquired mDialog Corp. Effective June 25, 2014, Google Inc acquired Appurify Inc, a San Francisco-based developer of mobile bugging application software."},{"Symbol":"GOOG","Name":"Alphabet Inc Class C","Sector":"Information Technology","Description":"Google Inc. (Google) is a global technology company. The Company's business is primarily focused around key areas, such as search, advertising, operating systems and platforms, enterprise and hardware products. The Company generates revenue primarily by delivering online advertising. The Company also generates revenues from Motorola by selling hardware products. The Company provides its products and services in more than 100 languages and in more than 50 countries, regions, and territories. Effective May 16, 2014, Google Inc acquired Quest Visual Inc. Effective May 20, 2014, Google Inc acquired Enterproid Inc, doing business as Divide. In June 2014, Google Inc acquired mDialog Corp. Effective June 25, 2014, Google Inc acquired Appurify Inc, a San Francisco-based developer of mobile bugging application software."},{"Symbol":"MO","Name":"Altria Group Inc","Sector":"Consumer Staples","Description":"Altria Group, Inc. is a holding company. At December 31, 2012, Altria Group, Inc.'s direct and indirect wholly owned subsidiaries included Philip Morris USA Inc. (PM USA), which is engaged in the manufacture and sale of cigarettes and certain smokeless products in the United States; John Middleton Co. (Middleton), which is engaged in the manufacture and sale of machine-made cigars and pipe tobacco, and is a wholly owned subsidiary of PM USA; and UST LLC (UST), which through its direct and indirect wholly owned subsidiaries, including U.S. Smokeless Tobacco Company LLC (USSTC) and Ste. Michelle Wine Estates Ltd. (Ste. Michelle), is engaged in the manufacture and sale of smokeless products and wine. Philip Morris Capital Corporation (PMCC), another wholly owned subsidiary of Altria Group, Inc., maintains a portfolio of leveraged and direct finance leases. In addition, Altria Group, Inc. held approximately 26.9% of the economic and voting interest of SABMiller plc (SABMiller)."},{"Symbol":"AMZN","Name":"Amazon.com Inc","Sector":"Consumer Discretionary","Description":"Amazon.com, Inc. (Amazon.com) serves consumers through its retail websites and focus on selection, price, and convenience. The Company offers programs that enables sellers to sell their products on its websites and their own branded websites and to fulfill orders through them , and programs that allow authors, musicians, filmmakers, application developers, and others to publish and sell content. The Company operates in two segments: North America and International. The Company serves consumers through its retail websites, and focus on selection, price, and convenience. The Company designs its websites to enable millions of products to be sold by the Company and by third parties across dozens of product categories. Customers access its websites directly and through its mobile websites and apps. It also manufactures and sells Kindle devices. In October 2013, Amazon.com Inc acquired TenMarks Education Inc."},{"Symbol":"AEE","Name":"Ameren Corp","Sector":"Utilities","Description":"Ameren Corporation (Ameren) is a utility holding company. The Company's principal subsidiaries are Union Electric Company (Ameren Missouri), Ameren Illinois Company (Ameren Illinois) and Ameren Energy Resources Company, LLC (AER). The Company operates in three segments: Ameren Missouri, Ameren Illinois, and Merchant Generation. Ameren Missouri operates a rate-regulated electric generation, transmission and distribution business, and a rate-regulated natural gas transmission and distribution business in Missouri. Ameren Illinois operates a rate-regulated electric and natural gas transmission and distribution business in Illinois. In December 2013, Dynegy Inc announced that its subsidiary, Illinois Power Holdings (IPH) completed its acquisition of New Ameren Energy Resources (AER), an Ameren subsidiary."},{"Symbol":"AAL","Name":"American Airlines Group","Sector":"Industrials","Description":"American Airlines Group Inc., through its subsidiaries, operates in the airline industry. As of December 31, 2014, the company operated 983 mainline jets, as well as 566 regional aircrafts through regional airline subsidiaries and third-party regional carriers. It serves 339 destinations in 54 countries. The company was formerly known as AMR Corporation and changed its name to American Airlines Group Inc. in December 2013. American Airlines Group Inc. was founded in 1934 and is headquartered in Fort Worth, Texas."},{"Symbol":"AEP","Name":"American Electric Power","Sector":"Utilities","Description":"American Electric Power Company, Inc. (AEP) is a utility holding company that owns, directly or indirectly, all of the outstanding common stock of its public utility subsidiaries and varying percentages of other subsidiaries. The service areas of AEP's public utility subsidiaries cover portions of the states of Arkansas, Indiana, Kentucky, Louisiana, Michigan, Ohio, Oklahoma, Tennessee, Texas, Virginia and West Virginia. The generating and transmission facilities of AEP's public utility subsidiaries are interconnected and their operations are coordinated. Transmission networks are interconnected with distribution facilities in the territories served. On December 31, 2011, Columbus Southern Power Company (CSPCo) merged with and into Ohio Power Company (OPCo) with OPCo being the surviving entity. In March 2012, the Company's subsidiary, AEP Retail Energy acquired BlueStar Energy Holdings Inc. and its independent retail electric supplier BlueStar Energy Solutions."},{"Symbol":"AXP","Name":"American Express Co","Sector":"Financials","Description":"American Express Company (American Express) is a global service company. The Company's range of products and services includes Charge and credit card products, Expense management products and services, Consumer and business travel services, Stored value products, such as travelers cheques and other prepaid products, Network services, merchant acquisition and processing, servicing and settlement, and point-of-sale, marketing and information products and services for merchants, and fee services, including market and trend analyses and related consulting services, fraud prevention services, and the design of customized customer loyalty and rewards programs. It operates in four segments: U.S. Card Services, International Card Services, Global Commercial Services (GCS) and Global Network & Merchant Services (GNMS). American Express and its principal operating subsidiary, American Express Travel Related Services Company, Inc. (TRS), are bank holding companies."},{"Symbol":"AIG","Name":"American International Group, Inc.","Sector":"Financials","Description":"American International Group, Inc. (AIG) is a global insurance company. The Company provides a range of property casualty insurance, life insurance, retirement products, mortgage insurance and other financial services to customers in more than 130 countries. It diverse offerings include products and services that help businesses and individuals protect their assets, manage risks and provide for retirement security. It earns revenues primarily from insurance premiums, policy fees from universal life insurance and investment products, and income from investments. Its segments include AIG Property Casualty and AIG Life and Retirement. During the year ended December 31, 2012, the Chartis segment was renamed AIG Property Casualty and the SunAmerica segment was renamed AIG Life and Retirement."},{"Symbol":"AMT","Name":"American Tower Corp A","Sector":"Real Estate","Description":"American Tower Corporation is a holding company. The Company conducts its operations through its directly and indirectly owned subsidiaries and joint ventures. It is a wireless and broadcast communications infrastructure company that owns, operates and develops communications sites. Its primary business includes leasing antenna space on multi-tenant communications sites to wireless service providers, radio and television broadcast companies, wireless data providers, government agencies and municipalities and tenants in a number of other industries. This business is its rental and management operations, which accounted for approximately 98% of its total revenues during the year ended December 31, 2011. In January 2012, the Company merged with and into American Tower REIT, Inc. In October 2013, the Company acquired MIP Tower Holdings LLC. In November2013, NII Holdings, Inc sold 1,483 communications sites in Mexico to American Tower Corporation."},{"Symbol":"AWK","Name":"American Water Works Company Inc","Sector":"Utilities","Description":"American Water Works Company, Inc. (American Water) is a water and wastewater utility company. American Water provides drinking water, wastewater and other water related services to an estimated 15 million people in more than 30 states and in two Canadian provinces. The Company operates in two segments: the Regulated Businesses and the Market-Based Operations. During the year ended December 31, 2011, its Regulated Businesses segment accounted for 88.8% of its total consolidated operating revenue. During 2011, its Market-Based Operations segment accounted for 12.3% of total consolidated operating revenue. In November 2013, American Water Works Company, Inc announced that its Virginia American Water subsidiary has completed its acquisition of Dale Service Corporation. In November 2013, American Water Works Company Inc's West Virginia American Water announced that it has completed the purchase of the Town of Pratt's water system."},{"Symbol":"AMP","Name":"Ameriprise Financial","Sector":"Financials","Description":"Ameriprise Financial, Inc. (Ameriprise Financial) is a holding company primarily engaged in business through its subsidiaries. The Company is a diversified financial services company. Ameriprise Financial operates in five segments: Advice & Wealth Management, Asset Management, Annuities, Protection and Corporate & Other. As of December 31, 2012, the Company had a network of more than 9,700 affiliated advisors. Ameriprise Financial offers its branded advisors training, tools, leadership, marketing programs and other field and centralized support to assist them in delivering advice and product solutions to clients. It uses three principal brands for its businesses in the United States: Ameriprise Financial, Columbia Management and RiverSource. The Company uses its Threadneedle brand for its international asset manager's products."},{"Symbol":"ABC","Name":"AmerisourceBergen Corp","Sector":"Health Care","Description":"AmerisourceBergen Corporation (AmerisourceBergenis) is a pharmaceutical services company serving the United States, Canada and select global markets. The Company distributes a range of pharmaceuticalsgeneric pharmaceuticals, over-the-counter healthcare products, home healthcare supplies and equipment, and related services to a range of healthcare providers located in the United States, Canada and selected global markets, including acute care hospitals and health systems, independent and chain retail pharmacies, mail order pharmacies, medical and dialysis clinics, physicians and physician group practices, long-term care and other alternate site pharmacies, and other customers. In May 2013, the Company completed the divestiture of its contract packaging business, AndersonBrecon. In June 2013, the Company announced that has completed the divestiture of AmerisourceBergen Canada Corporation (ABCC)."},{"Symbol":"AME","Name":"AMETEK Inc","Sector":"Industrials","Description":"AMETEK, Inc. (AMETEK) is a global manufacturer of electronic instruments and electromechanical devices with operations in North America, Europe, Asia and South America. The Company markets its products worldwide through two groups: the Electronic Instruments Group (EIG) and the Electromechanical Group (EMG). EIG builds monitoring, testing, calibration and display devices for the process, aerospace, industrial, power and medical markets. EMG produces engineered electromechanical connectors for hermetic (moisture-proof) applications, specialty metals for niche markets and brushless air-moving motors, blowers and heat exchangers. End markets include aerospace, defense, mass transit, medical, office products and other industrial markets. In December 2013, the Company announced that it has acquired Powervar, a provider of power management systems and uninterruptible power supply (UPS) systems."},{"Symbol":"AMGN","Name":"Amgen Inc","Sector":"Health Care","Description":"Amgen Inc. is a global biotechnology pioneer that discovers, develops, manufactures and delivers human therapeutics. Its medicines help millions of patients in the fight against cancer, kidney disease, rheumatoid arthritis (RA), bone disease, and other serious illnesses. On December 10, 2012, the Company acquired all of the outstanding stock of deCODE Genetics (deCODE). In July 5, 2012, the Company acquired KAI Pharmaceuticals, a privately held company based in South San Francisco. In September 2013, Swedish Orphan Biovitrum AB (publ) (Sobi) announced that they have acquired the full rights to develop and commercialize Kineret (anakinra) from Amgen Inc for all therapeutic indications."},{"Symbol":"APH","Name":"Amphenol Corp","Sector":"Information Technology","Description":"Amphenol Corporation (Amphenol) is a designer, manufacturer and marketer of electrical, electronic and fiber optic connectors, interconnect systems and coaxial and high-speed specialty cable. Amphenol designs and manufactures connectors and interconnect systems, which are used primarily to conduct electrical and optical signals for a range of electronic applications. It has developed a range of connector and interconnect products for information technology and communications equipment applications, including the converging voice, video and data communications markets. The Company offers a range of interconnect products for factory automation and motion control systems, machine tools, instrumentation and medical systems, mass transportation applications and automotive safety systems and a diverse range of on-board electronics. In addition, Amphenol is a supplier of high performance, military-specification, circular environmental connectors."},{"Symbol":"APC","Name":"Anadarko Petroleum Corp","Sector":"Energy","Description":"Anadarko Petroleum Corporation (Anadarko) is an independent exploration and production company. As of December 31, 2011, the Company had over 2.5 billion barrels of oil equivalent (BOE) of proved reserves. Anadarko's asset portfolio includes positions in onshore resource plays in the Rocky Mountains region, the southern United States, and the Appalachian basin. The Company is also independent producers in the deepwater Gulf of Mexico, and has production and exploration activities globally, including positions in high-potential basins located in East and West Africa, Algeria, China, Alaska, and New Zealand. The Company operates in three segments: Oil and gas exploration and production, Midstream, and Marketing. In August 2012, Western Gas Partners, LP acquired an additional 24% membership interest in Chipeta Processing LLC from Anadarko."},{"Symbol":"ADI","Name":"Analog Devices, Inc.","Sector":"Information Technology","Description":"Analog Devices, Inc. (Analog Devices) is engaged in the design, manufacture and marketing of a range of analog, mixed-signal and digital signal processing integrated circuits (ICs). The Company produces a range of products, including data converters, amplifiers and linear products, radio frequency (RF) ICs, power management products, sensors based on micro-electro mechanical systems (MEMS) technology and other sensors, and processing products, including DSP and other processors, which are designed to meet the needs of a base of customers. The Company's products are embedded inside many different types of electronic equipment, including industrial process control systems; instrumentation and measurement systems; wireless infrastructure equipment, and aerospace and defense electronics. In November 2013, Analog Devices, Inc completed the sale of the assets of its microphone product line to InvenSense, Inc."},{"Symbol":"ANTM","Name":"Anthem Inc.","Sector":"Health Care","Description":"Anthem, Inc., a health benefits company, through its subsidiaries, provides a range of medical products in the United States. The company offers a spectrum of network-based managed care health benefit plans to large and small employer, individual, Medicaid, and senior markets. Its managed care plans include preferred provider organizations; health maintenance organizations; point-of-service plans; traditional indemnity plans and other hybrid plans, such as consumer-driven health plans; and hospital only and limited benefit products. The company also provides a range of managed care services to self-funded customers, including claims processing, underwriting, stop loss insurance, actuarial services, provider network access, medical cost management, disease management, wellness programs, and other administrative services. In addition, it offers an array of specialty and other insurance products and services, such as behavioral health benefit services, dental, vision, life and disability insurance benefits, radiology benefit management, analytics-driven personal health care guidance, and long-term care insurance. Further, the company provides services to the federal Government in connection with the federal Employee Program and various medicare programs; and operates as a licensee of the Blue Cross and Blue Shield Association. As of December 31, 2013, it served 35.7 million medical members through its affiliated health plans; and approximately 67.8 million individuals. The company was founded in WellPoint, Inc. and changed its name to Anthem, Inc. in December 2014. Anthem, Inc. is headquartered in Indianapolis, Indiana."},{"Symbol":"AON","Name":"Aon plc","Sector":"Financials","Description":"Aon plc provides risk management services, insurance and reinsurance brokerage, and human resource consulting and outsourcing services worldwide. It operates through two segments, Risk Solutions and HR Solutions. The Risk Solutions segment offers retail brokerage services, including affinity products, managing general underwriting, placement, captive management services, and data and analytics; risk management solutions for property liability, general liability, professional liability, directors' and officers' liability, workers' compensation, and various healthcare products; and health and benefits consulting services comprising structuring, funding, and administering employee benefit programs. This segment also provides advisory services to technology, financial services, agribusiness, aviation, construction, health care, energy, and other industries. In addition, this segment offers reinsurance brokerage services for treaty and facultative reinsurance, as well as specialty lines, such as professional liability, workers' compensation, accident, life, and health; actuarial, enterprise risk management, catastrophe management, and rating agency advisory services; and capital management transaction and advisory services for insurance and reinsurance clients. The HR Solutions segment offers human capital services in the areas of retirement, compensation, strategic human capital, investment consulting, benefits administration, exchanges, and human resource business process outsourcing. Aon plc was founded in 1919 and is headquartered in London, the United Kingdom."},{"Symbol":"APA","Name":"Apache Corporation","Sector":"Energy","Description":"Apache Corporation (Apache) is an independent energy company, which explores for, develops, and produces natural gas, crude oil, and natural gas liquids. As of December 31, 2011, Apache had exploration and production interests in six countries: the United States, Canada, Egypt, Australia, offshore the United Kingdom in the North Sea, and Argentina. During the year ended December 31, 2011, it participated in drilling 1,087 gross wells, with 1,005 completed as producers. As of December 31, 2011, in addition to its completed wells, several wells had not reached completion: 39 in the United States (28.44 net); 50 in Canada (42.69 net); 23 in Egypt (21.75 net); six in the North Sea (4.91 net); one in Australia (0.33 net), and three in Argentina (3.00 net). In November 2013, the Company sold one-third minority participation in its Egypt oil and gas business to Sinopec International Petroleum Exploration and Production Corporation."},{"Symbol":"AIV","Name":"Apartment Investment & Mgmt","Sector":"Real Estate","Description":"Apartment Investment and Management Company (Aimco) is a self-administered and self-managed real estate investment trust (REIT). AIMCO Properties, L.P. (the Aimco Operating Partnership), is a partnership formed to conduct the Company's business, which is focused on the ownership, management and redevelopment of apartment communities located in the United States. Aimco, through its wholly owned subsidiaries, AIMCO-GP, Inc. and AIMCO-LP Trust, owns an ownership interests in the Aimco Operating Partnership. Aimco conducts all of its business and owns all of its assets through the Aimco Operating Partnership. As of December 31, 2012 , the Company's portfolio of owned properties consisted of 265 properties with 67,977 apartment units. The Company's business is organized around two activities: Property Operations and Portfolio Management. During the year ended December 31, 2012, the Company acquired 84 properties in San Diego, 42 in Manhattan and 488 in Phoenix."},{"Symbol":"AAPL","Name":"Apple Inc.","Sector":"Information Technology","Description":"Apple Inc. (Apple) designs, manufactures and markets mobile communication and media devices, personal computers, and portable digital music players, and a variety of related software, services, peripherals, networking solutions, and third-party digital content and applications. The Company's products and services include iPhone, iPad, Mac, iPod, Apple TV, a portfolio of consumer and professional software applications, the iOS and OS X operating systems, iCloud, and a variety of accessory, service and support offerings. The Company also delivers digital content and applications through the iTunes Store, App StoreSM, iBookstoreSM, and Mac App Store. The Company distributes its products worldwide through its retail stores, online stores, and direct sales force, as well as through third-party cellular network carriers, wholesalers, retailers, and value-added resellers. In February 2012, the Company acquired app-search engine Chomp."},{"Symbol":"AMAT","Name":"Applied Materials Inc","Sector":"Information Technology","Description":"Applied Materials, Inc. (Applied) provides manufacturing equipment, services and software to the global semiconductor, flat panel display, solar photovoltaic (PV) and related industries. Applied's customers include manufacturers of semiconductor wafers and chips, flat panel liquid crystal displays (LCDs), solar PV cells and modules, and other electronic devices. These customers may use what they manufacture in their own end products or sell the items to other companies for use in advanced electronic components. Applied operates in four segments: Silicon Systems Group, Applied Global Services, Display, and Energy and Environmental Solutions. In November 2011, the Company acquired Varian Semiconductor Associates, Inc."},{"Symbol":"ADM","Name":"Archer-Daniels-Midland Co","Sector":"Consumer Staples","Description":"Archer-Daniels-Midland-Company, is engaged in the processing of oilseeds, corn, wheat, cocoa, and other agricultural commodities. The Company manufactures protein meal, vegetable oil, corn sweeteners, flour, biodiesel, ethanol, and other value-added food and feed ingredients. The Company also has a grain elevator and transportation network to procure, store, clean, and transport agricultural commodities, such as oilseeds, corn, wheat, milo, oats, and barley, as well as processed agricultural commodities. Its operations are classified into three business segments: Oilseeds Processing, Corn Processing, and Agricultural Services. In January 2012, the Company acquired three grain elevators in Slovakia from the companies Palma Group a.s. and Polnonakup Hont a.s. In October 2012, the Company acquired 10% interest in GrainCorp Ltd."},{"Symbol":"ARNC","Name":"Arconic Inc","Sector":"Industrials","Description":""},{"Symbol":"AJG","Name":"Arthur J. Gallagher & Co.","Sector":"Financials","Description":"Arthur J. Gallagher & Co. (Gallagher) along with its subsidiaries, provides insurance brokerage and third-party claims settlement, and administration services to entities in the United States and abroad. It operates in three segments: brokerage, risk management and corporate. The Brokerage segment primarily consists of retail and wholesale insurance brokerage operations. The Company's risk management segment provides contract claim settlement and administration services for enterprises that choose to self-insure some or all of their property/casualty coverages and for insurance companies that choose to outsource some or all of their property/casualty claims departments. In November 2013, Arthur J. Gallagher & Co. announced the acquisition of The Parks Johnson Agency. In November 2013, the Company acquired the Giles Group of Companies. Effective November 14, 2013, the Company acquired Giles Insurance Brokers Ltd."},{"Symbol":"AIZ","Name":"Assurant Inc","Sector":"Financials","Description":"Assurant, Inc. (Assurant) is a provider of specialized insurance products and related services in North America and select worldwide markets. The Company operates in four segments: Assurant Solutions, Assurant Specialty Property, Assurant Health, and Assurant Employee Benefits. The products offered by the segments include warranties and service contracts, pre-funded funeral insurance, lender-placed homeowners insurance, manufactured housing homeowners insurance, individual health and small employer group health insurance, group dental, disability, and life insurance and employee-funded voluntary benefits. On June 21, 2011, the Company acquired the SureDeposit business, the provider of security deposit alternatives to the multifamily housing industry. In October 2013, FirstService Corporation completed the sale of its Field Asset Services business to Assurant, Inc. In October 2013, Assurant Inc acquired Lifestyle Services Group Ltd."},{"Symbol":"T","Name":"AT&T Inc","Sector":"Telecommunications Services","Description":"AT&T Inc. provides telecommunications services to consumers and businesses in the United States and internationally. Its Wireless segment offers various wireless voice, data, text, and other services, including local wireless communications services, long-distance services, and roaming services. This segment also sells various handsets, wirelessly enabled computers, and personal computer wireless data cards through its owned stores, agents, or third-party retail stores; and accessories comprising carrying cases, hands-free devices, batteries, battery chargers, and other items to consumers, as well as to agents and third-party distributors. As of December 31, 2013, this segment served approximately 110 million wireless subscribers. The companys Wireline segment provides data services, such as switched and dedicated transport, DSL Internet access, network integration, managed Web-hosting, packet, and enterprise networking services, as well as local, interstate, and international wholesale networking capacity to other service providers. This segment also offers voice services consisting of local and long-distance services; wholesale switched access services to other service providers; and outsourcing services, integration services and customer premises equipment, and government-related services. This segment served approximately 12 million retail consumer access lines, 10 million retail business access lines, and 2 million wholesale access lines. The company has strategic relationship with IBM to provide businesses with a source for network security and threat management. The company was formerly known as SBC Communications Inc. and changed its name to AT&T Inc. in November 2005. AT&T, Inc. was founded in 1983 and is based in Dallas, Texas."},{"Symbol":"ADSK","Name":"Autodesk Inc","Sector":"Information Technology","Description":"Autodesk, Inc. (Autodesk) is a design software and services company. The Company operates in four operating segments: Platform Solutions and Emerging Business (PSEB), Architecture, Engineering and Construction (AEC), Manufacturing (MFG) and Media and Entertainment (M&E). The Company serves customers in the architecture, engineering and construction; manufacturing, and digital media and entertainment industries. The Company's software products enable its customers to experience their ideas before they are real by allowing them to imagine, design, and create their ideas and to visualize, simulate and analyze real-world performance early in the design process by creating digital prototypes. In August 2012, the Company acquired certain assets of Inforbix, LLC. In October 2012, it acquired certain assets of HSMWorks Aps. In October 2012, it acquired Qontext. In January 2013, the Company acquired PI-VR. Effective March 19, 2013, it acquired Firehole Technologies Inc."},{"Symbol":"ADP","Name":"Automatic Data Processing","Sector":"Information Technology","Description":"Automatic Data Processing, Inc. (ADP) is a provider of business outsourcing solutions. ADP offers a wide range of human resource, payroll, tax and benefits administration solutions from a single source. ADP is also a provider of integrated computing solutions to auto, truck, motorcycle, marine, recreational vehicle, and heavy equipment dealers throughout the world. The Company's operating segments include: Employer Services, professional employer organization (PEO) Services, and Dealer Services. In October 2011, the Company acquired WALLACE - The Training Tax Credit Company. In January 2012, the Company acquired Indian payroll business of Randstad Holding NV. In April 2012, it acquired the human resource solutions subsidiary of SHPS, Inc. In June 2013, Automatic Data Processing, Inc. announced that it has acquired Payroll S.A."},{"Symbol":"AN","Name":"AutoNation Inc","Sector":"Consumer Discretionary","Description":"AutoNation, Inc., through its subsidiaries, operates as an automotive retailer in the United States. The company operates in three segments: Domestic, Import, and Premium Luxury. It offers a range of automotive products and services, including new and used vehicles, parts and service comprising automotive repair and maintenance services, and wholesale parts and collision businesses; and automotive finance and insurance products that include arranging financing for vehicle purchases through third-party finance sources. The company also provides a range of vehicle maintenance, repair, and collision repair services, such as warranty and customer-pay service work. In addition, it offers various vehicle protection products, such as extended service contracts, maintenance programs, guaranteed auto protection, tire and wheel protection, and theft protection products. As of November 4, 2014, the company owned and operated 277 new vehicle franchises, which sell 34 brands in 15 states. AutoNation, Inc. was founded in 1991 and is headquartered in Fort Lauderdale, Florida."},{"Symbol":"AZO","Name":"AutoZone Inc","Sector":"Consumer Discretionary","Description":"AutoZone, Inc. (AutoZone) is a retailer and a distributor of automotive replacement parts and accessories in the United States. As of August 25, 2012, AutoZone operated 4,685 stores in the United States, including Puerto Rico, and 321 in Mexico. The Company operates in two segments: Auto Parts Stores and Other. The Auto Parts Stores segment is a retailer and distributor of automotive parts and accessories through the Company's 5,006 stores in the United States, Puerto Rico, and Mexico. The Other category reflects business activities that are not separately reportable, including ALLDATA which produces, sells and maintains diagnostic and repair information software used in the automotive repair industry, and e-Commerce, which includes direct sales to customers through www.autozone.com. During the fiscal year ended August 25, 2012 (fiscal 2012), the Company opened 193 stores and relocated 10 stores."},{"Symbol":"AVB","Name":"AvalonBay Communities, Inc.","Sector":"Real Estate","Description":"AvalonBay Communities, Inc. is a real estate investment trust (REIT) . It is engaged in developing, acquiring, owning and operating apartment communities in high barrier to entry markets of the United States. As of January 31, 2013, it owned a direct or indirect ownership interest in 178 operating apartment communities containing 52,427 apartment homes in nine states and the District of Columbia, of which 155 communities containing 45,056 apartment homes were consolidated for financial reporting purposes, two communities containing 674 apartment homes were held by joint ventures in which it holds an ownership interest, and 21 communities containing 6,697 apartment homes were owned by the Funds.The Company's real estate investments consist of the segments include Established Communities, Other Stabilized Communities and Development/Redevelopment Communities."},{"Symbol":"AVY","Name":"Avery Dennison Corp","Sector":"Materials","Description":"Avery Dennison Corporation (Avery Dennison), is engaged in the production of pressure-sensitive materials, and a variety of tickets, tags, labels other converted products, and office and consumer products through embossing, printing, stamping and die-cutting. The Company operates in two segments: Pressure-sensitive Materials and Retail Branding and Information Solutions. In addition to its reportable segments, the Company has other specialty converting businesses comprised of Vancive Medical Technologies (Vancive) and Designed and Engineered Solutions (DES). Some are sold by the Company in converted form as printable media, tapes and reflective sheeting. In July 2013, Avery Dennison Corp completed the sale of its Office and Consumer Products and Designed and Engineered Solutions businesses to CCL Industries Inc."},{"Symbol":"BHI","Name":"Baker Hughes Inc","Sector":"Energy","Description":"DELISTED - Baker Hughes Incorporated (Baker Hughes) is engaged in the oilfield services industry. Baker Hughes is a supplier of oilfield services, products, technology and systems to the worldwide oil and natural gas industry. It also provides industrial and other products and services to the downstream refining, and the process and pipeline industries. The Company may conduct its operations through subsidiaries, affiliates, ventures and alliances. It operates in more than 80 countries worldwide. The Company operates in five segments. Four of these segments represent its oilfield operations and their geographic organization: North America (U.S. Land, Gulf of Mexico and Canada), Latin America, Europe/Africa/Russia Caspian and Middle East/Asia Pacific. Its Industrial Services and Other segment includes downstream chemicals, process and pipeline services, and the reservoir development services group."},{"Symbol":"BLL","Name":"Ball Corp","Sector":"Materials","Description":"Ball Corporation (Ball) is a supplier of metal packaging to the beverage, food, personal care and household products industries. The Company operates in four segments: metal beverage packaging, Americas and Asia; metal beverage packaging, Europe; metal food and household products packaging, Americas, and aerospace and technologies. Ball also provides aerospace and other technologies and services to governmental and commercial customers within its aerospace and technologies segment. Its product lines are aluminum and steel beverage containers. Ball also produces steel food containers and steel and aluminum aerosol containers for beverages, food, personal care and household products, as well as steel paint cans, decorative steel tins and aluminum slugs. In December 2012, the Company acquired Envases del Plata S.A. de C.V. During the year ended December 31, 2012, the Company acquired Tubettificio Europeo S.p.A. (Tubettificio)."},{"Symbol":"BAC","Name":"Bank of America Corp","Sector":"Financials","Description":"Bank of America Corporation (Bank of America) is a bank holding company, and a financial holding company. Bank of America is a financial institution, serving individual consumers, small and middle market businesses, corporations and Governments with a range of banking, investing, asset management and other financial and risk management products and services. Through its banking and various nonbanking subsidiaries throughout the United States and in international markets, the Company provides a range of banking and nonbanking financial services and products through five business segments: Consumer & Business Banking (CBB), Consumer Real Estate Services (CRES), Global Banking, Global Markets and Global Wealth & Investment Management (GWIM), with the remaining operations recorded in all Other. In October 2013, Bank of America Corporation announced the completion of the merger of its Merrill Lynch & Co., Inc. subsidiary into Bank of America Corporation."},{"Symbol":"BCR","Name":"Bard (C.R.) Inc.","Sector":"Health Care","Description":"C. R. Bard, Inc. (Bard) is engaged in the design, manufacture, packaging, distribution and sale of medical, surgical, diagnostic and patient care devices. As of December 31, 2011, the Company sold a range of products to hospitals, individual healthcare professionals, extended care facilities and alternate site facilities on a global basis. The Company participates in the markets for vascular, urology, oncology and surgical specialty products. Bard also has a product group of other products. Through subsidiaries and a joint venture, Bard markets its products to customers in over 100 countries outside the United States. The Company's principal international markets were in Europe and Japan as of December 31, 2011. On November 10, 2011, Bard acquired Medivance, Inc. On December 16, 2011, the Company acquired Lutonix, Inc."},{"Symbol":"BAX","Name":"Baxter International Inc.","Sector":"Health Care","Description":"Baxter International Inc. (Baxter,) is a global, diversified healthcare company. Baxter, through its subsidiaries, develops, manufactures and markets products that save and sustain the lives of people with hemophilia, immune disorders, infectious diseases, kidney disease, trauma, and other chronic and acute medical conditions. The Company operated in two segments: BioScience and Medication Delivery. It is engaged in the medical devices, pharmaceuticals and biotechnology to create products that advance patient care worldwide. These products are used by hospitals, kidney dialysis centers, nursing homes, rehabilitation centers, doctors' offices, clinical and medical research laboratories, and by patients at home under physician supervision. Baxter manufactures products in 27 countries and sells the products in more than 100 countries. In September 2013, Baxter International Inc completed the acquisition of Gambro AB."},{"Symbol":"BBT","Name":"BB&T Corporation","Sector":"Financials","Description":"BB&T Corporation (BB&T) is a financial holding company. BB&T conducts its business operations primarily through its commercial bank subsidiary, Branch Bank, and other nonbank subsidiaries. The Company's principal operating subsidiaries include Branch Banking and Trust Company, Winston-Salem, North Carolina; BB&T Securities, LLC, Richmond, Virginia; Regional Acceptance Corporation, Greenville, North Carolina; American Coastal Insurance Company, Davie, Florida, and Sterling Capital Management, LLC, Charlotte, North Carolina. BB&T's operations are divided into six reportable business segments: Community Banking, Residential Mortgage Banking, Dealer Financial Services, Specialized Lending, Insurance Services and Financial Services. In April 2012, it acquired the life and property and casualty insurance operating divisions of Roseland, New Jersey - based Crump Group Inc. On July 31, 2012, it acquired BankAtlantic."},{"Symbol":"BDX","Name":"Becton Dickinson","Sector":"Health Care","Description":"Becton Dickinson and Co, formerly Becton Dickinson & Co is a global medical technology company engaged in the development, manufacture and sale of medical devices, instrument systems and reagents used by healthcare institutions, life science researchers, clinical laboratories, the pharmaceutical industry and the general public. The Company' s operations consist of three business segments: BD Medical, BD Diagnostics and BD Biosciences. On February 9, 2012, the Company acquired a 100% interest in KIESTRA Lab Automation BV. On August 24, 2012, the Company acquired a 100% interest in Sirigen Group Limited. On October 31, 2012, the Company sold its BD Biosciences -Discovery Labware unit. In December 2012, the Company acquired Safety Syringes, Inc. Effective March 12, 2013, the Company acquired Cato Software Solutions GmbH."},{"Symbol":"BBBY","Name":"Bed Bath & Beyond","Sector":"Consumer Discretionary","Description":"Bed Bath & Beyond Inc. is a chain of retail stores, operating under the names Bed Bath & Beyond (BBB), Christmas Tree Shops (CTS), Harmon and Harmon Face Values (Harmon), buybuy BABY and World Market or Cost Plus World Market (World Market). In addition, it is a partner in a joint venture, which operates three stores in the Mexico City market under the name Bed Bath & Beyond. The Company sells a range of domestics merchandise and home furnishings. Domestics merchandise includes categories, such as bed linens and related items, bath items and kitchen textiles. Home furnishings include categories, such as kitchen and tabletop items, fine tabletop, basic housewares and general home. During fiscal year ended March 2, 2013 (fiscal 2012), the Company opened a total of 38 stores, including 12 BBB stores throughout the United States and Canada, five CTS stores, one Harmon store and 18 buybuy BABY stores, and six World Market stores throughout the United States and closed one BBB store."},{"Symbol":"BRK.B","Name":"Berkshire Hathaway","Sector":"Financials","Description":""},{"Symbol":"BBY","Name":"Best Buy Co. Inc.","Sector":"Consumer Discretionary","Description":"Best Buy Co., Inc. is a multinational retailer of consumer electronics, computing and mobile phone products, entertainment products, appliances and related services. The Company operates retail stores and call centers, and conducts online retail operations under a range of brand names, such as Best Buy (BestBuy.com, BestBuy.ca), Best Buy Mobile (BestBuyMobile.com), The Carphone Warehouse (CarphoneWarehouse.com), Five Star, Future Shop (FutureShop.ca), Geek Squad, Magnolia Audio Video, Pacific Sales and The Phone House (PhoneHouse.com). During the fiscal year ended March 3, 2012 (fiscal 2012), it operated in two segments: Domestic and International. During fiscal 2012, it opened 135 new stores and closed five stores in its Domestic segment. During fiscal 2012, it opened 219 new stores, within its Best Buy Europe and Five Star operations, and closed 114 stores in its International segment, within its Best Buy Europe business."},{"Symbol":"BIIB","Name":"BIOGEN IDEC Inc.","Sector":"Health Care","Description":"Biogen Idec Inc. (Biogen Idec) is a global biotechnology company. The Company discovers, develops, manufactures and markets therapies for the treatment of neurodegenerative diseases, hemophilia and autoimmune disorders. The Company's products include AVONEX, TYSABRI, FAMPYRA, FUMADERM and RITUXAN. In September 2011, the Company acquired from the Dompe Group the noncontrolling interests in its joint venture sales affiliates in Italy and Switzerland. In April 2013, Elan Corporation, plc closed the TYSABRI (natalizumab) Collaboration Transaction with Biogen Idec."},{"Symbol":"BLK","Name":"BlackRock","Sector":"Financials","Description":"BlackRock, Inc. (BlackRock) is an investment management firm. The Company provides a range of investment and risks management services. The Company's clients include retail, high net worth (HNW) and institutional investors, consists of pension funds, official institutions, endowments, insurance companies, corporations, financial institutions, central banks and sovereign wealth funds. The Company's platform enables the Company to offer active (alpha) investments with index (beta) products and risk management to develop tailored solutions for clients. Its product range includes single- and multi-asset class portfolios investing in equities, fixed income, alternatives and/or money market instruments. On February 14, 2013, BlackRock Inc sold its entire interest in Avocet Mining plc. In July 2013, it announced that it has completed the acquisition of Credit Suisse's exchange traded funds (ETF) business. In October 2013, BlackRock Inc acquired Macquarie Global Property Advisors Ltd."},{"Symbol":"HRB","Name":"Block H&R","Sector":"Financials","Description":"H&R Block, Inc. (H&R Block) through its subsidiaries provides tax preparation and banking services. The Company's Tax Services segment provides assisted income tax return preparation, digital tax solutions and other services and products related to income tax return preparation to the general public primarily in the United States and its territories, Canada and Australia. This segment also offers financial services through H&R Block Bank (HRB Bank). In addition to the Company's Tax Services segment, the Company separately reports corporate operations which include net interest margin and gains or losses relating to mortgage loans held for investment, real estate owned and residual interests in securitizations, along with interest expense on borrowings, other corporate expenses and eliminations of intercompany activities. Through ownership of HRB Bank, the Company is a savings and loans holding company (SLHC)."},{"Symbol":"BA","Name":"Boeing Company","Sector":"Industrials","Description":"The Boeing Company (Boeing) is an aerospace company. The Company operates in five segments: Commercial Airplanes, Boeing Military Aircraft (BMA), Network & Space Systems (N&SS), Global Services & Support (GS&S) and Boeing Capital Corporation (BCC). Boeing Defence, Space & Security (BDS) consists of three capabilities-driven businesses: BMA, N&SS and GS&S. Its Other segment includes the unallocated activities of engineering, operations and technology (EO&T) and Shared Services Group (SSG), as well as intercompany guarantees provided to BCC. EO&T provides Boeing with technical and functional capabilities, including information technology, research and development, test and evaluation, technology strategy development, environmental remediation management and intellectual property management. In February 2013, the Company acquired CPU Technology Inc.'s Acalis business."},{"Symbol":"BWA","Name":"BorgWarner","Sector":"Consumer Discretionary","Description":"BorgWarner Inc. is a global supplier of engineered automotive systems and components primarily for powertrain applications. The Company's products are manufactured and sold worldwide, primarily to original equipment manufacturers (OEMs) of light vehicles (passenger cars, sport-utility vehicles (SUVs), vans and light-trucks). The Company's products are also sold to other OEMs of commercial vehicles (medium-duty trucks, heavy-duty trucks and buses) and off-highway vehicles (agricultural and construction machinery and marine applications). It also manufactures and sells its products to certain Tier One vehicle systems suppliers and into the aftermarket for light, commercial and off-highway vehicles. On January 31, 2011, the Company acquired 100% of the stock of Haldex Traction Holding AB (Haldex Traction Systems) of Haldex Group. In July 2012, the Company sold its spark plug business to Federal-Mogul Corporation."},{"Symbol":"BXP","Name":"Boston Properties","Sector":"Real Estate","Description":"Boston Properties, Inc. is an integrated, self-administered and self-managed real estate investment trust (REIT). The Company is an owner and developer of office properties in the United States. The Company's properties are concentrated in five markets: Boston, New York, Princeton, San Francisco and Washington, DC. As of December 31, 2012, the Company owned or had interests in 157 properties, totaling approximately 44.4 million net rentable square feet, including seven properties under construction totaling approximately 2.8 million net rentable square feet. In March 2012, the Company acquired 100 Federal Street in Boston, Massachusetts from an affiliate of Bank of America, N.A. On March 1, 2012, the Company acquired 453 Ravendale Drive. On October 4, 2012, the Company acquired Fountain Square. In June 2013, Sungate Fifth Avenue LLC and 767 LLC, acquired a 40% interest in the General Motors Building located in New York, from joint venture partners of Boston Properties Inc."},{"Symbol":"BSX","Name":"Boston Scientific","Sector":"Health Care","Description":"Boston Scientific Corporation is a developer, manufacturer and marketer of medical devices that are used in a range of interventional medical specialties. During the year ended December 31, 2011, its products were offered for sale by seven core businesses: Interventional Cardiology, CRM, Endoscopy, Peripheral Interventions, Urology/Women's Health, Neuromodulation, and Electrophysiology. In January 2011, it completed the acquisition of Intelect Medical, Inc. In June 2012, the Company acquired Cameron Health, Inc. of San Clemente, California and, as a result, added to its product portfolio subcutaneous implantable cardioverter defibrillator, called the S-ICD System. In November 2013, the Company announced that it has acquired Bard EP, the electrophysiology (EP) business of C.R. Bard, Inc."},{"Symbol":"BMY","Name":"Bristol-Myers Squibb","Sector":"Health Care","Description":"Bristol-Myers Squibb Company (BMS) is engaged in the discovery, development, licensing, manufacturing, marketing, distribution and sale of biopharmaceutical products on a global basis. It operates in BioPharmaceuticals. Its products are sold worldwide, primarily to wholesalers, retail pharmacies, hospitals, government entities and the medical profession. It manufactures products in the United States (U.S.), Puerto Rico and in 6 foreign countries. In February 2012, it acquired Inhibitex, Inc. (Inhibitex). In August 2012, the Company announced the acquisition of Amylin Pharmaceuticals, Inc. In August 2012, Synergy Pharmaceuticals Inc announced that it signed an Asset Purchase Agreement with the Company and acquired the assets related to FV-100, an orally available nucleoside analogue, being developed for the treatment of shingles."},{"Symbol":"AVGO","Name":"Broadcom","Sector":"Information Technology","Description":"Avago Technologies Limited (Avago) is a designer, developer and global supplier of a range of analog semiconductor devices with a focus on III-V based products. Its product portfolio is extensive and includes over 6,500 products that it sells into four primary target markets: wireless communications, wired infrastructure, industrial and automotive electronics, and consumer and computing peripherals. Applications for its products in these target markets include cellular phones, consumer appliances, data networking and telecommunications equipment, enterprise storage and servers, renewable energy and smart power grid applications, factory automation, displays, optical mice and printers. In April 2013, Avago Technologies Ltd acquired Javelin Semiconductor Inc. In June 2013, Avago Technologies Ltd announced that it has completed its acquisition of CyOptics, Inc."},{"Symbol":"BF.B","Name":"Brown-Forman Corp.","Sector":"Consumer Staples","Description":""},{"Symbol":"CHRW","Name":"C. H. Robinson Worldwide","Sector":"Industrials","Description":"C.H. Robinson Worldwide, Inc. (C.H. Robinson) is a third party logistics company. The Company provides freight transportation services and logistics solutions to companies of all sizes, in a range of industries. The Company operates through a network of 276 offices, which the Company calls branches, in North America, Europe, Asia, South America, and Australia. The Company has developed global transportation and distribution networks to provide transportation and supply chain services worldwide. In addition to transportation, the Company provides sourcing services (Sourcing). The Company's Sourcing business is primarily the buying, selling, and marketing of fresh produce. The Company supplies fresh produce through its network of independent produce growers and suppliers. The Company's customers include grocery retailers and restaurants, produce wholesalers, and foodservice distributors."},{"Symbol":"CA","Name":"CA, Inc.","Sector":"Information Technology","Description":"CA, Inc. (CA) is an independent enterprise information technology (IT) management software and solutions company. CA develops and delivers software and services. The Company operates in three segments: Mainframe Solutions, Enterprise Solutions and Services. The Company addresses components of the computing environment, including people, information, processes, systems, networks, applications and databases, across hardware and software platforms and programs. It offers a portfolio of software solutions, including mainframe; service assurance; security (identity and access management); service and portfolio management, and virtualization and service automation. It delivers its products on-premises or, for certain products, using Software-as-a-Service (SaaS). In June 2013, the Company announced it has completed the acquisition of privately-held Layer 7 Technologies."},{"Symbol":"COG","Name":"Cabot Oil & Gas","Sector":"Energy","Description":"Cabot Oil & Gas Corporation is an independent oil and gas company engaged in the development, exploitation and exploration of oil and gas properties. The Company's exploration, development and production operations are primarily concentrated in three plays: the Marcellus Shale in Pennsylvania, the Eagle Ford in south Texas and the Marmaton oil play in Oklahoma. The Company also has non-core operations in various other unconventional and conventional plays throughout the continental United States. The Company's assets are concentrated in areas with hydrocarbon resources, which are conducive to multi-well, repeatable drilling programs. In December 2012, the Company sold certain proved oil and gas properties located in south Texas. In June 2012, the Company sold a 35% non-operated working interest associated with certain of its Pearsall Shale undeveloped leaseholds in south Texas."},{"Symbol":"CPB","Name":"Campbell Soup","Sector":"Consumer Staples","Description":"Campbell Soup Company (Campbell) together with its subsidiaries, is a manufacturer and marketer of branded convenience food products. The Company operates in five segments: U.S. Simple Meals; Global Baking and Snacking; International Simple Meals and Beverages; U.S. Beverages; and North America Foodservice. In June 2012, the Company purchased 1300 Admiral Wilson Boulevard in Camden. On August 6, 2012, the Company completed the acquisition of BF Bolthouse Holdco LLC (Bolthouse Farms). In September 2012, Vilmorin & Cie SA acquired the tomato and pepper breeding and sales business of the Company. In June 2013, Campbell Soup Co completed the acquisition of Plum Organics. In August 2013, Campbell Soup Company completed the acquisition of Kelsen Group A/S. In October 2013, it completed the sale of its European simple meals business to CVC Capital Partners."},{"Symbol":"COF","Name":"Capital One Financial","Sector":"Financials","Description":"Capital One Financial Corporation is a diversified financial services holding company with banking and non-banking subsidiaries. Capital One Financial Corporation and its subsidiaries offer an array of financial products and services to consumers, small businesses and commercial clients through branches, the Internet and other distribution channels. As of December 31, 2012, the Company's principal subsidiaries included Capital One Bank (USA), National Association (COBN), which offers credit and debit card products, other lending products and deposit products, and Capital One, National Association (CONA), which offers a spectrum of banking products and financial services to consumers, small businesses and commercial clients. On February 17, 2012, the Company acquired ING Direct business in the United States (ING Direct) from ING Groep N.V., ING Bank N.V., ING Direct N.V. and ING Direct Bancorp. In November 2013, Capital One Financial Corp acquired Beech Street Capital LLC."},{"Symbol":"CAH","Name":"Cardinal Health Inc.","Sector":"Health Care","Description":"Cardinal Health, Inc. is a healthcare services company providing products and services that help pharmacies, hospitals, surgery centers, physician offices and other healthcare providers. During the fiscal year ended June 30, 2012 (fiscal 2012), the Company operated in two segments: Pharmaceutical and Medical. The Pharmaceutical segment distributes branded and generic pharmaceutical, over-the-counter healthcare, and consumer products through its pharmaceutical distribution business to retailers. The Medical segment distributes a range of medical, surgical and laboratory products to hospitals, surgery centers, laboratories, physician offices and other healthcare providers. Effective March 18, 2013, it merged with AssuraMed Holding Inc."},{"Symbol":"KMX","Name":"Carmax Inc","Sector":"Consumer Discretionary","Description":"CarMax, Inc. (CarMax) is a holding company and its operations are conducted through its subsidiaries. CarMax also wholesales used vehicles. The Company is a retailer of used cars, based on the 408,080 used vehicles it retailed during the fiscal year ended February 29, 2012 (fiscal 2012). As of February 29, 2012, it operated 108 used car superstores in 53 metropolitan markets. In addition, it is a wholesale vehicle auction operator. It sold 316,649 wholesale vehicles through its on-site auctions in fiscal 2012. As of February 29, 2012, the Company conducted weekly or bi-weekly auctions at 52 of its 108 used car superstores. In addition, it sells new vehicles at four locations under franchise agreements with three new car manufacturers. In fiscal 2012, new vehicles comprised only 2% of its total retail vehicle unit sales."},{"Symbol":"CCL","Name":"Carnival Corp.","Sector":"Consumer Discretionary","Description":"Carnival Corporation is a cruise company. The Company operates in two segments: North America and Europe, Australia & Asia (EAA). Its North America segment cruise brands include Carnival Cruise Lines, Holland America Line, Princess Cruises (Princess) and Seabourn. Its EAA segment cruise brands include AIDA Cruises (AIDA), Costa Cruises (Costa), Cunard, Ibero Cruises (Ibero), P&O Cruises (Australia) and P&O Cruises (United Kingdom). In addition to its cruise brands, it has a Cruise Support segment that includes its cruise port and related facilities located in Cozumel, Mexico; Grand Turk, Turks and Caicos Islands and Roatn, Honduras. In addition to its cruise operations, it owns Holland America Princess Alaska Tours, a tour company in Alaska and the Canadian Yukon. As of November 30, 2012, tour company owned and operated, among other things, 12 hotels or lodges, over 300 motorcoaches and 20 domed rail cars."},{"Symbol":"CAT","Name":"Caterpillar Inc.","Sector":"Industrials","Description":"Caterpillar Inc. (Caterpillar) is a manufacturer of construction and mining equipment, diesel and natural gas engines, industrial gas turbines and diesel-electric locomotives. It operates in two segments: Machinery and Power Systems, and Financial Products. Machinery and Power Systems represents a total of Construction Industries, Resource Industries, Power Systems and All Other segments and related corporate items and eliminations. Financial Products includes the Company's Financial Products Segment. This category includes Cat Financial and Caterpillar Insurance Holdings Inc. Effective March 1, 2012, it announced that Caterpillar Japan Ltd. acquired Caterpillar Tohoku Ltd. In August 2012, Platinum Equity, LLC. acquired a majority interest in Caterpillar Logistics Services. Caterpillar will retain a 35% interest in the business. In October 2012, Finning International Inc acquired from Caterpillar of the former Bucyrus distribution and support business in its dealership territory."},{"Symbol":"CBOE","Name":"CBOE Holdings","Sector":"Financials","Description":"CBOE Holdings, Inc. (CBOE Holdings) is the holding company. The primary business of the Company is the operation of markets for the trading of listed, or exchange-traded, options contracts on three broad product categories: the stocks of individual corporations (equity options), various market indexes (index options) and other exchange-traded products (ETP options), such as exchange-traded funds (ETF options) and exchange-traded notes (ETN options). The Company offers futures products through its futures market. In 2012, the Company introduced S&P 500 Range options on CBOE, and the CBOE NASDAQ-100 Volatility Index futures and S&P 500 Variance futures, all traded on CFE. Security futures and options on the CBOE Emerging Markets ETF Volatility Index, the CBOE Brazil ETF Volatility Index and the CBOE Crude Oil ETF Volatility Index were also launched on CFE and CBOE, respectively."},{"Symbol":"CBG","Name":"CBRE Group","Sector":"Real Estate","Description":"CBRE Group, Inc, is a holding company that conducts all of its operations through its indirect subsidiaries. The Company is a commercial real estate services firm. The Company offers a range of services to occupiers, owners, lenders and investors in office, retail, industrial, multi-family and other types of commercial real estate.The Company operates in five segments: Americas, Europe, Middle East and Africa (EMEA), Asia Pacific, Global Investment Management and Development Services. In June 2013, the Company acquired SOGESMAINT-CBRE. Effective September 2, 2013, CBRE Group Inc acquired an undisclosed majority interest in Basale Sverige AB, from Basale Gruppen AS, In September 2013, CBRE Group Inc acquired Fameco. In November 2013, CBRE Group Inc acquired Alan Selby & Partners LLP. In December 2013, CBRE Group Inc acquired The CAC Group."},{"Symbol":"CBS","Name":"CBS Corp.","Sector":"Consumer Discretionary","Description":"CBS Corporation is a mass media company. The Company has operations in segments, which include Entertainment, Cable Networks, Publishing, Local Broadcasting and Outdoor. During the year ended December 31, 2011, contributions to the Company's consolidated revenues from its segments were Entertainment 52%, Cable Networks 11%, Publishing 6%, Local Broadcasting 19% and Outdoor 13%. During 2011, it generated approximately 15% of its total revenues from international regions. Effective March 26, 2013, the Company acquired 50% interest in The TV Guide Network from Lions Gate Entertainment Corp. In June 2013, the Company acquired TV Guide Digital, which includes the popular TVGuide.com and TV Guide Mobile properties. In October 2013, Platinum Equity and CBS Corporation announced that an affiliate of Platinum Equity acquired the assets of CBS Outdoor International (CBSO International)."},{"Symbol":"CELG","Name":"Celgene Corp.","Sector":"Health Care","Description":"Celgene Corporation is a global biopharmaceutical company engaged in the discovery, development and commercialization of therapies designed to treat cancer and immune-inflammatory related diseases. It is engaged in the research and development, which is designed to bring new therapies to market, and is engaged in research in several scientific areas that may deliver therapies, focusing areas, such as intracellular signaling pathways in cancer and immune cells, immunomodulation in cancer and autoimmune diseases, and therapeutic application of cell therapies. Its primary commercial stage products include REVLIMID, VIDAZA, THALOMID, ABRAXANE and ISTODAX. Additional sources of revenue include a licensing agreement with Novartis, which entitles it to royalties on FOCALIN XR and the entire RITALIN family of drugs, the sale of services through its Cellular Therapeutics subsidiary and other miscellaneous licensing agreements. In March 2012, it acquired Avila Therapeutics."},{"Symbol":"CNC","Name":"Centene Corporation","Sector":"Health Care","Description":"Centene Corporation is a multi-line healthcare enterprise, which provides programs and services to the number of under-insured and uninsured individuals. The Company provides member-focused services through locally based staff by assisting in accessing care, coordinating referrals to related health and social services and addressing member concerns and questions. It also provides education and outreach programs to inform and assist members in accessing appropriate healthcare services. It combines its decentralized local approach for care with a centralized infrastructure of support functions, such as finance, information systems and claims processing. The Company operates in two segments: Medicaid Managed Care and Specialty Services. Effective April 1, 2013, it acquired Specialty Therapeutic Care Holdings Inc doing business as AcariaHealth."},{"Symbol":"CNP","Name":"CenterPoint Energy","Sector":"Utilities","Description":"CenterPoint Energy, Inc. is a domestic energy delivery company. The Company's business segments include Electric Transmission and Distribution, Natural Gas Distribution, Competitive Natural Gas Sales and Services, Interstate Pipelines, Field Services and Other Operations. The Company serves metered customers primarily in Arkansas, Louisiana, Minnesota, Mississippi, Oklahoma and Texas. The company also owns a 58.3% interest in a midstream partnership it jointly controls with OGE Energy Corp. with operations in natural gas and liquids-rich producing areas of Oklahoma, Texas, Arkansas and Louisiana."},{"Symbol":"CTL","Name":"CenturyLink Inc","Sector":"Telecommunications Services","Description":"CenturyLink, Inc. (CenturyLink) is an integrated communications company. The Company is engaged primarily in providing a range of communications services to its residential, business, governmental and wholesale customers. The Company's communications services include local and long-distance, network access, private line, public access, broadband, data, managed hosting (including cloud hosting), colocation, Wireless and video services. In certain local and regional markets, the Company also provides local access and fiber transport services to competitive local exchange carriers (CLECs) and security monitoring. The Company operates in four segments: Regional markets, Wholesale markets, Enterprise markets-network and Enterprise markets-data hosting. In November 2013, CenturyLink Inc acquired Tier 3 LLC."},{"Symbol":"CERN","Name":"Cerner","Sector":"Health Care","Description":"Cerner Corporation is a supplier of healthcare information technology solutions, services, devices and hardware. Cerner solutions optimize processes for healthcare organizations. These solutions are licensed by 9,300 facilities globally, including more than 2,650 hospitals; 3,750 physician practices 40,000 physicians; 500 ambulatory facilities, such as laboratories, ambulatory centers, cardiac facilities, radiology clinics and surgery centers; 800 home health facilities; 40 employer sites and 1,600 retail pharmacies. It operates in two segments: domestic, which includes revenue contributions and expenditures associated with business activity in the United States, and global, which includes revenue contributions and expenditures linked to business activity in Argentina, Aruba, Canada, Cayman Islands, Chile, Puerto Rico, Saudi Arabia, Singapore, Spain and the United Arab Emirates. Effective March 18, 2013, it acquired Labotix Automation Inc."},{"Symbol":"CF","Name":"CF Industries Holdings Inc","Sector":"Materials","Description":"CF Industries Holdings, Inc. is a manufacturer and distributor of nitrogen and phosphate fertilizer products worldwide. The Company operates in two segments: the nitrogen segment and the phosphate segment. The Company's principal products in the nitrogen segment are ammonia, granular urea, urea ammonium nitrate solution (UAN) and ammonium nitrate (AN).Its other nitrogen products include urea liquor, diesel exhaust fluid (DEF) and aqua ammonia, which are sold primarily to its industrial customers. Its principal products in the phosphate segment are diammonium phosphate (DAP) and monoammonium phosphate (MAP). During the year ended December 31, 2012, the Company sold 13.0 million tons of nitrogen fertilizers and 2.0 million tons of phosphate fertilizers. In May 2013, CF Industries Holdings Inc acquired 34% stake in Canadian Fertilizers Ltd."},{"Symbol":"SCHW","Name":"Charles Schwab Corporation","Sector":"Financials","Description":"The Charles Schwab Corporation (CSC) is a savings and loan holding company. The Company engages, through its subsidiaries, in securities brokerage, banking, and related financial services. The Company provides financial services to individuals and institutional clients through two segments: Investor Services and Institutional Services. The Investor Services segment provides retail brokerage and banking services to individual investors. The Institutional Services segment provides custodial, trading and support services to independent investment advisors (IAs). Its business subsidiaries include Charles Schwab & Co., Inc. (Schwab), Charles Schwab Bank and Charles Schwab Investment Management, Inc. (CSIM), which is the investment advisor for Schwab's mutual funds. In November 2011, the Company acquired Compliance11, Inc. In September 2012, GAIN Capital Holdings Inc. acquired Open E Cry, LLC. In December 2012, the Company acquired ThomasPartners, Inc."},{"Symbol":"CHTR","Name":"Charter Communications","Sector":"Consumer Discretionary","Description":"Charter Communications, Inc. (Charter) provides cable services in the United States, offering a range of entertainment, information and communications solutions to residential and commercial customers. Its infrastructure consists of a hybrid fiber coaxial cable plant passing approximately 12 million homes, with 98% of homes passed at 550 megahertz or greater and 98% of plant miles two-way active. A national Internet protocol (IP) infrastructure interconnects Charter markets. During the year ended December 31, 2011, it generated 50% revenue from its residential video service. During 2011, it also generated revenue from Internet, telephone service and advertising. As of December 31, 2011, it served approximately 5.2 million residential and commercial customers. In July 2013, Charter Communications Inc and Cablevision Systems Corp announced the completion of acquisition of Cablevision's Bresnan Broadband Holdings, LLC (Optimum West)."},{"Symbol":"CHK","Name":"Chesapeake Energy","Sector":"Energy","Description":"Chesapeake Energy Corporation (Chesapeake) is a natural gas and oil exploration and production company. Chesapeake is engaged in the exploration, development and acquisition of properties for the production of natural gas and oil from underground reservoirs. It also provides substantial marketing, midstream, drilling and other oilfield services. Its operations are located onshore and in the continental United States. As of December 31, 2011, the Company owned interests in approximately 45,700 producing natural gas and oil wells that produced approximately 3.5 billion cubic feet of natural gas equivalent per day, net to its interest. The Company operates in three segments: exploration and production; marketing, gathering and compression, and oilfield services. In June 2011, the Company acquired Bronco Drilling Company, Inc. In August 2013, SemGroup Corporation completed acquisition of the gas gathering and processing assets owned by Chesapeake Energy Corporation."},{"Symbol":"CVX","Name":"Chevron Corp.","Sector":"Energy","Description":"Chevron Corporation (Chevron) manages its investments in subsidiaries and affiliates and provides administrative, financial, management and technology support to the United States and international subsidiaries that engage in fully integrated petroleum operations, chemicals operations, mining activities, power generation and energy services. Upstream operations consist primarily of exploring for, developing and producing crude oil and natural gas; processing, transportation and regasification associated with liquefied natural gas; transporting crude oil by international oil export pipelines; transporting, storage and marketing of natural gas, and a gas-to-liquids project. Downstream operations consist primarily of refining crude oil into petroleum products; transporting crude oil and refined products by pipeline, marine vessel, motor equipment and rail car, and manufacturing and marketing of commodity petrochemicals, plastics for industrial uses and fuel and lubricant additives."},{"Symbol":"CMG","Name":"Chipotle Mexican Grill","Sector":"Consumer Discretionary","Description":"Chipotle Mexican Grill, Inc. (Chipotle) operates Chipotle Mexican Grill restaurants. Chipotle Mexican Grill restaurants serve a menu of burritos, tacos, burrito bowls (a burrito without the tortilla) and salads. As of December 31, 2012, the Company operated 1,410 restaurants, including Chipotle restaurants throughout the United States, as well as five in Canada, five in London, England, and one in Paris, France, and also one ShopHouse Southeast Asian Kitchen, a restaurant in Washington, D.C. Chipotle categorizes its restaurants as either end-caps (at the end of a line of retail outlets), in-lines (in a line of retail outlets), free-standing or other. As of December 31, 2012, of its restaurants in operation, the Company had 258 free-standing units, 861 end-cap locations, 233 in-line locations and 58 other. The average restaurant size is about 2,535 square feet and seats about 56 people. Most of its restaurants also feature outdoor patio space."},{"Symbol":"CB","Name":"Chubb Limited","Sector":"Financials","Description":"The Chubb Corporation (Chubb) is a holding company for a family of property and casualty insurance companies known as the Chubb Group of Insurance Companies (the P&C Group). The P&C Group provides property and casualty insurance to businesses and individuals worldwide. The P&C Group is divided into three business units: Chubb Personal Insurance, Chubb Commercial Insurance and Chubb Specialty Insurance. Chubb Personal Insurance offers coverage of fine homes, automobiles and other personal possessions along with options for high limits of personal liability coverage. Chubb Commercial Insurance offers a range of commercial insurance products, including coverage for multiple peril, casualty, workers' compensation and property and marine. Chubb Specialty Insurance offers a variety of professional liability products for privately and publicly owned companies, financial institutions, professional firms and healthcare organizations. Chubb Specialty Insurance also includes its surety business."},{"Symbol":"CHD","Name":"Church & Dwight","Sector":"Consumer Staples","Description":"Church & Dwight Co., Inc. develops, manufactures and markets a range of household, personal care and specialty products. The Company's brands include ARM & HAMMER, (used in multiple product categories, such as baking soda, carpet deodorization and laundry detergent), TROJAN Condoms, XTRA laundry detergent, OXICLEAN pre-wash laundry additive, NAIR depilatories, FIRST RESPONSE home pregnancy and ovulation test kits, ORAJEL oral analgesics and SPINBRUSH battery-operated toothbrushes. The Company operates in three segments: Consumer Domestic, Consumer International and Specialty Products. During the year ended December 31, 2011, the Consumer Domestic, Consumer International and Specialty Products segments represented approximately 72%, 19% and 9%, respectively, of the Company's net sales. On June 28, 2011, the Company acquired the BATISTE dry shampoo brand from Vivalis, Limited. In October 2012, it acquired Avid Health, Inc. (Avid)."},{"Symbol":"CI","Name":"CIGNA Corp.","Sector":"Health Care","Description":"Cigna Corporation (Cigna) is a holding company. Cigna is a global health service company, with insurance subsidiaries that are providers of medical, dental, disability, life and accident insurance and related products and services. In the United States, these products and services are offered through employers and other groups, and in selected international markets, Cigna offers supplemental health, life and accident insurance products and international health care coverage and services to businesses, governmental and non-governmental organizations and individuals. Cigna operates in five segments: Health Care, Disability and Life, International, Run-off Reinsurance, and Other Operations, including Corporate-owned Life Insurance. September 2013, Cigna Corporation completed its acquisition of Alegis Care, a portfolio company of Triton Pacific Capital Partners. Effective September 3, 2013, Cigna Corp acquired Home Physicians Management LLC."},{"Symbol":"XEC","Name":"Cimarex Energy","Sector":"Energy","Description":"Cimarex Energy Co. is an oil and gas exploration and production company. Its operations are focused in two main areas: the Mid-Continent region and the Permian Basin. The Mid-Continent region consists of Oklahoma, the Texas Panhandle, and southwest Kansas. Its Permian Basin region encompasses west Texas and southeast New Mexico. It also has operations in the Gulf Coast area, primarily in southeast Texas. As of December 31, 2012, its proved oil and gas reserves totaled 2.3 trillion cubic feet equivalent, consisting of 1.3 trillion cubic feet of gas and 168 million barrels of oil and natural gas liquids (liquids). Of total proved reserves, 45% are liquids and 80% are classified as proved developed. During the year ended December 31, 2012, its production averaged 626.5 million cubic feet equivalent per day, consisted of 323.8 million cubic feet of gas and 50,457 barrels of liquids. The wells it operates account for 68% of total proved reserves and approximately 80% of production."},{"Symbol":"CINF","Name":"Cincinnati Financial","Sector":"Financials","Description":"Cincinnati Financial Corporation is engaged in property casualty insurance marketed through independent insurance agents in 39 states. During the year ended December 31, 2012, the Company owned 100% of three subsidiaries: The Cincinnati Insurance Company, CSU Producer Resources Inc., and CFC Investment Company. The Company operates in five segments: Commercial lines property casualty insurance, Personal lines property casualty insurance, Excess and surplus lines property casualty insurance, Life insurance and Investments. Its standard market property casualty insurance group includes two subsidiaries: The Cincinnati Casualty Company and The Cincinnati Indemnity Company. This group writes a range of business, homeowner and auto policies."},{"Symbol":"CTAS","Name":"Cintas Corporation","Sector":"Industrials","Description":"Cintas Corporation (Cintas) provides specialized products and services to businesses of all types throughout the North America, Latin America, Europe and Asia. The Company operates in four segments: Rental Uniforms and Ancillary Products, Uniform Direct Sales, First Aid, Safety and Fire Protection Services, and Document Management Services. As of May 31, 2013, the Company provided products and services to over one million businesses. As of May 31, 2013, Cintas had approximately 8,200 local delivery routes, 446 operational facilities and eight distribution centers."},{"Symbol":"CSCO","Name":"Cisco Systems","Sector":"Information Technology","Description":"Cisco Systems, Inc. (Cisco) designs, manufactures, and sells Internet protocol (IP)-based networking and other products related to the communications and information technology (IT) industry and provide services associated with these products and their use. The Company provides a line of products for transporting data, voice, and video within buildings, across campuses, and worldwide. Its products are designed to transform how people connect, communicate, and collaborate. Its products are installed at enterprise businesses, public institutions, telecommunications companies, commercial businesses, and personal residences. The Company operates in three segments: The Americas; Europe, Middle East, and Africa (EMEA), and Asia Pacific, Japan, and China (APJC). In October 2013, Cisco Systems Inc completed the acquisition of Sourcefire, Inc. In October 2013, the Company announced that it has completed the acquisition of privately held WHIPTAIL."},{"Symbol":"C","Name":"Citigroup Inc.","Sector":"Financials","Description":"Citigroup Inc. (Citigroup) is a global diversified financial services holding company whose businesses provide consumers, corporations, governments and institutions with a broad range of financial products and services, including consumer banking and credit, corporate and investment banking, securities brokerage, transaction services and wealth management. The Company operates in two segments: Citicorp, consisting of the Company's Global Consumer Banking businesses and Institutional Clients Group, and Citi Holdings, consisting of Brokerage and Asset Management, Local Consumer Lending and Special Asset Pool. In February 2012, it sold its 9.85% interest in Housing Development Finance Corporation Ltd. In May 2012, it sold 10.1% interest in Akbank T.A.S. In September 2012, the Company sold EMI Group's recorded music division, EMI Music, to Vivendi and subsidiary, Universal Music Group. In August 2013, Pershing Square Capital Management sold 18% interest to Citigroup Inc."},{"Symbol":"CFG","Name":"Citizens Financial Group","Sector":"Financials","Description":"Citizens Financial Group, Inc. operates as the bank holding company for Citizens Bank, N.A. and Citizens Bank of Pennsylvania that provide retail and commercial banking products and services in the United States. It operates through two segments, Consumer Banking and Commercial Banking. The Consumer Banking segment focuses on retail customers and small businesses with traditional banking products and services, including checking, savings, home loans, student loans, credit cards, business loans, and financial management services. This segment also provides indirect auto finance for new and used vehicles through automotive dealerships. The Commercial Banking segment provides various financial products and solutions, including loans, leases, trade financing, deposits, cash management, foreign exchange, interest rate risk management, corporate finance, and capital markets advisory capabilities. It focuses on small and middle-market companies, and serves government banking, not-for-profit, healthcare, technology, asset finance, franchise finance, asset-based lending, commercial real estate, private equity, and sponsor finance industries. As of December 31, 2014, the company operated 1,200 branches in 11 states across the New England, Mid-Atlantic, and Midwest regions, as well as online, telephone, and mobile banking platforms. It also maintains approximately 100 retail and commercial non-branch offices located in 11 other states and the District of Columbia outside its branch footprint. The company was formerly known as RBS Citizens Financial Group, Inc. and changed its name to Citizens Financial Group, Inc. in April 2014. The company was founded in 1828 and is headquartered in Providence, Rhode Island."},{"Symbol":"CTXS","Name":"Citrix Systems","Sector":"Information Technology","Description":"Citrix Systems, Inc. is a cloud computing company. The Company designs, develops and markets technology solutions that enable information technology (IT) services. The Enterprise division and the Online Services division constitute its two segments. Its revenues are derived from sales of Enterprise division products, which include its Desktop Solutions, Datacenter and Cloud Solutions, Cloud-based Data Solutions and related technical services and from its Online Services division's Web collaboration, remote access and support services. It markets and licenses its products directly to enterprise customers, over the Web, and through systems integrators (Sis) in addition to indirectly through value-added resellers (VARs), value-added distributors (VADs) and original equipment manufacturers (OEMs). In January 2013, the Company acquired Zenprise."},{"Symbol":"CME","Name":"CME Group Inc.","Sector":"Financials","Description":"CME Group Inc. (CME Group) offer a range of global products across all asset classes based on interest rates, equity indexes, foreign exchange, energy, agricultural commodities, metals, weather and real estate. The Company's products include both exchange-traded and over-the-counter derivatives. CME Group also provides clearing and settlement services for exchange-traded contracts, as well as for cleared over-the-counter derivatives transactions. In April 2012, it acquired a 100% interest in GreenX Holdings LLC. On July 2, 2012, S&P Capital IQ, a business line of The McGraw-Hill Companies acquired London-based Credit Market Analysis Limited (CMA) from CME Group. In November 2013, CME Group Inc announced the sale of the NYMEX Building located at One North End Avenue to Brookfield Office Properties."},{"Symbol":"CMS","Name":"CMS Energy","Sector":"Utilities","Description":"CMS Energy Corporation (CMS Energy) is an energy company operating primarily in Michigan. CMS Energy is the parent holding company of several subsidiaries, including Consumers Energy Company (Consumers) and CMS Enterprises Company (CMS Enterprises). Consumers is an electric and gas utility, and CMS Enterprises, primarily a domestic independent power producer. Consumers serves individuals and businesses operating in the alternative energy, automotive, chemical, metal, and food products industries, as well as a diversified group of other industries. CMS Enterprises, through its subsidiaries and equity investments, is engaged primarily in independent power production and owns power generation facilities fueled mostly by natural gas and biomass. CMS Energy operates in three business segments: electric utility, gas utility and enterprises, its non-utility operations and investments."},{"Symbol":"COH","Name":"Coach Inc.","Sector":"Consumer Discretionary","Description":"Coach, Inc. (Coach) is a marketer of accessories and gifts for women and men. The Company offers a range of modern, fashionable handbags and accessories. Its product offerings include women's and men's bags, accessories, footwear, wearables, jewelry, travel bags, sunwear, watches and fragrance. The Company operates in two segments: North America, which includes sales to North American consumers through Company-operated stores, including the Internet, and sales to wholesale customers and distributors and International, which includes sales to consumers through Company-operated stores in Japan and mainland China, including the Internet, Hong Kong, Macau, Singapore, Taiwan, Malaysia and Korea and sales to wholesale customers and distributors in 25 countries."},{"Symbol":"KO","Name":"Coca Cola Company","Sector":"Consumer Staples","Description":"The Coca-Cola Company is a beverage company. The Company owns or licenses and markets more than 500 nonalcoholic beverage brands, primarily sparkling beverages but also a variety of still beverages, such as waters, enhanced waters, juices and juice drinks, ready-to-drink teas and coffees, and energy and sports drinks. It owns and markets a range of nonalcoholic sparkling beverage brands, which includes Coca-Cola, Diet Coke, Fanta and Sprite. The Company's segments include Eurasia and Africa, Europe, Latin America, North America, Pacific, Bottling Investments and Corporate. In January 2013, Sacramento Coca-Cola Bottling Company announced that it had been acquired by the Company. Effective February 22, 2013, Coca-Cola Co acquired interest in Fresh Trading Ltd. In November 2013, Coca-Cola Company and ZICO Beverages LLC announced that Coca-Cola has acquired the ownership interest in ZICO."},{"Symbol":"CTSH","Name":"Cognizant Technology Solutions","Sector":"Information Technology","Description":"Cognizant Technology Solutions Corporation (Cognizant) is a provider of custom information technology, consulting and business process outsourcing services. The Company is engaged in Business, Process, Operations and Information Technology Consulting, Application Development and Systems Integration, Enterprise Information Management (EIM), Application Testing, Application Maintenance, Information Technology Infrastructure Services, and Business and Knowledge Process Outsourcing, or BPO and KPO. The Company operates in four segments: Financial Services; Healthcare; Manufacturing, Retail and Logistics, and Other, which includes communications, information, media and entertainment, and high technology. In October 2013, Cognizant Technology Solutions of India acquired Equinox Consulting SAS."},{"Symbol":"CL","Name":"Colgate-Palmolive","Sector":"Consumer Staples","Description":"Colgate-Palmolive Company (Colgate) is a consumer products company whose products are marketed in over 200 countries and territories throughout the world. It operates in two segments: Oral, Personal and Home Care and Pet Nutrition. Oral Care business products include Colgate Total, Colgate Sensitive Pro-Relief, Colgate Max Fresh, Colgate Optic White and Colgate Luminous White toothpastes, Colgate 360' manual toothbrushes and Colgate and Colgate Plax mouth rinses. Colgate's Oral Care business also includes dental floss and pharmaceutical products for dentists and oral health professionals. Its Personal Care products also include Palmolive, Softsoap and Sanex brand shower gels, Palmolive, Irish Spring and Protex bar soaps and Speed Stick, Lady Speed Stick and Sanex deodorants and antiperspirants. It sells liquid hand soap under the Palmolive, Protex and Softsoap brands. Colgate, through its Hill's Pet Nutrition segment (Hill's), manufactures pet nutrition products for dogs and cats."},{"Symbol":"CMCSA","Name":"Comcast Corp.","Sector":"Consumer Discretionary","Description":"Comcast Corporation (Comcast) is a provider of entertainment, information and communications products and services. The Company operates in five segments: Cable Communications provides video, high-speed Internet and voice services (cable services) to residential and business customers; Cable Networks consists primarily of its national cable television networks, its regional sports and news networks, its international cable networks, its cable television production studio, and its related digital media properties; Broadcast Television consists primarily of its NBC and Telemundo broadcast networks, its NBC and Telemundo owned local television stations, its broadcast television production operations, and its related digital media properties; Filmed Entertainment consists of the operations of Universal Pictures., and Theme Parks consists primarily of its Universal theme parks in Orlando and Hollywood. Effective March 19, 2013, it acquired a 49% interest in NBCUniversal Media LLC."},{"Symbol":"CMA","Name":"Comerica Inc.","Sector":"Financials","Description":"Comerica Incorporated (Comerica) is a financial services company. The Company has three business segments: the Business Bank, the Retail Bank, and Wealth Management. Comerica operates in three primary geographic markets: Texas, California and Michigan. The Company's business bank meets the needs of middle market businesses, multinational corporations and governmental entities by offering various products and services, including commercial loans and lines of credit, deposits, cash management, capital market products, international trade finance, letters of credit, foreign exchange management services and loan syndication services. As of December 31, 2012, Comerica owned directly or indirectly all the stock of two active banking and 49 non-banking subsidiaries."},{"Symbol":"CAG","Name":"ConAgra Foods Inc.","Sector":"Consumer Staples","Description":"ConAgra Foods, Inc. is a packaged food company. The Company operates in four segments: Consumer Foods, Commercial Foods, Ralcorp Food Group, and Ralcorp Frozen Bakery Products. Consumers can find recognized brands such as Banquet, Chef Boyarde, Egg Beaters, Healthy Choice, Hebrew National, Hunt's, Marie Callender's, Odom's Tennessee Pride, Orville Redenbacher's, PAM, Peter Pan, Reddi-wip, Slim Jim, Snack Pack, and many other ConAgra Foods brands and products, along with food sold by ConAgra Foods under private brands, in grocery, convenience, mass merchandise, club stores, and drugstores. In January 2013, it acquired Ralcorp. Ralcorp manufactures private brand products. In September 2013, Brynwood Partners VI L.P. announced that its portfolio company, Lightlife Foods, Inc., had acquired Lightlife, one of ConAgra Foods' brands with product lines that include vegetarian-based burgers, hotdogs and other meatless frozen and refrigerated items."},{"Symbol":"CXO","Name":"Concho Resources","Sector":"Energy","Description":"Concho Resources Inc.( Concho) is an independent oil and natural gas company engaged in the acquisition, development and exploration of oil and natural gas properties. The Company's core operating areas are located in the Permian Basin region of Southeast New Mexico and West Texas, an onshore oil and natural gas basin in the United States. The Permian Basin is one of the oil and natural gas producing regions in the United States. In July 2012, it completed an acquisition of producing and non-producing assets from Three Rivers Operating Company LLC and certain affiliated entities. In February 2012, the Company sold its remaining Permian Basin assets to Concho Resources Inc.In December 2012, Concho Resources Inc closed its divestiture of certain non-core producing properties located in the Permian Basin of West Texas and Southeast New Mexico to Legacy Reserves LP."},{"Symbol":"COP","Name":"ConocoPhillips","Sector":"Energy","Description":"ConocoPhillips explores for, produces, transports and markets crude oil, natural gas, natural gas liquids, liquefied natural gas and bitumen on a worldwide basis. The portfolio includes assets in North America, Europe, Asia and Australia; growing North American shale and oil sands businesses; a number of international development projects, and a global exploration program. On May 1, 2012, the Company spun off the downstream portion of its operations to Phillips 66. In August 2012, it closed a transaction with LUKOIL for the sale of ConocoPhillips' indirect 30% interest in NaryanMarNefteGaz (NMNG) and certain related assets. In August 2013, the Company announced that it has closed a transaction with the National Gas Company of Trinidad and Tobago Limited (NGC) for the sale of its wholly owned subsidiary, Trinidad and Tobago Holdings LLC. In November 2013, the Company completed the transaction with Pertamina for the sale of its Algeria business."},{"Symbol":"ED","Name":"Consolidated Edison","Sector":"Utilities","Description":"Consolidated Edison, Inc. (Con Edison) is a holding company, which owns Consolidated Edison Company of New York, Inc. (CECONY), which delivers electricity, natural gas and steam to customers in New York City and Westchester County; Orange and Rockland Utilities, Inc. (O&R) (together with CECONY referred to as the Utilities), which delivers electricity and natural gas to customers primarily located in southeastern New York, and northern New Jersey and northeastern Pennsylvania, and competitive energy businesses, which provide retail and wholesale electricity supply and energy services. CECONY's business operations are its regulated electric, gas and steam delivery businesses. O&R's business operations are its regulated electric and gas delivery businesses. In July 2012, Consolidated Edison Development, a wholly owned subsidiary of Con Edison, and GCL Solar Energy Inc., a wholly owned subsidiary of GCL-Poly Energy Holdings Limited, acquired two solar photovoltaic projects."},{"Symbol":"STZ","Name":"Constellation Brands","Sector":"Consumer Staples","Description":"Constellation Brands, Inc., is a wine company. The Company is a marketer of imported beer in the United States. through its investment in Crown Imports, LLC (Crown Imports), a joint venture with Grupo Modelo, S.A.B. de C.V. (Modelo) pursuant to which Modelo's Mexican beer portfolio (the Modelo Brands) are imported, marketed and sold by the joint venture in the United States. The Company operates in three business segments: Constellation Wines North America (wine and spirits) (CWNA), Corporate Operations and Other, and Crown Imports.In October 5, 2011, the Company acquired the remaining 50.1% equity interest in Ruffino S.r.l. (Ruffino). In June 2013, Constellation Brands Inc completed its acquisition of Grupo Modelo's U.S. beer business from Anheuser-Busch InBev. In June 2013, Constellation Brands Inc acquired the remaining 50% interest in Crown Imports LLC."},{"Symbol":"GLW","Name":"Corning Inc.","Sector":"Information Technology","Description":"Corning Incorporated (Corning) is a global, technology-based corporation. The Company operates in five segments: Display Technologies, Telecommunications, Environmental Technologies, Specialty Materials and Life Sciences. During the year ended December 31, 2011, Corning launched Corning Lotus Glass, an environmentally friendly, display glass developed to enable technologies, including organic light-emitting diode (OLED) displays and next generation liquid crystal displays (LCD). Corning Lotus Glass helps support the demanding manufacturing processes of both OLED and liquid crystal displays for portable devices, such as smart phones, tablets, and notebook computers. In March 2011, the Company acquired all outstanding shares from the shareholders of MobileAccess. In December 2011, it acquired Mediatech, Inc. In November 2012, Corning acquired the majority of the Discovery Labware business from Becton, Dickinson and Company. In May 2013, the Company acquired Bargoa SA."},{"Symbol":"COST","Name":"Costco Co.","Sector":"Consumer Staples","Description":"Costco Wholesale Corporation (Costco) with its subsidiaries is engaged in the operation of membership warehouses in the United States and Puerto Rico, Canada, the United Kingdom, Mexico, Japan, Australia, and through majority owned subsidiaries in Taiwan and Korea. Costco buys the majority of its merchandise directly from manufacturers and route it to a cross-docking consolidation point (depot) or directly to its warehouses. The Company's depots receive container-based shipments from manufacturers and reallocate these goods for shipment to its individual warehouses, generally in less than 24hours. Costco's typical warehouse format averages approximately 143,000 square feet. The Company's warehouses generally operate on a seven-day, 69-hour week. The Company carries an average of approximately 3,300 to 3,800 active stock keeping units (SKUs) per warehouse in its core warehouse business. Many consumable products are offered for sale in case, carton, or multiple-pack quantities only."},{"Symbol":"COTY","Name":"Coty, Inc","Sector":"Consumer Staples","Description":"Coty Inc. is engage in the manufacturing, marketing and distribution of women's and men's fragrances, color cosmetics and skin and body care related products globally. The Company operates in three segments: Fragrances, Color Cosmetics and Skin & Body Care. The Company's power brands consists of adidas, Calvin Klein, Chloe, Davidoff, Marc Jacobs, OPI, philosophy, Playboy, Rimmel and Sally Hansen. The Company sells products in each of its segments through retailers, including hypermarkets, supermarkets, independent and chain drug stores and pharmacies, upscale perfumeries, upscale and mid-tier department stores, nail salons, specialty retailers, duty-free shops and traditional food, drug and mass retailers."},{"Symbol":"CCI","Name":"Crown Castle International Corp.","Sector":"Real Estate","Description":"Crown Castle International Corp. (CCIC) owns, operates and leases shared wireless infrastructure, including towers and other structures, such as rooftops (towers); distributed antenna systems (DAS)(each such system is a network of antennas for the benefit of wireless carriers and is connected by fiber to communication hubs designed to facilitate wireless communications), and interests in land under third party towers in various forms (third party land interests) (unless the context otherwise suggests or requires, references herein to wireless infrastructure include towers, DAS and third party land interests). Its core business is renting space or physical capacity (collectively, space) on its towers, DAS and, to a lesser extent, third party land interests (collectively, site rental business) through long-term contracts in various forms, including license, sublease and lease agreements (collectively, contracts). In April 2012, it acquired NextG Networks, Inc."},{"Symbol":"CSRA","Name":"CSRA Inc.","Sector":"Information Technology","Description":""},{"Symbol":"CSX","Name":"CSX Corp.","Sector":"Industrials","Description":"CSX Corporation (CSX), together with its subsidiaries, is a transportation supplier. The Company provides rail-based transportation services, including traditional rail service and the transport of intermodal containers and trailers. CSX's operating subsidiary, CSX Transportation, Inc. (CSXT), provides link to the transportation supply chain through its approximately 21,000 route mile rail network, which serves centers in 23 states east of the Mississippi River, the District of Columbia and the Canadian provinces of Ontario and Quebec. It has access to over 70 ocean, river and lake port terminals along the Atlantic and Gulf Coasts, the Mississippi River, the Great Lakes and the St. Lawrence Seaway. The Company's intermodal business links customers to railroads through trucks and terminals. CSXT also serves production and distribution facilities through track connections to approximately 240 short-line and regional railroads."},{"Symbol":"CMI","Name":"Cummins Inc.","Sector":"Industrials","Description":"Cummins Inc. is a diesel engine manufacturer. The Company designs, manufactures, distributes and services diesel and natural gas engines, electric power generation systems and engine-related component products, including filtration, exhaust aftertreatment, fuel systems, fuel systems, controls systems, air handling systems and electric power. The Company sells its products to original equipment manufacturers (OEMs), distributors and other customers worldwide. It serves its customers through a network of more than 600 company owned and independent distributor locations and more than 6,500 dealer locations in more than 190 countries and territories. It has four segments: Engine, Power Generation, Components and Distribution. In April 2011, the Company sold its exhaust business to Global Tube. In July 2012, the Company acquired the emission control assets of Hilite International in Marktheidenfeld. The Hilite assets are part of Cummins Emission Solution (CES)."},{"Symbol":"CVS","Name":"CVS Health","Sector":"Consumer Staples","Description":"CVS Caremark Corporation (CVS Caremark), together with its subsidiaries, is a pharmacy health care provider in the United States. CVS Caremark provides pharmacy services through its pharmacy benefit management (PBM), mail order and specialty pharmacy division, CVS Caremark Pharmacy Services; approximately 7,300 CVS/pharmacy retail stores; retail-based health clinic subsidiary, MinuteClinic, and its online retail pharmacy, CVS.com. The Company operates in three business segments: Pharmacy Services, Retail Pharmacy and Corporate. Its corporate segment provides management and administrative services to support the overall operations of the Company. In April 2012, Health Net, Inc.'s subsidiary, Health Net Life Insurance Company, sold its Medicare stand-alone Prescription Drug Plan (Medicare PDP) business to a subsidiary of CVS Caremark. In February 2013, it bought Drogaria Onofre."},{"Symbol":"DHI","Name":"D. R. Horton","Sector":"Consumer Discretionary","Description":"D.R. Horton, Inc. is the homebuilding companies in the United States. The Company constructs and sells homes through its operating divisions in 26 states and 77 metropolitan markets of the United States, primarily under the name of D.R. Horton, America's Builder. During the fiscal year ended September 30, 2012 (fiscal 2012), the Company closed 18,890 homes. Through its financial services operations, the Company provides mortgages financing and title agency services to homebuyers in many of its homebuilding markets. DHI Mortgage, its 100% owned subsidiary, provides mortgage financing services primarily to the Company's homebuilding customers and generally sells the mortgages it originates and the related servicing rights to third-party purchasers. In August 2012, it acquired the homebuilding operations of Breland Homes."},{"Symbol":"DHR","Name":"Danaher Corp.","Sector":"Health Care","Description":"Danaher Corporation (Danaher) designs, manufactures and markets professional, medical, industrial and commercial products and services. The Company's research and development, manufacturing, sales, distribution, service and administrative facilities are located in more than 50 countries. It operates in five segments: Test & Measurement; Environmental; Life Sciences & Diagnostics; Dental; and Industrial Technologies. In April 2011, the Company sold its Pacific Scientific Aerospace (PSA) business. On June 30, 2011, the Company acquired Beckman Coulter, Inc. (Beckman Coulter). In January 2012, the Company sold its Accu-Sort businesses. In February 2012, the Company sold its Kollmorgen Electro-Optical (KEO) business. In January 2013, the Company acquired Navman Wireless. Effective August 2, 2013, Danaher Corp acquired Teletrac Inc, a developer of transportation software, from Vector Capital."},{"Symbol":"DRI","Name":"Darden Restaurants","Sector":"Consumer Discretionary","Description":"Darden Restaurants, Inc. is a full service restaurant company. As of May 26, 2013, the Company operated through subsidiaries 2,138 restaurants in the United States and Canada. In the United States, it operated 2,105 restaurants in all 50 states, including 822 Olive Garden, 678 Red Lobster, 430 LongHorn Steakhouse, 49 The Capital Grille, 44 Yard House, 33 Bahama Breeze, 31 Seasons 52, nine Eddie V's Prime Seafood, six test synergy restaurants (which house both a Red Lobster and Olive Garden restaurant in the same building) and three Wildfish Seafood Grille restaurants. As of May 26, 2013, it operated 33 restaurants, including 27 Red Lobster and six Olive Garden restaurants in Canada. Through subsidiaries, the Company owns and operates all of its restaurants in the United States and Canada, except for three restaurants located in Florida and three restaurants in California."},{"Symbol":"DVA","Name":"DaVita Inc.","Sector":"Health Care","Description":"DaVita HealthCare Partners Inc., formerly DaVita Inc., is a provider of dialysis services in the United States for patients suffering from chronic kidney failure, also known as end stage renal disease (ESRD). As of December 31, 2011, the Company provided dialysis and administrative services through a network of 1,809 outpatient dialysis centers located in the United States throughout 43 states and the District of Columbia, serving a total of approximately 142,000 patients. It also provides acute inpatient dialysis services in approximately 900 hospitals and related laboratory services throughout the United States. In July 2013, DaVita, a division of DaVita HealthCare Partners Inc announced the acquisition of the dialysis operations of Malaysia's Caring Dialysis Centre Group (CDC Group) by DVA (Malaysia) Sdn Bhd."},{"Symbol":"DE","Name":"Deere & Co.","Sector":"Industrials","Description":"Deere & Company (John Deere) along with its subsidiaries, operates in three segments: agriculture and turf, construction and forestry and financial services. The John Deere agriculture and turf segment manufactures and distributes a line of agricultural and turf equipment and related service parts. John Deere construction, earthmoving, material handling and forestry equipment includes a broad range of backhoe loaders, crawler dozers and loaders, four-wheel-drive loaders, excavators, motor graders, articulated dump trucks, landscape loaders, skid-steer loaders, log skidders, log feller bunchers, log loaders, log forwarders, log harvesters and a range of attachments. The financial services segment primarily finances sales and leases by John Deere dealers of new and used agriculture and turf equipment and construction and forestry equipment."},{"Symbol":"DLPH","Name":"Delphi Automotive","Sector":"Consumer Discretionary","Description":"Delphi Automotive PLC (Delphi) is a global vehicle components manufacturer and provides electrical and electronic, powertrain, safety and thermal technology solutions to the global automotive and commercial vehicle markets. As of December 31, 2012, the Company operated 126 manufacturing facilities and 15 technical centers utilizing a regional service model that enables it to serve its global customers. The Company operates through four segments: Electrical / Electronic Architecture; Powertrain Systems; Electronics and Safety and Thermal Systems. In October 2012, the Company acquired FCI Group's Motorized Vehicles Division."},{"Symbol":"DAL","Name":"Delta Air Lines","Sector":"Industrials","Description":"Delta Air Lines, Inc. (Delta) provides scheduled air transportation for passengers and cargo throughout the United States and around the world. The Company's route network gives it a presence in every domestic and international market. Delta's route network is centered around the hub system it operate at airports in Amsterdam, Atlanta, Cincinnati, Detroit, Memphis, Minneapolis-St. Paul, New York-JFK, Paris-Charles de Gaulle, Salt Lake City and Tokyo-Narita. Each of these hub operations includes flights that gather and distribute traffic from markets in the geographic region surrounding the hub to domestic and international cities and to other hubs. The Company's network is supported by a fleet of aircraft that is varied in terms of size and capabilities. In June 2013, Delta Air Lines Inc acquired a 49% stake in Virgin Atlantic Airways Ltd."},{"Symbol":"XRAY","Name":"Dentsply Sirona","Sector":"Health Care","Description":"DENTSPLY International Inc. (DENTSPLY) is a designer, developer, manufacturer and marketer of a range of dental products. The Company operates in four segments, all of which are primarily engaged in the design, manufacture and distribution of dental products in four principal categories: dental consumables, dental laboratory products, dental specialty products and consumable medical device products. DENTSPLY conducts its business in over 120 foreign countries, principally through its foreign subsidiaries. It operates in Canada, the European market, which includes Germany, Switzerland, France, Italy, and the United Kingdom. It also has a market presence in Central and South America, South Africa and Pacific Rim. It has also established marketing activities in Moscow, Russia. On August 31, 2011, the Company acquired Astra Tech AB. In November 2013, the Company acquired QAHR, a direct dental selling organization with headquarters in Hong Kong and operations also in mainland China."},{"Symbol":"DVN","Name":"Devon Energy Corp.","Sector":"Energy","Description":"Devon Energy Corporation (Devon) is an independent energy company engaged primarily in the exploration, development and production of oil, natural gas and natural gas liquids (NGLs). The Company's operations are concentrated in various North American onshore areas in the United States and Canada. The Company also owns natural gas pipelines, plants and treatment facilities in many of its producing areas. The Company holds 14 million net acres, of which roughly two-thirds are undeveloped. The Company's properties in the United States include Barnett Shale, Cana-Woodford Shale, Permian Basin , Gulf Coast/East Texas, Granite Wash , and Mississippian. Its properties in Canada include Jackfish, Pike, Lloydminster. In August 2013, Meritage Midstream Services II, LLC acquired Thunder Creek Gas Services, LLC (Thunder Creek) from Devon and PVR Partners, L.P (PVR). In November 2013, the Company acquired GeoSouthern Energy's assets in the Eagle Ford oil play."},{"Symbol":"DLR","Name":"Digital Realty Trust","Sector":"Real Estate","Description":"Digital Realty Trust, Inc. is a real estate investment trust (REIT). The Company owns, acquires, develops, redevelops and manages technology-related real estate. The Company's operates through Digital Realty Trust, L.P. and owns a 97.8% common general partnership interest. Digital Realty Trust, L.P. holds substantially all the assets of the Company and holds the ownership interests in the Company's joint ventures. As of December 31, 2012, it owned 117 properties, excluding three properties held as investments in unconsolidated joint ventures, of which 92 are located throughout North America, 21 are located in Europe and one is located in Asia. In March 2013, it completed the acquisition of 371 Gough Road in Markham, Ontario (Canada), a 120,000 square foot data center development property. In September 2013, the Company announced the completion of the acquisition of a 15,000 square meter site in Osaka."},{"Symbol":"DFS","Name":"Discover Financial Services","Sector":"Financials","Description":"Discover Financial Services is a direct banking and payment services company. The Company is a bank holding company and a financial holding company. The Company offer credit cards, student loans, personal loans and deposit products through its Discover Bank subsidiary and home loans through its Discover Home Loans, Inc. subsidiary (Discover Home Loans). The Company operates the Discover Network, its credit card payments network; the PULSE network (PULSE), its automated teller machine (ATM), debit and electronic funds transfer network, and Diners Club International (Diners Club), its global payments network. The Company operates in two segments: Direct Banking and Payment Services. In June 2012, Tree.Com Inc sold the operating assets of its Home Loan Center, Inc. business to a wholly owned subsidiary of Discover Financial Services."},{"Symbol":"DISCA","Name":"Discovery Communications-A","Sector":"Consumer Discretionary","Description":"Discovery Communications, Inc. (Discovery) is a global nonfiction media and entertainment company that provide programming across multiple distribution platforms worldwide. Discovery operates in three segments: U.S. Networks, International Networks and Education and Other. The Company's U.S. Networks, consists principally of domestic cable and satellite television networks, Websites and other digital media services. Its International Networks consists primarily of international cable and satellite television networks and Websites. It's Education and other consists principally of curriculum-based education product and service offerings and postproduction audio services. In November 2013, the Company announced it has acquired Espresso Group Limited, provider of primary school digital education content in the United Kingdom."},{"Symbol":"DISCK","Name":"Discovery Communications-C","Sector":"Consumer Discretionary","Description":"Discovery Communications, Inc. (Discovery) is a global nonfiction media and entertainment company that provide programming across multiple distribution platforms worldwide. Discovery operates in three segments: U.S. Networks, International Networks and Education and Other. The Company's U.S. Networks, consists principally of domestic cable and satellite television networks, Websites and other digital media services. Its International Networks consists primarily of international cable and satellite television networks and Websites. It's Education and other consists principally of curriculum-based education product and service offerings and postproduction audio services. In November 2013, the Company announced it has acquired Espresso Group Limited, provider of primary school digital education content in the United Kingdom."},{"Symbol":"DG","Name":"Dollar General","Sector":"Consumer Discretionary","Description":"Dollar General Corporation is a discount retailer in the United States by number of stores, with 9,961 stores located in 39 states as of March 2, 2012, primarily in the southern, southwestern, midwestern and eastern United States. The Company offers a selection of merchandise, including consumables, seasonal, home products and apparel. The Company's merchandise includes national brands from manufacturers, as well as private brand selections with prices at substantial discounts to national brands. It offers its merchandise at everyday low prices through its convenient small-box (approximately 7,200 square feet) locations. During the fiscal year ended February 3, 2012 (fiscal 2011), it opened 625 stores and remodeled or relocated 575 stores, and closed 60 stores. Its small box stores offer consumable items, including packaged and refrigerated foods."},{"Symbol":"DLTR","Name":"Dollar Tree","Sector":"Consumer Discretionary","Description":"Dollar Tree, Inc. (Dollar Tree) is an operator of discount variety stores offering merchandise at the fixed price. As of January 28, 2012, the Company operated 4,351 discount variety retail stores. Its stores operate under the names of Dollar Tree, Deal$, Dollar Tree Deal$, Dollar Giant and Dollar Bills. As of January 28, 2012, it operated 4,252 stores in 48 states and the District of Columbia, as well as 99 stores in Canada. It buys approximately 58% to 60% of its merchandise domestically and imports the remaining 40% to 42%. Its domestic purchases include basic, seasonal, closeouts and promotional merchandise. It maintains a selection of products within variety store categories. During the fiscal year ended January 28, 2012 (fiscal 2011), the Company opened 278 new stores."},{"Symbol":"D","Name":"Dominion Resources","Sector":"Utilities","Description":"Dominion Resources, Inc. (Dominion), is a producer and transporter of energy. The Company is a provider of electricity, natural gas and related services to customers primarily in the eastern region of the United States Dominion's portfolio of assets includes approximately 27,500 megawatts of generating capacity, 6,300 miles of electric transmission lines, 56,900 miles of electric distribution lines, 11,000 miles of natural gas transmission, gathering and storage pipeline and 21,800 miles of gas distribution pipeline, exclusive of service lines of two inches in diameter or less. Dominion also operates one of the underground natural gas storage systems, with approximately 947 billion cubic feet of storage capacity, and serves nearly six million utility and retail energy customers in 15 states. In July 2013, Dominion Resources Inc acquired three solar-power development projects near Indianapolis, Ind., from Sunrise Energy Ventures."},{"Symbol":"DOV","Name":"Dover Corp.","Sector":"Industrials","Description":"Dover Corporation (Dover) manufactures a range of specialized products and components and also offers related services and consumables. The Company operates in four segments: Communication Technologies, Energy, Engineered Systems, and Printing & Identification. In July 2014, the Company announced that it has completed the sale of DEK Printing Machines unit (DEK). In July 2014, Dover acquired The WellMark Company. In August 2014, the Company acquired Liquip International."},{"Symbol":"DOW","Name":"Dow Chemical","Sector":"Materials","Description":"The Dow Chemical Company combines the power of science and technology to passionately innovate what is essential to human progress. The Company connects chemistry and innovation with the principles of sustainability to help address many of the problems, such as the need for clean water, renewable energy generation and conservation, and increasing agricultural productivity. The Company conducts its worldwide operations through global businesses, which are reported in six operating segments: Electronic and Functional Materials, Coatings and Infrastructure Solutions, Agricultural Sciences, Performance Materials, Performance Plastics, and Feedstocks and Energy. In December 2013, W. R. Grace & Co announced that it has completed the acquisition of the assets of the Polypropylene Licensing and Catalysts business of The Dow Chemical Company."},{"Symbol":"DPS","Name":"Dr Pepper Snapple Group","Sector":"Consumer Staples","Description":"Dr Pepper Snapple Group, Inc. (DPS) is an integrated brand owner, manufacturer and distributor of non-alcoholic beverages in the United States, Canada and Mexico with a diverse portfolio of flavored (non-cola) carbonated soft drinks (CSDs) and non-carbonated beverages (NCBs), including ready-to-drink teas, juices, juice drinks and mixers. The Company operates in three segments: Beverage Concentrates, Packaged Beverages and Latin America Beverages. The Company primarily serves two groups of customers: bottlers and distributors and retailers. As of December 31, 2011, it operated 20 manufacturing facilities across the United States and Mexico, excluding its manufacturing facility for its joint venture with Acqua Minerale San Benedetto. Effective March 1, 2013, it acquired Dr. Pepper/7-UP Bottling Co of the West, a producer and wholesaler of bottled soft drinks."},{"Symbol":"DTE","Name":"DTE Energy Co.","Sector":"Utilities","Description":"DTE Energy Company (DTE Energy) , is a diversified energy company. The Company is engaged in utility operations consist primarily of Detroit Edison and MichCon. The Electric Utility segment consists principally of Detroit Edison. The Gas Utility segment consists of MichCon and Citizens. Its non-utility operations include Gas Storage and Pipelines. Unconventional Gas Production is engaged in unconventional gas and oil project development and production. Power and Industrial Projects consists of projects that deliver energy and utility-type products and services to industrial, commercial and institutional customers; provide coal transportation and marketing, and sell electricity from biomass-fired energy projects. Energy Trading consists of energy marketing and trading operations. Corporate and Other includes various holding company activities and holds certain non-utility debt and energy-related investments."},{"Symbol":"DD","Name":"Du Pont (E.I.)","Sector":"Materials","Description":"Dupont E I De Nemours & Co, formerly E. I. du Pont de Nemours and Company, manufacturing, seed production or selling activities and some are distributors of products manufactured by the Company. The Company's segments are Agriculture, Electronics & Communications, Industrial Biosciences, Nutrition & Health, Performance Chemicals, Performance Coatings, Performance Materials, Safety & Protection and Pharmaceuticals. During the year ended December 31, 2011, the Electronics & Communications segment completed the acquisition of Innovalight, Inc., a company specializing in advanced silicon inks and process technologies that increase the efficiency of crystalline silicon solar cells. As of September 22, 2011, it acquired Danisco A/S (Danisco), a global enzyme and specialty food ingredients company. In May 2012, it acquired from Bunge full ownership of the Solae, LLC joint venture. In February 2013, the Company sold DuPont Performance Coatings."},{"Symbol":"DUK","Name":"Duke Energy","Sector":"Utilities","Description":"Duke Energy Corporation (Duke Energy) is an energy company. Duke Energy operates in the United States primarily through its direct and indirect wholly owned subsidiaries, Duke Energy Carolinas, LLC (Duke Energy Carolinas), Carolina Power & Light Company d/b/a Progress Energy Carolinas, Inc. (Progress Energy Carolinas), Florida Power Corporation d/b/a Progress Energy Florida, Inc. (Progress Energy Florida), Duke Energy Ohio, Inc. (Duke Energy Ohio), and Duke Energy Indiana, Inc. (Duke Energy Indiana), as well as in Latin America through Duke Energy International, LLC (DEI). Duke Energy's segment includes U.S. Franchised Electric and Gas (USFE&G), Commercial Power and International Energy. In December 2012, the Company's subsidiary acquired CGE Group's Iberoamericana de Energia Ibener S.A. (Ibener) subsidiary in Chile. In June 2013, Duke Energy Corp acquired an undisclosed minority stake in Clean Power Finance Inc."},{"Symbol":"DNB","Name":"Dun & Bradstreet","Sector":"Industrials","Description":"The Dun & Bradstreet Corporation (D&B) is the source of commercial information and insight on businesses, enabling customers to Decide with Confidence. As of December 31, 2012, the Company's global commercial database contained more than 220 million business records. The database is enhanced by its DUNSRight Quality Process, which transforms commercial data into valuable insight which is the foundation of its global solutions. Customers use D&B Risk Management Solutions to mitigate credit and supplier risk, increase cash flow and drive profitability; D&B Sales & Marketing Solutions to provide data management capabilities that provide marketing solutions to increase revenue from new and existing customers, and D&B Internet Solutions to convert prospects into clients by enabling business professionals to research companies, executives and industries."},{"Symbol":"ETFC","Name":"E*Trade","Sector":"Financials","Description":"E*TRADE Financial Corporation (E*TRADE) is a financial services company, which provides online brokerage and related products and services primarily to individual retail investors under the brand E*TRADE Financial. The Company also provides investor-focused banking products, primarily sweep deposits and savings products, to retail investors. E*TRADE operates in two segments: trading and investing and balance sheet management. Trading and investing includes retail brokerage products and services; investor-focused banking products; market making, and corporate services. Balance sheet management includes the management of asset allocation and credit, liquidity and interest rate risk; loans previously originated or purchased from third parties, and customer cash and deposits. The Company's subsidiaries include E*TRADE Bank, E*TRADE Securities LLC, E*TRADE Clearing LLC and E*TRADE Capital Markets, LLC."},{"Symbol":"EMN","Name":"Eastman Chemical","Sector":"Materials","Description":"Eastman Chemical Company (Eastman) is a global specialty chemicals company that produces a range of advanced materials, chemicals and fibers that are found in products people use every day. Eastman has over 40 manufacturing sites in 16 countries and equity interests in joint ventures that supply chemicals, plastics, and fibers products to customers throughout the world. Eastman has a portfolio of specialty businesses that hold positions and provide products that enhance performance in a variety of end markets such as transportation, building and construction, and consumables. Eastman management believes that the Company's end-market diversity is a source of strength, as these markets are benefiting from longer-term global trends such as energy efficiency, a rising middle class in emerging economies, and increased health and wellness. On July 2, 2012, the Company completed its acquisition of Solutia Inc. (Solutia)."},{"Symbol":"ETN","Name":"Eaton Corporation","Sector":"Industrials","Description":"No Description Available"},{"Symbol":"EBAY","Name":"eBay Inc.","Sector":"Information Technology","Description":"eBay Inc is a global technology company that enables commerce through three reportable segments: Marketplaces, Payments, and GSI. The Company by providing online platforms, tools and services to help individuals and small, medium and merchants around the globe engage in online and mobile commerce and payments, the Company can facilitate transactions. The company also generates revenue through marketing services, classifieds and advertising. The Company has also created an open source platform that provides software developers and merchants access to its applications programming interfaces, or APIs, to develop software and solutions for commerce. Its developer community includes more than 800,000 members. Effective July 4, 2013, eBay Inc acquired 2ememain.be and 2dehands.be, providers of e-commerce retail services. Effective September 6, 2013, eBay Inc acquired Decide Inc. Effective September 27, 2013, eBay Inc acquired Bureau of Trade."},{"Symbol":"ECL","Name":"Ecolab Inc.","Sector":"Materials","Description":"Ecolab Inc. (Ecolab) develops and markets products and services for the hospitality, foodservice, healthcare and industrial markets. The Company provides cleaning and sanitizing products and programs, as well as pest elimination, equipment maintenance and repair services primarily to customers in the foodservice, food and beverage processing, hospitality, healthcare, government and education, retail, textile care, commercial facilities management and vehicle wash sectors. The Company business segments include United States Cleaning & Sanitizing segment, United States Other Services segment, International segment, Water Services segment, Paper Services segment and Energy Services segment. In April 2013, it acquired Champion Technologies and its related company Corsicana Technologies. In August 2013, Ecolab Inc. sold all the capital equipment design and build business of its Mobotec air emissions control business to The Power Industrial Group."},{"Symbol":"EIX","Name":"Edison Int'l","Sector":"Utilities","Description":"Edison International, is a holding company of Southern California Edison Company (SCE). SCE is a public utility primarily engaged in the business of supplying electricity to an approximately 50,000 square-mile area of southern California. The SCE service territory contains a population of nearly 14 million people and SCE serves the population through approximately 5 million customer accounts. In August 2013, Edison International completed the acquisition of SoCore Energy, LLC."},{"Symbol":"EW","Name":"Edwards Lifesciences","Sector":"Health Care","Description":"Edwards Lifesciences Corporation (Edwards Lifesciences) is focused on technologies that treat structural heart disease and critically ill patients. A pioneer in the development and commercialization of heart valve products, Edwards Lifesciences is the manufacturer of tissue heart valves and repair products used to replace or repair a patient's diseased or defective heart valve. The Company also provides hemodynamic monitoring systems used to measure a patient's cardiovascular function in the hospital setting. The products and technologies provided by Edwards Lifesciences to treat advanced cardiovascular disease or critically ill patients are categorized into three main areas: Surgical Heart Valve Therapy, which combines surgical heart valves and Cardiac Surgery Systems; Transcatheter Heart Valves; and Critical Care, which includes Vascular. In October 2012, it acquired BMEYE B.V."},{"Symbol":"EA","Name":"Electronic Arts","Sector":"Information Technology","Description":"Electronic Arts Inc. develops, markets, publishes and distributes game software content and services that can be played by consumers on a variety of video game machines and electronic devices. The Company provides games for mobile devices and Internet-only games, such as its free-to-play offerings that are available only through online and wireless delivery and also offers multi-player online games and game services on a free-to-play and subscription basis. The Company's portfolio of brands includes wholly owned brands, such as Battlefield, Mass Effect, Need for Speed, The Sims, Bejeweled, and Plants v. Zombies. Its portfolio also includes brands based on licensed intellectual property, including sports-based brands, such as Madden NFL and FIFA. The Company operates development studios (which develop products and perform other related functions) in North America, Europe, Asia and Australia."},{"Symbol":"EMR","Name":"Emerson Electric Company","Sector":"Industrials","Description":"Emerson Electric Co. (Emerson) is a diversified global technology company. The Company is engaged in designing and supplying products and technology, and delivering engineering services and solutions in a range of industrial, commercial and consumer markets globally. The Company operates in five business segments: Process Management, Industrial Automation, Network Power, Commercial and Residential Solutions, and Climate Technologies. The Company's production operations are electronics assembly, metal stamping, forming, casting, machining, welding, plating, heat treating, painting and assembly. In April 2012, it acquired the Marine Container and Boiler business of Johnson Controls, Inc. In September 2012, it sold Knaack, L.L.C. to Werner Co. In October 2013, Emerson Electric Co acquired Enardo LLC. In November 2013, the Company sold 51% interest in its computing and power business to Platinum Equity."},{"Symbol":"ETR","Name":"Entergy Corp.","Sector":"Utilities","Description":"Entergy Corporation (Entergy) is an integrated energy company engaged primarily in electric power production and retail distribution operations. It owns and operates power plants with approximately 30,000 megawatts of electric generating capacity, including more than 10,000 megawatts of nuclear power. Entergy delivers electricity to 2.8 million utility customers in Arkansas, Louisiana, Mississippi and Texas. Entergy operates through two business segments: Utility and Entergy Wholesale Commodities. The Utility business segment includes the generation, transmission, distribution, and sale of electric power in portions of Arkansas, Mississippi, Texas, and Louisiana, including the City of New Orleans; and operates a small natural gas distribution business. The Entergy Wholesale Commodities business segment includes the ownership and operation of six nuclear power plants located in the northern United States and the sale of the electric power produced by those plants to wholesale customers."},{"Symbol":"EVHC","Name":"Envision Healthcare","Sector":"Health Care","Description":"Envision Healthcare Holdings, Inc., is a provider of physician-led, outsourced medical services in the United States. The Company offers a range of clinically-based and coordinated care solutions across the patient continuum from medical transportation to hospital encounters to comprehensive care alternatives in various settings. The Company conduts its business primarily through two operating subsidiaries, EmCare Holdings, Inc. (EmCare) and American Medical Response, Inc. (AMR). The Company markets its services primarily under the EmCare and AMR brands. EmCareis a provider of integrated facility-based physician services, including emergency, anesthesiology, hospitalist/inpatient care, radiology, tele-radiology and surgery. EmCare also offers physician-led care management solutions outside the hospital. AMR is a provider and manager of community-based medical transportation services, including emergency (911), non-emergency, and managed transportation."},{"Symbol":"EOG","Name":"EOG Resources","Sector":"Energy","Description":"EOG Resources, Inc.(EOG) explores , develops, produces and markets crude oil and natural gas primarily in producing basins in the United States of America , Canada, The Republic of Trinidad and Tobago (Trinidad), the United Kingdom , The People's Republic of China (China), the Argentine Republic (Argentina) and, from time to time, select other international areas. As of December 31, 2012, EOG's total estimated net proved reserves were 1,811 million barrels of oil equivalent, of which 701 million barrels were crude oil and condensate reserves, 320 million barrels were natural gas liquids (NGLs) reserves and 4,740 billion cubic feet, or 790 million barrels of oil equivalent, were natural gas. EOG's operations are all crude oil and natural gas exploration and production related."},{"Symbol":"EQT","Name":"EQT Corporation","Sector":"Energy","Description":"EQT Corporation (EQT) conducts its business through three business segments: EQT Production, EQT Midstream and Distribution. EQT Production is a natural gas producer in the Appalachian Basin with 5.4 trillion cubic feet equivalent of proved reserves across 3.5 million acres, as of December 31, 2011. EQT Midstream provides gathering, transmission and storage services for the Company's produced gas and to independent third parties in the Appalachian Basin. Distribution, through its regulated natural gas distribution subsidiary, Equitable Gas Company, LLC (Equitable Gas), distributes and sells natural gas to residential, commercial and industrial customers in southwestern Pennsylvania, West Virginia and eastern Kentucky; operates a small gathering system in Pennsylvania, and provides off-system sales activities, which include the purchase and delivery of gas to customers. On February 1, 2011, EQT Midstream sold Langley and the associated NGL pipeline to MarkWest Energy Partners, L.P."},{"Symbol":"EFX","Name":"Equifax Inc.","Sector":"Industrials","Description":"Equifax Inc. is a provider of information solutions for businesses and consumers. The Company's clients and customers include financial institutions, corporations, governments and individuals. Its products and services are based on databases of consumer and business information derived from various types of credit, financial, employment, income, public record, demographic and marketing data. The Company operates in five segments: U.S. Consumer Information Solutions (USCIS), International, TALX Workforce Solutions, North America Personal Solutions and North America Commercial Solutions. On May 31, 2011, the Company completed the merger of its Brazilian business with Boa Vista Servicos S.A. On February 6, 2013, Nationstar Mortgage Holdings Inc acquired Equifax Settlement Services Holding, LLC from the Company. In July 2013, Equifax Inc. announced it has acquired TrustedID."},{"Symbol":"EQIX","Name":"Equinix","Sector":"Real Estate","Description":"Equinix, Inc. (Equinix) connects businesses with partners and customers worldwide through a global platform of data centers. The Company connects approximately 4000 customers, across the Americas, Europe, Middle East and Africa (EMEA) and Asia-Pacific. Platform Equinix combines international business exchange (IBX) data centers, a global footprint and ecosystems. The Company offers each customer a choice of business partners and solutions based on their colocation, interconnection and managed IT service needs. Equinix offers customers direct interconnection to an aggregation of bandwidth providers, including the Internet Service Providers (ISPs), broadband access networks and international carriers. Its customers include carriers and other bandwidth providers, cloud and information technology services providers, content providers, financial companies and global enterprises. On July 2, 2012, (the ancotel Acquisition Date), the Company acquired a privately-held company in Germany."},{"Symbol":"EQR","Name":"Equity Residential","Sector":"Real Estate","Description":"Equity Residential (EQR) is a real estate investment trust (REIT). The Company is focused on the acquisition, development and management of multi-family residential properties, which includes the generation of rental and other related income through the leasing of apartment units to residents, in United States. ERP Operating Limited Partnership (or Operating Partnership), which is an Illinois limited partnership, conducts the multifamily residential property business of EQR. All of the Company's property ownership, development and related business operations are conducted through the Operating Partnership. The Operating Partnership holds all of the assets of the Company, including the Company's ownership interests in its joint ventures. In December 2012, it acquired four multifamily properties totaling 1,134 units."},{"Symbol":"ESS","Name":"Essex Property Trust, Inc.","Sector":"Real Estate","Description":"Essex Property Trust, Inc. (Essex) operates as a self-administered and self-managed real estate investment trust (REIT). The Company is engaged primarily in the ownership, operation, management, acquisition, development and redevelopment of apartment communities. As of December 31, 2012, the Company owned or held an interest in 163 communities, aggregating 33,468 units, located along the West Coast, as well as five commercial buildings (totaling approximately 315,900 square feet), and five active development projects with 2,495 units in various stages of development (collectively, the Portfolio). The Company owns all of its interest in its real estate investments directly or indirectly through Essex Portfolio, L.P. (the Operating Partnership). The Company is the sole general partner of the Operating Partnership and as of December 31, 2012 owns a 94.5% general partnership interest. During the year ended December 31, 2012, the Company acquired 15 communities."},{"Symbol":"EL","Name":"Estee Lauder Cos.","Sector":"Consumer Staples","Description":"The Estee Lauder Companies Inc. is a manufacturer and marketer of skin care, makeup, fragrance and hair care products. The Company's products are sold in over 150 countries and territories under a number of brand names, including Estee Lauder, Aramis, Clinique, Origins, M.A.C, Bobbi Brown, La Mer and Aveda. It is also the global licensee for fragrances and/or cosmetics sold under brand names, such as Tommy Hilfiger, Donna Karan, Michael Kors, Tom Ford and Coach. It sells its products principally through limited distribution channels to complement the images associated with its brands. These channels include over 30,000 points of sale, consisting of upscale department stores, specialty retailers, upscale perfumeries and pharmacies and prestige salons and spas."},{"Symbol":"ES","Name":"Eversource Energy","Sector":"Utilities","Description":"EnergySolutions, Inc. (EnergySolutions) is a provider of a range of nuclear services to government and commercial customers. The Company's range of nuclear services includes engineering, in-plant support services, spent nuclear fuel management, decontamination and decommissioning (D&D), operation of nuclear reactors, logistics, transportation, processing and low-level radioactive waste (LLRW) disposal. The Company also owns and operates strategic processing and disposal facilities. The Global Commercial Group includes three business divisions: Commercial Services, Logistics, Processing and Disposal (LP&D) and International. In May 2013, Energy Capital Partners II LLC, a unit of Energy Capital Partners, through its wholly owned subsidiary, acquired the entire share capital of EnergySolutions Inc."},{"Symbol":"EXC","Name":"Exelon Corp.","Sector":"Utilities","Description":"Exelon Corporation (Exelon) is an energy provider and holding company for several energy businesses. Exelon is engaged in the energy generation business through its Exelon Generation Company, LLC (Generation) subsidiary; wholesale and retail energy sales through its Constellation business unit, and the energy delivery business through its Baltimore Gas and Electric (BGE), Commonwealth Edison Company (ComEd) and PECO Energy Company (PECO) subsidiaries. It operates in 47 states, the District of Columbia and Canada. Exelon Generation has approximately 35,000 megawatts of owned capacity. Constellation provides energy products and services to approximately 100,000 business and public sector customers and approximately 1 million residential customers. Exelon's utilities deliver electricity and natural gas to more than 6.6 million customers in central Maryland, northern Illinois and southeastern Pennsylvania. On March 12, 2012, Constellation Energy Group, Inc. merged into Exelon."},{"Symbol":"EXPE","Name":"Expedia Inc.","Sector":"Consumer Discretionary","Description":"Expedia, Inc. (Expedia) is an online travel company. As of December 31, 2011, the Company's portfolio of travel brands featuring supply portfolio, including over 145,000 hotels in 200 countries, 300 airlines, packages, rental cars, cruises, as well as destination services and activities. Its portfolio of brands includes Expedia.com, a service online travel agency with sites in over 25 countries; luxury travel specialist, Classic Vacations; destination services and concierge services provider Expedia Local Expert (ELE); Expedia CruiseShipCenters; Egencia, the corporate travel management company; eLong, Inc. (eLong). In November 2011, Renren Inc. sold eLong, Inc. to Expedia, Inc. On December 20, 2011, the Company completed the spin-off of TripAdvisor, Inc., which includes its brand, as well as 18 other travel media brands. During the year ended December 31, 2011, the Company acquired Travelforce and Traveldoo."},{"Symbol":"EXPD","Name":"Expeditors Int'l","Sector":"Industrials","Description":"Expeditors International of Washington, Inc. is engaged in the business of providing global logistics services. The Company offers its customers a seamless international network supporting the movement and strategic positioning of goods. The Company's services include the consolidation or forwarding of air and ocean freight. In each United States office, and in many overseas offices, the Company acts as a customs broker. The Company also provides additional services including value-added distribution, vendor consolidation, cargo insurance, domestic time definite transportation services, purchase order management and customized logistics solutions."},{"Symbol":"ESRX","Name":"Express Scripts","Sector":"Health Care","Description":"No Description Available"},{"Symbol":"EXR","Name":"Extra Space Storage","Sector":"Real Estate","Description":"Extra Space Storage Inc. is a self-administered and self-managed real estate investment trust (REIT). The Company owns, operates, manages, acquires, develops and redevelops professionally managed self-storage facilities. As of December 31, 2011, Extra Space Storage Inc. held ownership interests in 697 operating properties. Of these operating properties, 356 are wholly owned, and 341 are owned in joint venture partnerships. An additional 185 operating properties that are owned by franchisees or third-parties in exchange for a management fee, bringing the total number of operating properties, which it owns and/or manages to 882. The Company operates in three segments: property management, acquisition and development; rental operations, and tenant reinsurance."},{"Symbol":"XOM","Name":"Exxon Mobil Corp.","Sector":"Energy","Description":"Exxon Mobil Corporation (Exxon Mobil) is a manufacturer and marketer of commodity petrochemicals, including olefins, aromatics, polyethylene and polypropylene plastics and a range of specialty products. The Company has a number of divisions and affiliates with names that include ExxonMobil, Exxon, Esso or Mobil. Divisions and affiliated companies of ExxonMobil operate or market products in the United States and other countries of the world. Their principal business is energy, involving exploration for, and production of, crude oil and natural gas, manufacture of petroleum products and transportation and sale of crude oil, natural gas and petroleum products. In January 2012, Apache Corporation acquired Exxon Mobil's Mobil North Sea Limited assets including the Beryl field and related properties. In April 2013, BNK Petroleum (US) Inc. sold Tishomingo Field, Oklahoma assets other than the Caney and upper Sycamore formations to XTO Energy Inc., a subsidiary of Exxon Mobil Corporation."},{"Symbol":"FFIV","Name":"F5 Networks","Sector":"Information Technology","Description":"F5 Networks, Inc. is a provider of Application Delivery Networking (ADN) technology that secures and optimizes the delivery of network-based applications and the security, performance and availability of servers, and other network resources. The Company's principal products are application delivery controllers (ADCs) that include the Company's BIG-IP family of appliances and its line of scalable VIPRION systems. These products are deployed in front of Web and application servers, balancing traffic and performing compute-intensive functions, such as encrypting and un-encrypting transmissions, screening traffic for security threats, maintaining open connections with servers, speeding the flow of traffic, managing access to applications and data and a range of other functions that improve the security, performance, and availability of applications. In February 2012, the Company acquired Traffix Systems."},{"Symbol":"FB","Name":"Facebook, Inc.","Sector":"Information Technology","Description":"Facebook, Inc. (Facebook) is engaged in building products to create utility for users, developers, and advertisers. People use Facebook to stay connected with their friends and family, to discover what is going on in the world around them, and to share and express what matters to them to the people they care about. Developers can use the Facebook Platform to build applications and Websites that integrate with Facebook to reach its global network of users and to build personalized and social products. Advertisers can engage with more than 900 million monthly active users (MAUs) on Facebook or subsets of its users based on information they have chosen to share with the Company, such as their age, location, gender, or interests. In September 2013, Mail.Ru Group Limited sold its remaining shares in Facebook Inc. Effective September 25, 2013, Facebook Inc acquired Mobile Technologies, a developer of online applications. In October 2013, Facebook Inc acquired Onavo Inc."},{"Symbol":"FAST","Name":"Fastenal Co","Sector":"Industrials","Description":"Fastenal Company is engaged in selling industrial and construction supplies in a wholesale and retail fashion. The industrial and construction supplies are grouped into eleven product lines. The Company distributes the supplies through a network of approximately 2,600 company owned stores. Most of its customers are in the manufacturing and non-residential construction markets. The manufacturing market includes both original equipment manufacturers (OEM) and maintenance and repair operations (MRO). The non-residential construction market includes general, electrical, plumbing, sheet metal, and road contractors. Other users of its product include farmers, truckers, railroads, mining companies, federal, state, and local governmental entities, schools, and certain retail trades. Geographically, its stores and customers are primarily located in North America. As of December 31, 2011, the Company had 2,585 store locations."},{"Symbol":"FRT","Name":"Federal Realty Investment Trust","Sector":"Real Estate","Description":"Federal Realty Investment Trust (the Trust) is an equity real estate investment trust (REIT) specializing in the ownership, management, and redevelopment of retail and mixed-use properties located in metropolitan markets in the Northeast and Mid-Atlantic regions of the United States, as well as in California. As of December 31, 2011, the Trust owned or had interest in community and neighborhood shopping centers and mixed-use properties, which are operated as 87 retail real estate projects, consisting approximately 19.3 million square feet. On December 30, 2011, it acquired an 8.1 acre land parcel adjacent to Plaza El Segundo. In January 2012, the Company acquired 89.9% interest in Montrose Crossing. In January 2013, the Company acquired East Bay Bridge shopping center. In April 2013, it acquired a shopping center on 9 acres directly across from the Noroton Heights train station."},{"Symbol":"FDX","Name":"FedEx Corporation","Sector":"Industrials","Description":"FedEx Corporation (FedEx) is a holding company. The Company provides a portfolio of transportation, e-commerce and business services under the FedEx brand. Federal Express Corporation (FedEx Express) is an express transportation company, offering time-certain delivery within one to three business days and serving markets. FedEx Ground Package System, Inc. (FedEx Ground) is a provider of small-package ground delivery service. FedEx Freight Inc (FedEx Freight) is a provider of less-than-truckload (LTL) freight services. FedEx Corporate Services, Inc. (FedEx Services) provides the Company's other companies with sales, marketing, information technology, communications and back-office support. In June 2012, the Company acquired polish courier company Opek Sp. z o.o. In July 2012, the Company acquired TATEX. In July 2012, the Company's, FedEx Express business unit, acquired Rapidao Cometa, a transportation and logistics company."},{"Symbol":"FIS","Name":"Fidelity National Information Services","Sector":"Information Technology","Description":"Fidelity National Information Services, Inc. (FIS) is a global provider of banking and payments technologies. The Company operates in four segments: Financial Solutions Group (FSG), Payment Solutions Group (PSG), International Solutions Group (ISG) and Corporate and Other Segment. The Company is engaged in payment processing and banking solutions, providing software, services and outsourcing of the technology. The Company offers financial institution core processing, card issuer and transaction processing services, including the NYCE Network, a national electronic funds transfer (EFT) network. FIS serves more than 14,000 institutions in over 100 countries. In April 2012, it acquired ICS Risk Advisors and Memento, Inc. In August 2012, the Company completed the sale of its Healthcare Solutions business to an investment fund affiliated with Lightyear Capital LLC. In March 2013, it announced the acquisition of mFoundry."},{"Symbol":"FITB","Name":"Fifth Third Bancorp","Sector":"Financials","Description":"Fifth Third Bancorp (the Bancorp) is a diversified financial services company. As of December 31, 2011, the Bancorp had $117 billion in assets, operated 15 affiliates with 1,316 full-service Banking Centers, including 104 Bank Mart locations open seven days a week inside select grocery stores, and 2,425 automated teller machines (ATMs) in 12 states throughout the Midwestern and Southeastern regions of the United States. The Bancorp operates in four business segments: Commercial Banking, Branch Banking, Consumer Lending and Investment Advisors. The Bancorp also has a 49% interest in Vantiv Holding, LLC."},{"Symbol":"FSLR","Name":"First Solar Inc","Sector":"Information Technology","Description":"First Solar, Inc. manufactures and sells photovoltaic (PV) solar modules with an advanced thin-film semiconductor technology, and it designs, constructs, and sells PV solar power systems. The Company is a thin-film PV solar module manufacturer and a PV solar module manufacturer. It operates its business in two segments: components segment and systems segment. Its components segment involves the design, manufacture, and sale of solar modules, which convert sunlight into electricity. Its systems business involves the sale of its solar modules coupled with the engineering, procurement and construction of the solar PV power plant. In September 2013, First Solar, Inc and K Road Power Holdings LLC announced that First Solar acquired from K Road the 250 megawatt (MW)AC photovoltaic Moapa Solar Project in Nevada."},{"Symbol":"FE","Name":"FirstEnergy Corp","Sector":"Utilities","Description":"FirstEnergy Corp. (FirstEnergy) is engaged in the holding, directly or indirectly, of its subsidiaries: Ohio Edison Company (OE), The Cleveland Electric Illuminating Company (CEI), The Toledo Edison Company (TE), Pennsylvania Power Company (Penn) (a wholly owned subsidiary of OE), American Transmission Systems, Incorporated (ATSI), Jersey Central Power & Light Company (JCP&L), Metropolitan Edison Company (Met-Ed), Pennsylvania Electric Company (Penelec). FirstEnergy operates in three segments: Regulated Distribution, Regulated Independent Transmission and Competitive Energy Services. On February 25, 2011, AE merged with a subsidiary of FirstEnergy and becoming a wholly owned subsidiary of FirstEnergy. On July 28, 2011, FirstEnergy completed the sale of the Fremont Energy Center. On October 18, 2011, FirstEnergy sold its Richland (432 mega-watts) and Stryker (18 mega-watts) Peaking Facilities. On October 18, 2011, Gunvor Group, Ltd. purchased a 33% in Global Holding, a joint venture."},{"Symbol":"FISV","Name":"Fiserv Inc","Sector":"Information Technology","Description":"Fiserv, Inc. (Fiserv) is a global provider of financial services technology. The Company serves approximately 16,000 clients worldwide, including banks, thrifts, credit unions, investment management firms, leasing and finance companies, retailers, merchants and government agencies. The Company's operations are reported in the Payments and Industry Products (Payments) and Financial Institution Services (Financial) business segments. The Company provides account processing systems; electronic payments processing products and services , such as electronic bills payment and presentment, card-based transaction processing and network services, ACH transaction processing, account-to-account transfer products and person-to-person payments; Internet and mobile banking systems, and related services including document and payment card production and distribution, check processing and imaging, source capture systems, and lending and risk management products and services."},{"Symbol":"FLIR","Name":"FLIR Systems","Sector":"Information Technology","Description":"FLIR Systems, Inc. (FLIR), is a designer, manufacturer, and marketer of thermal imaging systems. The Company's advanced sensors and integrated sensor systems enable the gathering and analysis of critical information through a range of applications in commercial, industrial, and government markets worldwide. Its business is organized into two divisions: Commercial Systems and Government Systems. Commercial Systems division includes Thermal Vision & Measurement and Raymarine. Government Systems division include Surveillance, Detection, and Integrated Systems. It offers a range of sensor products, including infrared imaging cameras and systems, detector cores, CBRNE threat detectors, test and measurement instruments, radars, maritime electronics, and related products and solutions. In August 2013, Tessera Technologies Inc announced that FLIR Systems, Inc. acquired a portion of the assets of Tessera's Micro-Optics business based in Charlotte, North Carolina."},{"Symbol":"FLS","Name":"Flowserve Corporation","Sector":"Industrials","Description":"Flowserve Corporation is a manufacturer and aftermarket service provider of flow control systems. The Company develops and manufacture precision-engineered flows control equipment integral to the movement, control and protection of the flow of materials in its customers' critical processes. The Company operates in three segments: Engineered Product Division (EPD), which includes long leads time, custom and other engineered pumps and pump systems, mechanical seals, auxiliary systems and replacement parts and related services, Industrial Product Division (IPD), which includes pre-configured engineered pumps and pump systems and related products and services, and Flow Control Division (FCD), which includes engineered and industrial valves, control valves, actuators and controls and related services."},{"Symbol":"FLR","Name":"Fluor Corp.","Sector":"Industrials","Description":"Fluor Corporation (Fluor) is a holding company. It is a professional services company providing engineering, procurement, construction and maintenance, as well as project management services on a global basis. Fluor serves a diverse set of industries worldwide, including oil and gas, chemicals and petrochemicals, transportation, mining and metals, power, life sciences and manufacturing. It is also a primary service provider to the United Services federal government. Fluor operates in five principal segments: Oil & Gas, Industrial & Infrastructure, Government, Global Services and Power. Fluor Constructors International, Inc., which is organized and operates separately from the rest of its business, provides management and construction services in the United States and Canada, both independently and as a subcontractor on projects in each of its segments. In November 2011, it acquired a sulfur technology company, Goar, Allison & Associates, a unit of Air Products."},{"Symbol":"FMC","Name":"FMC Corporation","Sector":"Materials","Description":"FMC Corporation (FMC), is a diversified chemical company. FMC serves agricultural, consumer and industrial markets with solutions, applications and products. It operates in three business segments: Agricultural Products, Specialty Chemicals and Industrial Chemicals. Agricultural Products segment develops, markets and sells all three classes of crop protection chemicals, such as insecticides, herbicides, and fungicides, with particular strength in insecticides and herbicides. Specialty Chemicals consists of its BioPolymer and lithium businesses and focuses on food ingredients that are used to enhance texture, color, structure and physical stability; pharmaceutical additives for binding, encapsulation and disintegrate applications, specialty polymers and pharmaceutical synthesis. In October 2013, FMC Corporation announced the acquisition of the Center for Agricultural and Environmental Biosolutions (CAEB)."},{"Symbol":"FTI","Name":"FMC Technologies Inc.","Sector":"Energy","Description":"FMC Technologies, Inc. designs, manufactures and services systems and products, including subsea production and processing systems, surface wellhead production systems, high pressure fluid control equipment, measurement solutions and marine loading systems for the energy industry. Its segments are Subsea Technologies, which designs and manufactures products and systems and provides services used by oil and gas companies; Surface Technologies, which designs and manufactures products and systems and provides services used by oil and gas companies involved in land and offshore exploration and production of crude oil and natural gas, and Energy Infrastructure, which offers Measurement Solutions, Loading Systems and Material Handling Solutions. In April 2012, it acquired the remaining 55% interest in Schilling Robotics LLC. In October 2012, it acquired Pure Energy Services Ltd. In January 2013, it purchased a 173-acre parcel of land in Houston, Texas."},{"Symbol":"FL","Name":"Foot Locker Inc","Sector":"Consumer Discretionary","Description":"Foot Locker Inc."},{"Symbol":"F","Name":"Ford Motor","Sector":"Consumer Discretionary","Description":"Ford Motor Company (Ford) is a producer of automobiles. The Company together with its subsidiaries is engaged in other businesses, including financing vehicles. The Company operates in two segments: Automotive and Financial Services. Automotive includes Ford North America, Ford South America, Ford Europe, and Ford Asia Pacific Africa region. Financial services include Ford Motor Credit Company and Other Financial Service. The Company manufactures or distributes automobiles across six continents. Its automotive brands include Ford and Lincoln. Other Financial Services includes a range of businesses, including holding companies and real estate. Effective September 26, 2013, Ford Motor Company acquired Livio, a developer of software."},{"Symbol":"FTV","Name":"Fortive Corp","Sector":"Industrials","Description":"Fortive will be a diversified industrial growth company comprised of Professional Instrumentation and Industrial Technologies businesses that are recognized leaders in attractive markets. With 2015 revenues of $6.2 billion, Fortive's well-known brands hold leading positions in field instrumentation, transportation, sensing, product realization, automation and specialty, and franchise distribution. Fortive will be headquartered in Everett, Washington and employ a team of more than 24,000 research and development, manufacturing, sales, distribution, service and administrative employees in more than 40 countries around the world. With a culture rooted in continuous improvement, the core of our company's operating model will be the Fortive Business System. For more information please visit: www.fortive.com."},{"Symbol":"FBHS","Name":"Fortune Brands Home & Security","Sector":"Industrials","Description":"Fortune Brands Home & Security, Inc. is engaged in home and security products with companies focused on the design, manufacture and sale of products in Kitchen & Bath Cabinetry, Plumbing & Accessories, advanced material windows & entry door Systems, and security and storage products. The Company operates through four business segments: Kitchen & Bath Cabinetry, Plumbing & Accessories, Advanced Material Windows & Door Systems, and Security & Storage. The Kitchen & Bath Cabinetry segment manufactures custom, semi-custom, and stock cabinetry for the kitchen, bath, and other areas of the home. Plumbing & Accessories segment manufactures and assembles faucets, accessories, and kitchen sinks The Advanced Material Windows & Door Systems segment manufactures and sells fiberglass and steel entry door systems. In June 2013, Fortune Brands Home & Security Inc completed the acquisition of WoodCrafters Home Products."},{"Symbol":"BEN","Name":"Franklin Resources","Sector":"Financials","Description":"Franklin Resources, Inc. (Franklin) is a holding company. Franklin together with its various subsidiaries (collectively, the Company), is referred to as Franklin Templeton Investments, is a global investment management organization offering investment choices under the Franklin, Templeton, Mutual Series, Bissett, Fiduciary and Darby brand names. The Company operates in two segments: investment management and related services, and banking/finance. Its investment management and related services provides services to investment funds in jurisdictions globally, which include the United States- and non-the United States-registered open-end and closed-end funds, unregistered funds, and institutional, high net-worth and separately-managed accounts. Its banking/finance segment provides clients with select retail banking, private banking and consumer lending services through its banking and finance subsidiaries. In November 2012, it acquired a majority stake in K2 Advisors Holdings LLC."},{"Symbol":"FCX","Name":"Freeport-McMoRan Inc.","Sector":"Materials","Description":"Freeport-McMoRan Copper & Gold Inc. (FCX) is an international mining company. FCX is one of the copper, gold and molybdenum mining companies in terms of reserves and production. Its portfolio of assets includes the Grasberg minerals district in Indonesia, mining operations in North and South America, and the Tenke Fungurume (Tenke) minerals district in the Democratic Republic of Congo (DRC). The Grasberg minerals district contains the recoverable copper reserve and the gold reserve. It also operates Atlantic Copper, its wholly owned copper smelting and refining unit in Spain. FCX has its operations into five primary divisions: North America copper mines, South America mining, Indonesia mining, Africa mining and Molybdenum operations. In May 2013, the Company completes acquisition of Plains Exploration & Production Company. In June 2013, FCX acquired the remaining 64% interest in McMoRan Exploration Co."},{"Symbol":"FTR","Name":"Frontier Communications","Sector":"Telecommunications Services","Description":"Frontier Communications Corporation (Frontier) is a communications company providing services primarily to rural areas and small and medium-sized towns and cities in the United States. The Company offers a range of voice, data, Internet, and television services and products, some that are available a la carte, and others that are available as bundled or packaged solutions. It operated in 27 states, and was the Incumbent Local Exchange Carrier (ILEC), with approximately 5.3 million access lines and 1.8 million broadband connections as of December 31, 2011. During the year ended December 31, 2011, the Company added approximately 45,200 new High-Speed Internet (HSI) subscribers, net. At December 31, 2011, it had approximately 1,764,200 HSI customers. Throughout its properties, Frontier offers a video product through DISH Network (DISH) and, in addition, it offers fiber optic video services in three states. It added approximately 26,100 video subscribers, net during 2011."},{"Symbol":"GPS","Name":"Gap (The)","Sector":"Consumer Discretionary","Description":"The Gap, Inc. (Gap Inc.),is a global specialty apparel company. Gap Inc. offers apparel, accessories, and personal care products for men, women, children, and babies under the Gap, Old Navy, Banana Republic, Piperlime, and Athleta brands. The Company operates in two segments: Stores, which includes the operations of the retail stores for Gap, Old Navy, and Banana Republic, and Direct, which includes the operations for its online brands, both domestic and international. It has Company-operated stores in the United States, Canada, the United Kingdom, France, Ireland, Japan, China, and Italy. It also has franchise agreements with unaffiliated franchisees to operate Gap and Banana Republic stores. Under these agreements, third parties operate or will operate stores that sell apparel and related products under its brand names. In February 2014, GAP Inc announced the Old Navy's opening of the brand's first store in China."},{"Symbol":"GRMN","Name":"Garmin Ltd.","Sector":"Consumer Discretionary","Description":"Garmin Ltd. (Garmin) is a provider of navigation, communication and information devices and applications, which are enabled by global positioning system (GPS) technology. Garmin designs, develops, manufactures and markets a diverse family of hand-held, portable and fixed-mount GPS-enabled products and other navigation, communications and information products for the automotive/mobile, outdoor, fitness, marine, and general aviation markets. Garmin has four segments: Automotive/Mobile, Aviation, Marine, Outdoor and Fitness. In September 2012, its subsidiary acquired Nexus Marine AB, a designer and manufacturer of instrumentation for the sailing and yachting market."},{"Symbol":"GD","Name":"General Dynamics","Sector":"Industrials","Description":"General Dynamics Corporation is an aerospace and defense company that offers a portfolio of products and services in business aviation; combat vehicles, weapons systems and munitions; military and commercial shipbuilding, and communications and information technology. The Company operates through four business groups: Aerospace, Combat Systems, Marine Systems and Information Systems and Technology. In December 2011, it acquired Force Protection, Inc. In February 2012, the Company acquired the Enterprise Jet Center FBO at Hobby Airport in Houston, Texas. In June 8, 2012, the Company acquired IPW Holdings, Inc. In August 2012, it acquired Ship Repair and Coatings Division of Earl Industries. In August 2012, the Company acquired the defense operations of Gayston Corporation. In August 2012, the Company acquired Fidelis Security Systems, Inc. In September 2012, it acquired Open Kernel Labs, Inc. In December 2012, the Company acquired Applied Physical Sciences Corp."},{"Symbol":"GE","Name":"General Electric","Sector":"Industrials","Description":"General Electric Company (GE) is a diversified technology and financial services company. The products and services of the Company range from aircraft engines, power generation, water processing, and household appliances to medical imaging, business and consumer financing and industrial products. It serves customers in more than 100 countries. In June 2013, American Realty Capital Trust IV Inc announced that it has closed on the acquisition of 377 primarily net lease retail properties as part of its previously announced portfolio acquisition from certain affiliates of General Electric Co's GE Capital. In July 2013, General Electric Co's GE Oil & Gas completed its acquisition of Lufkin Industries, Inc. In August 2013, General Electric Company completed the acquisition of the aviation business of Ario S.p.A."},{"Symbol":"GGP","Name":"General Growth Properties Inc.","Sector":"Real Estate","Description":"General Growth Properties, Inc. is a real estate investment trust (REIT). The Company owns or with joint venture partners 144 regional malls (126 domestic and 18 in Brazil) consists of approximately 135 million square feet. The Company is engaged in ownership, operation, management and selective re-development of its Consolidated Properties and Unconsolidated Properties, which are primarily regional malls. As of December 31, 2012, the Company's segment was consists of 126 regional malls in the United States and 18 malls in Brazil, eight strip centers totaling 1.6 million square feet, primarily in the Western region of the United States, as well as seven stand-alone office buildings totaling 0.9 million square feet, concentrated in Columbia, Maryland."},{"Symbol":"GIS","Name":"General Mills","Sector":"Consumer Staples","Description":"General Mills, Inc. (General Mills) is a manufacturer and marketer of branded consumer foods sold through retail stores. The Company is also a supplier of branded and unbranded food products to the foodservice and commercial baking industries. The Company manufactures its products in 15 countries and markets them in more than 100 countries. The Company's joint ventures manufacture and market products in more than 130 countries and republics worldwide. General Mills operates in three segments: U.S. Retail, International, and Bakeries and Foodservice. In addition, the Company sells ready-to-eat cereals through its Cereal Partners Worldwide (CPW) joint venture. In February 2012, General Mills acquired Food Should Taste Good, a natural snack foods company based in Needham Heights, Mass. During the fiscal year ended May 27, 2012, the Company acquired a 51% interest in Yoplait S.A.S. and a 50% interest in Yoplait Marques S.A.S. In August 2012, it acquired Yoki Alimentos SA."},{"Symbol":"GM","Name":"General Motors","Sector":"Consumer Discretionary","Description":"General Motors Corporation (General Motors) designs, build and sell cars, trucks and automobiles parts globally. The Company also provides automotive financing services through General Motors Financial Company, Inc. (GM Financial). The Company's automotive operations meet the demands of the Company's customers through its four automotive segments: GM North America (GMNA), GM Europe (GME), GM International Operations (GMIO) and GM South America (GMSA). During the year ended December 31, 2012, the Company's total worldwide vehicle sales were 9.3 million. As of December 31, 2012 the Company had ownership stakes directly or indirectly in entities through various regional subsidiaries, including GM Korea Company (GM Korea), and Shanghai General Motors Co., Ltd. (SGM). In March 2012, the Company acquired from Ally Financial 100% interest of GMAC South America LLC. In October 2013, Fiat SpA's Fiat Group Automobiles completes acquisition of 50% stake in VM Motori SpA Held by the Company."},{"Symbol":"GPC","Name":"Genuine Parts","Sector":"Consumer Discretionary","Description":"Genuine Parts Company is a service organization engaged in the distribution of automotive replacement parts, industrial replacement parts, office products and electrical/electronic materials. The Company operates in four: Automotive Parts Group, Industrial Parts Group, Office Products Group and Electrical/Electronic Materials Group. The Automotive Parts Group distributes automotive parts and accessory items. In addition to over 427,000 available part numbers, the Company offers inventory, cataloging, marketing, training and other programs in the automotive aftermarket. The Industrial Parts Group is operated as Motion Industries, Inc. (Motion), a wholly owned subsidiary of the Company. During the year ended December 31, 2012, the Company's business were conducted throughout the United States, in Canada and in Mexico from approximately 2,000 locations. On February 1, 2012, the Company acquired Light Fabrications Inc."},{"Symbol":"GILD","Name":"Gilead Sciences","Sector":"Health Care","Description":"Gilead Sciences, Inc. (Gilead) is a research-based biopharmaceutical company that discovers, develops and commercializes medicines. Gilead's primary areas of focus include human immunodeficiency virus (HIV)/AIDS, liver diseases, such as hepatitis B and C and cardiovascular/metabolic and respiratory conditions. The Company has operations in North America, Europe and Asia Pacific. The Company's products include Atripla, Truvada, Viread, Complera/Eviplera, Emtriva, Hepsera, Letairis, Ranexa, Lexiscan/Rapiscan, AmBisome, Vistide, Macugen, Cayston and Tamiflu. In January 2012, the Company acquired Pharmasset, Inc. On February 8, 2013, its subsidiary, acquired YM BioSciences Inc."},{"Symbol":"GPN","Name":"Global Payments Inc","Sector":"Information Technology","Description":"Global Payments Inc. (Global Payments) is a provider of electronic payments transaction processing services for consumers, merchants, independent sales organizations (ISOs), financial institutions, government agencies and multi-national corporations located throughout the United States, Canada, the United Kingdom, Spain, the Asia-Pacific region, the Czech Republic, and the Russian Federation. The Company serves as an intermediary to facilitate electronic payment transactions and operates in two business segments: North America Merchant Services and International Merchant Services. On October 1, 2012, the Company completed the acquisition of Accelerated Payment Technologies (APT). The Company accepts a range of card and electronic based payments at the point of sale. The Company's business model provides payment products and services directly to merchants as its end customers."},{"Symbol":"GS","Name":"Goldman Sachs Group","Sector":"Financials","Description":"The Goldman Sachs Group, Inc. (Goldman Sachs) is a global investment banking, securities and investment management firm that provides a range of financial services to a substantial and diversified client base that includes corporations, financial institutions, governments and high-net-worth individuals. Goldman Sachs operates in four segments: Investment Banking, Institutional Client Services, Investing & Lending and Investment Management. In July 2013, Suncorp Group Ltd announced the completion of the Non-core portfolio sale to the Company. Effective August 6, 2013, Goldman Sachs Group Inc (Goldman) increased its interest by acquiring an 18.7% interest in Red de Carreteras de Occidente SAB de CV, a unit of Goldman's Goldman Sachs Infrastructure Partners fund, from Empresas ICA SAB de CV. In October 2013, Goldman Sachs Group Inc's unit bought a 50% stake in privately-held British motor insurer Hastings."},{"Symbol":"GT","Name":"Goodyear Tire & Rubber","Sector":"Consumer Discretionary","Description":"The Goodyear Tire & Rubber Company is a manufacturer of tires. The Company, together with subsidiaries and joint ventures, develops, manufactures, markets and distributes tires for a range of applications. The Company also manufactures and markets rubber-related chemicals for various applications. The Company is an operator of commercial truck service and tire retreading centers. During the year ended December 31, 2011, the Company operated approximately 1,400 tire and auto service center outlets where it offered its products for retail sale and provided automotive repair and other services. The Company manufactures its products in 53 manufacturing facilities in 22 countries, including the United States. It operates through four operating segments representing its regional tire businesses: North American Tire; Europe, Middle East and Africa Tire (EMEA); Latin American Tire, and Asia Pacific Tire."},{"Symbol":"GWW","Name":"Grainger (W.W.) Inc.","Sector":"Industrials","Description":"Grainger W W Inc., formerly W.W. Grainger, Inc., is a distributor of maintenance, repair and operating (MRO) supplies and other related products and services used by businesses and institutions primarily in the United States and Canada . The Company operates in two segments: United States and Canada. Other businesses include operations in Europe, Asia, Latin America and other United States operations. In August 2013, W.W. Grainger Inc acquired E&R Industrial Sales, Inc. In December 2013, the Company announced that it has completed the acquisition of Safety Solutions Inc."},{"Symbol":"HAL","Name":"Halliburton Co.","Sector":"Energy","Description":"Halliburton Company is an oilfield services company. The Company is provider of services and products to the energy industry related to the exploration, development, and production of oil and natural gas. It serves national, and independent oil and natural gas companies worldwide and operates in two segments: Completion and Production segment and Drilling and Evaluation segment. The Company conducts business worldwide in approximately 80 countries. The business operations of its divisions are organized around four primary geographic regions: North America, Latin America, Europe/Africa/CIS, and Middle East/Asia. During the year ended December 31, 2011, based on the location of services provided and products sold, 55% of its consolidated revenue was from the United States. In October 2011, the Company completed the acquisition of Multi-Chem Group, LLC. In August 2012, its Landmark Software and Services acquired Petris Technology Inc."},{"Symbol":"HBI","Name":"Hanesbrands Inc","Sector":"Consumer Discretionary","Description":"Hanesbrands Inc. is a consumer goods company with a portfolio of apparel brands, including Hanes, Champion, Bali, Playtex, Just My Size, L'eggs, barely there, Wonderbra, Gear for Sports, Stedman, Zorba, Rinbros, Sol y Oro, Outer Banks and Duofold. The Company designs, manufactures, sources and sells a range of basic apparel, such as T-shirts, bras, panties, men's underwear, kids' underwear, casualwear, activewear, socks and hosiery. The Company operates in five segments: Innerwear, Outerwear, Hosiery, Direct to Consumer and International. The Hanes brand covers all of its product categories, including men's underwear, kids' underwear, bras, panties, socks, T-shirts, fleece and sheer hosiery. Bali is the brand within its portfolio, offering a range of bras, panties and shapewear sold in the department store channel. In October 2013, the Company announced that it has closed the acquisition of Maidenform Brands, Inc."},{"Symbol":"HOG","Name":"Harley-Davidson","Sector":"Consumer Discretionary","Description":"Harley-Davidson, Inc. is the parent company for the groups of companies doing business as Harley-Davidson Motor Company (HDMC) and Harley-Davidson Financial Services (HDFS). The Company operates in the Motorcycles and Related Products (Motorcycles) segment and the Financial Services (Financial Services) segment. The Motorcycles segment designs, manufactures and sells at wholesale heavyweight (street legal with engine displacement of 651+cc) Harley-Davidson motorcycles, as well as a line of motorcycle parts, accessories, general merchandise and related services. The Company conducts business on a global basis, with sales in North America, Europe/Middle East/Africa (EMEA), Asia-Pacific and Latin America. The Financial Services segment consists of HDFS, which provides wholesale and retail financing and provides insurance and insurance-related programs primarily to Harley-Davidson dealers and their retail customers. HDFS conducts business principally in the United States and Canada."},{"Symbol":"HAR","Name":"Harman Int'l Industries","Sector":"Consumer Discretionary","Description":"DELISTED - Harman International Industries, Incorporated (Harman International) is engaged in the developing, manufacturing and marketing of audio products and electronic systems. The Company has developed a range of product offerings, which are sold under brand names like AKG, Crown, JBL, Infinity, Harman/Kardon, Lexicon, dbx, Digitec, BSS, Studer, Soundcraft, Mark Levinson, Becker, Revel, Logic 7 and Selenium. The Company has four segments: Infotainment, Lifestyle, Professional, and Other. On February 28, 2013, the Company acquired Martin Professional A/S from A/S Schouw & Co. In October 2013, the Company announced the acquisition of privately-held Duran Audio BV."},{"Symbol":"HRS","Name":"Harris Corporation","Sector":"Information Technology","Description":"Harris Corporation (Harris), together with its subsidiaries, is an international communications and information technology (IT) company serving government and commercial markets in more than 125 countries. The Company operates in three segments: RF Communications segment, Integrated Network Solutions segment and Government Communications Systems. Its RF Communications segment consists of United States Department of Defense and international tactical communications, and public safety and professional communications. Its Integrated Network Solutions segment consists of IT services, Harris CapRock Communications and Healthcare Solutions. During the fiscal year ended June 29, 2012, the Company discontinued its cyber integrated solutions operation (CIS) and broadcast communications operations (Broadcast Communications) which were part of its Integrated Network Solutions segment. In February 2013, the Company sold Broadcast Communications business to an affiliate of The Gores Group, LLC."},{"Symbol":"HIG","Name":"Hartford Financial Svc.Gp.","Sector":"Financials","Description":"Hartford Financial Services Group Inc., formerly The Hartford Financial Services Group, Inc., is an insurance and financial services company. The Company is a provider of investment products and life, property, and casualty insurance to both individual and business customers in the United States of America. The Company maintains a retail mutual fund operation, whereby the Company, through wholly owned subsidiaries, provides investment management and administrative services to The Hartford Mutual Funds, Inc. and The Hartford Mutual Funds II, Inc. (collectively, mutual funds), consisting of 57 mutual funds, as of December 31, 2011. The Company operates in four segments: Commercial Markets, Consumer Markets, Wealth Management and Runoff Operations. In October 2011, the Company sold Trumbull Services, LLC to ExlService Holdings, Inc."},{"Symbol":"HAS","Name":"Hasbro Inc.","Sector":"Consumer Discretionary","Description":"Hasbro, Inc., engaged in providing children's and family leisure time products with a range of portfolio of brands and entertainment properties. The Company's brand architecture identifies franchise brands, challenger brands, key partner brands and new brands. Franchise and challenger brands include many well-known brand names such as TRANSFORMERS, NERF, LITTLEST PET SHOP, MY LITTLE PONY, BABY ALIVE, G.I. JOE, MONOPOLY, MAGIC: THE GATHERING, PLAY-DOH and PLAYSKOOL. In addition to product offerings under Hasbro-owned brands or brands which if not entirely owned are broadly controlled by the Company, offerings may also include products branded and developed under key licenses. The licensed brands include BEYBLADE, SPIDER-MAN and THE AVENGERS from MARVEL, SESAME STREET, STAR WARS and ZYNGA product offerings. In July 2013, Hasbro Inc announced the acquisition of a majority stake in Backflip Studios."},{"Symbol":"HCA","Name":"HCA Holdings","Sector":"Health Care","Description":"HCA Holdings, Inc. (HCA) is a holding company whose affiliates owns and operates hospitals and related health care entities. HCA is a health care services companies in the United States. At December 31, 2011, it operated 163 hospitals, comprised of 157 general, acute care hospitals; five psychiatric hospitals, and one rehabilitation hospital. In addition, it operated 108 freestanding surgery centers. Its operations are structured into three geographically organized groups: the National, Southwest and Central Groups. At December 31, 2011, the National Group includes 64 hospitals located in Florida, South Carolina, southern Georgia, Alaska, California, Nevada, Utah and Idaho, the Southwest Group includes 46 hospitals located in Colorado, Texas, Oklahoma and the Wichita, Kansas market, and the Central Group includes 47 hospitals. During October 2011, the Company acquired the Colorado Health Foundation's (Foundation).In December 2011, it sold Palmyra Medical Center in Albany, Ga."},{"Symbol":"HCP","Name":"HCP Inc.","Sector":"Real Estate","Description":"HCP, Inc. (HCP) is a real estate investment trust (REIT). The Company invests primarily in real estate serving the healthcare industry in the United States. HCP acquires, develops, leases, manages and disposes of healthcare real estate and provides financing to healthcare providers. The Company's portfolio consists of investments in the five healthcare segments: senior housing, life science, medical office, post-acute/skilled nursing and hospital. The Company makes investments within its healthcare segments using the five investment products: properties under lease, debt investments, developments and redevelopments, investment management and RIDEA, which represents investments in senior housing operations. During the year ended December 31, 2011, the Company sold three senior housing facilities. In October 2012, Emeritus Corp announced that HCP closed the acquisition of 127 of the 133 senior housing communities."},{"Symbol":"HP","Name":"Helmerich & Payne","Sector":"Energy","Description":"Helmerich & Payne, Inc. is engaged in contract drilling of oil and gases wells for others and this business. The Company's contract drilling business is composed of three reportable business segments: U.S. Land, Offshore and International Land. During the fiscal year ended September 30, 2012 (fiscal 2012), the Company's U.S. Land operations drilled in Oklahoma, California, Texas, Wyoming, Colorado, Louisiana, Pennsylvania, Ohio, Utah, Arkansas, New Mexico, Montana, North Dakota and West Virginia. Offshore operations were conducted in the Gulf of Mexico, and offshore of California, Trinidad and Equatorial Guinea. During fiscal 2012, the Company's International Land segment operated in six international locations: Ecuador, Colombia, Argentina, Tunisia, Bahrain and United Arab Emirates. The Company is also engaged in the ownership, development and operation of commercial real estate and the research and development of rotary steerable technology."},{"Symbol":"HSIC","Name":"Henry Schein","Sector":"Health Care","Description":"Henry Schein, Inc. is a provider of health care products and services to office-based practitioners. The Company conducts its business through two segments: healthcare distribution and technology. The healthcare distribution segment aggregates its dental, medical, animal health and international operating segments. This segment consists of consumable products, small equipment, laboratory products, large dental equipment, equipment repair services, branded and generic pharmaceuticals, vaccines, surgical products, diagnostic tests, infection-control products and vitamins. Its technology group provides software, technology and other services to healthcare practitioners, primarily in the United States, Canada, the United Kingdom, Australia and New Zealand. In July 2013, Henry Schein Inc divested an unprofitable non controlling stake in a dental wholesale business in the Middle East."},{"Symbol":"HES","Name":"Hess Corporation","Sector":"Energy","Description":"Hess Corporation (Hess) is a global integrated energy company that operates in two segments: Exploration and Production (E&P) and Marketing and Refining (M&R). The E&P segment explores for, develops, produces, purchases, transports and sells crude oil and natural gas. These exploration and production activities take place principally in Algeria, Australia, Azerbaijan, Brazil, Brunei, China, Denmark, Egypt, Equatorial Guinea, France, Ghana, Indonesia, the Kurdistan region of Iraq, Libya, Malaysia, Norway, Peru, Russia, Thailand, the United Kingdom and the United States. The M&R segment manufactures refined petroleum products and purchases, markets and trades refined petroleum products, natural gas and electricity. In December 2013, the Company sold its Natuna A asset, located off the coast of Indonesia to PT Pertamina and PTT Exploration and Production company Limited."},{"Symbol":"HPE","Name":"Hewlett Packard Enterprise","Sector":"Information Technology","Description":"Hewlett Packard Enterprise Company offers IT consulting and technology services. The company was founded in 2014 and is based in Palo Alto, California. Hewlett Packard Enterprise Company operates as a subsidiary of Hewlett-Packard Company."},{"Symbol":"HOLX","Name":"Hologic","Sector":"Health Care","Description":"Hologic, Inc. is a developer, manufacturer and supplier of diagnostics products, medical imaging systems and surgical products to serving the healthcare needs of women. The Company operates in four segments: Breast Health, Diagnostics, GYN Surgical and Skeletal Health. The Company sells and services its products through a combination of direct sales and service forces and a network of independent distributors and sales representatives. On August 1, 2012, the Company acquired Gen-Probe Incorporated (Gen-Probe). Gen-Probe is engaged in molecular diagnostics products and services, which are used to diagnose human diseases, screen donated human blood and test transplant compatibility. In March 2013, it announced that it has completed its sale of the LIFECODES business unit to Immucor, Inc."},{"Symbol":"HD","Name":"Home Depot","Sector":"Consumer Discretionary","Description":"The Home Depot, Inc. (The Home Depot) is a home improvement retailer. The Company operates The Home Depot stores, which are full-service, warehouse-style stores. The Home Depot stores sell an assortment of building materials, home improvement and lawn and garden products and provide a number of services. The Home Depot stores average approximately 104,000 square feet of enclosed space, with approximately 24,000 additional square feet of outside garden area. The Home Depot stores serve three primary customer groups: do-it-yourself (D-I-Y) customers, do-it-for-me (D-I-F-M) customers and professional customers. As of February 3, 2013, the Company was operating 2,256 The Home Depot stores located throughout the United States, including the Commonwealth of Puerto Rico and the territories of the United States Virgin Islands and Guam, Canada and Mexico. In February 2013, it acquired Measurecomp LLC and HD Components LLC."},{"Symbol":"HON","Name":"Honeywell Int'l Inc.","Sector":"Industrials","Description":"Honeywell International Inc. (Honeywell) is a diversified technology and manufacturing company, serving customers worldwide with aerospace products and services, control, sensing and security technologies for buildings, homes and industry, turbochargers, automotive products, specialty chemicals, electronic and advanced materials, process technology for refining and petrochemicals, and energy efficient products and solutions for homes, business and transportation. Honeywell operates four business segments: Aerospace, Automation and Control Solutions, Performance Materials and Technologies, and Transportation Systems. In June 2013, the Company acquired RAE Systems, Inc. In September 2013, Honeywell International Inc announced that it has completed its acquisition of Intermec Inc, a provider of mobile computing, radio frequency identification solutions (RFID), voice-enabled workflow and data-collection solutions, and printing solutions."},{"Symbol":"HRL","Name":"Hormel Foods Corp.","Sector":"Consumer Staples","Description":"Hormel Foods Corporation is primarily engaged in the production of a variety of meat and food products and the marketing of those products throughout the United States and internationally. The Company markets its products through Hormel Foods International Corporation (HFIC), a wholly owned subsidiary. The Company's operates in five segments: grocery products, refrigerated foods, Jennie-O Turkey Store (JOTS), specialty foods, and all other. The meat products are sold fresh, frozen, cured, smoked, cooked, and canned. Perishable meat includes fresh meats, refrigerated meal solutions, sausages, hams, wieners, and bacon (excluding JOTS products). Shelf-stable includes canned luncheon meats, shelf-stable microwaveable entrees, stews, chilies, hash, meat spreads, flour and corn tortillas, salsas, tortilla chips, and other items that do not require refrigeration. The Other category primarily consists of nutritional food products and supplements, and industrial gelatin products."},{"Symbol":"HST","Name":"Host Hotels & Resorts","Sector":"Real Estate","Description":"Host Hotels & Resorts, Inc (Host Inc), incorporated on September 28, 1998 operates as a self-managed and self-administered REIT. Host Inc. owns properties and conducts operations through Host Hotels & Resorts, L.P. (Host L.P.) of which Host Inc. is the sole general partner and in which it holds approximately 98.6% of the partnership interests (OP units) as of December 31, 2012. As of February 25, 2013, the Company had 118 primarily luxury and upper-upscale hotels containing approximately 62,600 rooms, with the majority located in the United States of America, and 15 properties located outside of the United States of America, in Canada, New Zealand, Chile, Australia, Mexico and Brazil. In June 2013, the Company announced that it acquired the fee-simple interest in the 426-room Hyatt Place Waikiki Beach in Honolulu. In July 2013, the Company sold the 336-room Ritz-Carlton, San Francisco to an investment vehicle sponsored by Thayer Lodging Group."},{"Symbol":"HPQ","Name":"HP Inc.","Sector":"Information Technology","Description":"Hewlett-Packard Company (HP) is a provider of products, technologies, software, solutions and services to individual consumers, small- and medium-sized businesses (SMBs) and large enterprises, including customers in the Government, health and education sectors. Its operations are organized into seven segments: the Personal Systems Group (PSG), Services, the Imaging and Printing Group (IPG), Enterprise Servers, Storage and Networking (ESSN), HP Software, HP Financial Services (HPFS) and Corporate Investments. In March 2012, HP had consolidated PSG and IPG into a Printing and Personal Systems Group. HP continues to report the results of IPG and PSG separately. HP's offerings include personal computing and other access devices; multi-vendor customer services, including infrastructure technology and business process outsourcing, application development and support services, and imaging and printing-related products and services. In December 2011, the Company acquired Hiflex Software GmbH."},{"Symbol":"HUM","Name":"Humana Inc.","Sector":"Health Care","Description":"Humana Inc. (Humana) is a health care company. Humana operates in three segments: Retail, Employer Group, and Health and Well-Being Services. The Company offers a range of insurance products and health and wellness services that incorporate an integrated approach to lifelong well-being. As of December 31, 2011, it had approximately 11.2 million members in its medical benefit plans, as well as approximately 7.3 million members in its specialty products. During the year ended December, 31, 2011, 76% of its premiums and services revenue were derived from contracts with the federal government, including 16% related to its Medicare Advantage contracts in Florida with the Centers for Medicare and Medicaid Services (CMS). In August 2012, the Company acquired Harris, Rothenberg International, Inc. In November 2012, the Company acquired Certify Data Systems. In December 2012, the Company acquired Metropolitan Health Networks, Inc."},{"Symbol":"HBAN","Name":"Huntington Bancshares","Sector":"Financials","Description":"Huntington Bancshares Incorporated (Huntington) is a multi-state diversified regional bank holding company. Through its subsidiaries, including its bank subsidiary, The Huntington National Bank (the Bank), Huntington is engaged in providing full-service commercial, small business, consumer banking services, mortgage banking services, automobile financing, equipment leasing, investment management, trust services, brokerage services, customized insurance programs, and other financial products and services. The Company operates in four segments: Retail and Business Banking , Regional and Commercial Banking, Automobile Finance and Commercial Real Estate and Wealth Advisors, Government Finance, and Home Lending . At December 31, 2011, the Bank had 652 branches. A Treasury/Other function includes its insurance brokerage business, which specializes in commercial property and casualty, employee benefits, personal lines, life and disability and specialty lines of insurance."},{"Symbol":"IDXX","Name":"IDEXX Laboratories","Sector":"Health Care","Description":"IDEXX Laboratories, Inc. develops, manufactures and distributes products and provides services primarily for the companion animal veterinary, livestock and poultry, water testing and dairy markets. The Company operates through three segments: diagnostic and information technology-based products and services for the veterinary market, which it refers to as the Companion Animal Group (CAG), water quality products (Water) and products for livestock and poultry health, which it refer to as Livestock and Poultry Diagnostics (LPD). It also operates two smaller segments that consist of products for milk quality and safety (Dairy) and products for the human point-of-care medical diagnostics market (OPTI Medical). In November 2011, the Company acquired the research and diagnostic laboratory (RADIL) business of the College of Veterinary Medicine from the University of Missouri. In August 2013, IDEXX Laboratories, Inc. announced that it has acquired Madasa do Brasil Ltda."},{"Symbol":"ITW","Name":"Illinois Tool Works","Sector":"Industrials","Description":"Illinois Tool Works Inc. (ITW) is a multinational manufacturer of a diversified range of industrial products and equipment with operations in 58 countries. The Company operates in eight business segments: Transportation, Power Systems & Electronics, Industrial Packaging, Food Equipment; Construction Products, Polymers & Fluids, Decorative Surfaces, and All Other. The Company primarily serves the construction, general industrial, automotive, food institutional/restaurant and service, food and beverage, electronics, consumer durables, primary metals, and others markets. In March 2011, the Company acquired SOPUS Products' Car Care business. In July 2011, the Company acquired Despatch Industries. In July 2013, Illinois Tool Works Inc acquired Gold Pattern Holdings."},{"Symbol":"ILMN","Name":"Illumina Inc","Sector":"Health Care","Description":"Illumina, Inc. provides sequencing and array-based solutions for genetic analysis in North America, Europe, Latin America, the Asia-Pacific, the Middle East, and South Africa. The companys products include sequencing platforms that are based on its SBS technology, which provides researchers with various ranges of applications and the ability to sequence mammalian genomes; and array platforms consist of HiScan and iScan systems, as well as NextSeq 550 system that are array scanners for DNA and RNA analysis applications, including single nucleotide polymorphism genotyping, copy number variations analysis, gene expression analysis, and methylation analysis. It also offers various library preparation and sequencing kits to simplify workflows and accelerate analysis. In addition, the company provides genotyping, noninvasive prenatal test, and whole-genome sequencing services. It serves genomic research centers, academic institutions, government laboratories, hospitals, and reference laboratories, as well as pharmaceutical, biotechnology, agrigenomics, commercial molecular diagnostic, and consumer genomics companies. The company sells its products directly, as well as through distributors. It has a collaboration agreement with Merck Serono to develop a sequencing-based oncology diagnostic. Illumina, Inc. was founded in 1998 and is headquartered in San Diego, California."},{"Symbol":"INCY","Name":"Incyte","Sector":"Health Care","Description":"Incyte Corporation (Incyte) is a biopharmaceutical company focused on the discovery, development and commercialization of small molecule drugs to treat serious unmet medical needs. Its advanced compound, JAKAFI (ruxolitinib), also known as INCB18424 and INC424, is an oral Janus associated kinase (JAK) inhibitor used for the treatment for patients with intermediate or high-risk myelofibrosis (MF), including primary MF, post-polycythemia vera MF and post-essential thrombocythemia MF. The Company has a second oral JAK1 and JAK2 inhibitor, LY3009104 (INCB28050), which is subject to a collaboration agreement with Eli Lilly and Company in which Lilly received worldwide development and commercialization rights for the compound for inflammatory and autoimmune diseases. In November 2011, JAKAFI became commercially available in the United States and is being marketed in the United States."},{"Symbol":"IR","Name":"Ingersoll-Rand PLC","Sector":"Industrials","Description":"Ingersoll-Rand plc (IR-Ireland) is a diversified, global company that provides products, services and solutions to enhance the comfort of air in homes and buildings, transport and protect food and perishables, secure homes and commercial properties. IR-Ireland operates in four business segments: Climate Solutions, Residential Solutions, Industrial Technologies and Security Technologies. It generates revenue and cash primarily through the design, manufacture, sale and service of a diverse portfolio of industrial and commercial products that include Club Car, Ingersoll-Rand, Schlage, Thermo King and Trane. On September 30, 2011, IR-Ireland completed the transaction to sell 60% in the Hussmann business. In December 2013, the Company announced that it has completed the spinoff of the Company's commercial and residential security businesses named Allegion."},{"Symbol":"INTC","Name":"Intel Corp.","Sector":"Information Technology","Description":"Intel Corporation designs and manufactures integrated digital technology platforms. A platform consists of a microprocessor and chipset. The Company's platforms are used in a range of applications, such as personal computers (PCs) (including Ultrabook systems), data centers, tablets, smartphones, automobiles, automated factory systems and medical devices. In February 2013, it acquired ProFUSION-Comercio e Prestacao de Servicos em Tecnologia da Informacao Ltda. In April 2013, Intel Corp acquired Aepona Ltd. Effective July 16, 2013, Intel Corp acquired Omek Interactive Ltd. Effective August 15, 2013, Intel Corp acquired Fujitsu Semiconductor Wireless Products Inc, from Fujitsu Semiconductor Ltd, a wholly owned subsidiary of Fujitsu Ltd. Effective November 8, 2013, Intel Corp acquired Kno Inc, a developer of educational software.."},{"Symbol":"ICE","Name":"Intercontinental Exchange","Sector":"Financials","Description":"No Description Available"},{"Symbol":"IBM","Name":"International Business Machines","Sector":"Information Technology","Description":"International Business Machines Corporation (IBM) is an information technology (IT) company. IBM operates in five segments: Global Technology Services (GTS), Global Business Services (GBS), Software, Systems and Technology and Global Financing. GTS primarily provides IT infrastructure services and business process services. GBS provides professional services and application management services. Software consists primarily of middleware and operating systems software. Systems and Technology provides clients with business solutions requiring advanced computing power and storage capabilities. In October 2013, International Business Machines Corporation acquired Xtify Inc. In October 2013, the Company announced that it has completed the acquisition of The Now Factory, a privately held provider of analytics software that helps communications service providers (CSPs) deliver better customer experiences and drive new revenue opportunities."},{"Symbol":"IP","Name":"International Paper","Sector":"Materials","Description":"International Paper Company (International Paper) is a global paper and packaging company, with primary markets and manufacturing operations in North America, Europe, Latin America, Russia, Asia and North Africa. The Company operates in four segments: Industrial Packaging, Printing Papers, Consumer Packaging and Distribution. As of December 31, 2012, in the United States, the Company operated 28 pulp, paper and packaging mills, 187 converting and packaging plants, 18 recycling plants and three bag facilities. Production facilities as of December 31, 2012 in Europe, Asia, Latin America and South America included 11 pulp, paper and packaging mills, 65 converting and packaging plants, and two recycling plants. On July 2, 2012, it sold Ontario and Oxnard (Hueneme), California containerboard mills to New-Indy Containerboard LLC, and its New Johnsonville, Tennessee containerboard mill to Hood Container Corporation. On January 3, 2013, it acquired joint venture partner, Sabanci Holding."},{"Symbol":"IPG","Name":"Interpublic Group","Sector":"Consumer Discretionary","Description":"The Interpublic Group of Companies, Inc. (Interpublic) is a global advertising and marketing services companies. Interpublic's companies specialize in consumer advertising, digital marketing, communications planning and media buying, public relations and specialized communications disciplines. The Company has two segments: Integrated Agency Networks (IAN) and Constituency Management Group (CMG). IAN is comprised of McCann, Draftfcb, Lowe, Mediabrands and its domestic integrated agencies. CMG is comprised of a number of its specialist marketing services offerings. During the year ended December, 31, 2011, it completed 22 acquisitions, which included purchases of controlling interests in previously unconsolidated subsidiaries. In November 2013, Interpublic Group of Companies Inc's MRM, a global digital and direct agency within McCann Worldgroup, a unit of the Interpublic Group of Companies, acquired the Brazilian agency E/OU."},{"Symbol":"IFF","Name":"Intl Flavors & Fragrances","Sector":"Materials","Description":"International Flavors & Fragrances Inc. (IFF) creates, manufactures and supplies flavors and fragrances for the food, beverage, personal care and household products industries. The Company operates in two business segments: Flavors and Fragrances. Its Flavors business includes four categories of products: Savory, Beverages, Sweet, pharmaceutical and oral care (Sweet), and Dairy. Its Fragrances business consists of Fragrance Compounds and Fragrance Ingredients. Its manufacturing facilities are located in the United States, the Netherlands, Spain, Great Britain, Argentina, Brazil, Mexico, Australia, China, India, Indonesia, Japan and Singapore."},{"Symbol":"INTU","Name":"Intuit Inc.","Sector":"Information Technology","Description":"Intuit Inc. (Intuit) is a provider of business and financial management solutions for small businesses, consumers, accounting professionals and financial institutions. The Company organizes its portfolio of businesses into four principal categories: Small Business Group, Tax, Financial Services and Other Businesses. Small Business Group include seven segments: financial management solutions, employee management solutions, payment solutions, consumer tax, accounting professionals, financial services and other businesses. Tax include two segment: Consumer Tax and Accounting Professionals. Financial Services: This segment consists of digital (online and mobile) banking solutions and Other Businesse segment includes its global businesses. In June 2013, Intuit Inc acquired Elastic Intelligence Inc. Effective August 8, 2013, Intuit Inc acquired GoodApril, a provider of online tax planning services. Effective October 24, 2013, Intuit Inc acquired Level Up Analytics Inc."},{"Symbol":"ISRG","Name":"Intuitive Surgical Inc.","Sector":"Health Care","Description":"Intuitive Surgical, Inc. (Intuitive) designs, manufactures and markets da Vinci Surgical Systems and related instruments and accessories. A da Vinci Surgical System consists of a surgeon's console, a patient-side cart and a high performance vision system. The da Vinci Surgical System translates a surgeon's natural hand movements, which are performed on instrument controls at a console, into corresponding micro-movements of instruments positioned inside the patient through small incisions, or ports. The da Vinci Surgical System is designed to provide its operating surgeon with intuitive control, range of motion, fine tissue manipulation capability and three dimensional (3-D), high-definition (HD) vision while simultaneously allowing the surgeon to work through the small ports of MIS. On January 11, 2012, the Company acquired its Korean distributor."},{"Symbol":"IVZ","Name":"Invesco Ltd.","Sector":"Financials","Description":"Invesco Ltd. (Invesco) is an investment manager, dedicated to helping investors worldwide achieve their financial objectives. By delivering the combined power of its distinctive investment management capabilities, Invesco provides a comprehensive range of investment strategies and vehicles to its retail, institutional and high-net-worth clients around the world. It seeks to generate investment results, positive net flows, increased AUM and associated revenues. It measure relative investment performance by comparing its investment capabilities to competitors' products, industry benchmarks and client investment objectives. Generally, distributors, investment advisors and consultants take into consideration longer-term investment performance (e.g., three-year and five-year performance) in their selection of investment products and manager recommendations to their clients, although shorter-term performance may also be an important consideration."},{"Symbol":"IRM","Name":"Iron Mountain Incorporated","Sector":"Real Estate","Description":"Iron Mountain Incorporated (Iron Mountain) is engaged in storing records, primarily paper documents and data backup media, and provide information management services. The Company offers records management services, data protection and recovery services and information destruction services. Its information management services are divided into three categories: records management services, data protection and recovery services, and information destruction services. In May 2012, the Company acquired records storage firms File House Offsite Record Storage in Fredericksburg, Va., and Document Systems Inc. in Columbia, S.C. and offsite data protection provider First National Safe Deposit in the Philadelphia area. In May 2013, Iron Mountain Inc acquired Information Storage Consolidation Co LLC. In June 2013, Iron Mountain Inc announced that it has acquired Brazilian document storage firm Archivum. In October 2013, the Company announced that it has acquired Cornerstone Records Management."},{"Symbol":"JBHT","Name":"J. B. Hunt Transport Services","Sector":"Industrials","Description":"J B Hunt Transport Services Inc.(JBHT) is a holding company. The Company together with its wholly owned subsidiaries, operates as a surface transportation and delivery services to a diverse group of customers and consumers throughout the continental United States, Canada and Mexico. The Company operates in four segments: Intermodal (JBI), Dedicated Contract Services (DCS), Integrated Capacity Solutions (ICS), and full-load dry-van (JBT).The Company's service offerings include transportation of full-truckload containerized freight, which the Company directly transports utilizing its company-controlled revenue equipment and company drivers or independent contractors. The Company also provides customized freight movement, revenue equipment, labor, systems and delivery services that are tailored to meet individual customers' requirements and typically involve long-term contracts."},{"Symbol":"JEC","Name":"Jacobs Engineering Group","Sector":"Industrials","Description":"Jacobs Engineering Group Inc. is a technical professional services firm. The Company provides a diverse range of technical, professional, and construction services to a number of industrial, commercial, and governmental clients. The Company provides four categories of services: Project Services, Process, Scientific, and Systems Consulting services, Construction services, and Operations and Maintenance services. In June 2013, Jacobs Engineering Group Inc., announced that it has acquired Compass Technology Services, Inc. In October 2013, Jacobs Engineering Group Inc announced that its South African joint venture, Jacobs Matasis (Proprietary) Limited acquired the business and assets of Ilitha Projects and Ilitha Staffing, two South African professional services companies. In November 2013, Jacobs Engineering Group Inc acquired an undisclosed majority interest in Guimar Engenharia SA. In November 2013, the Company acquired MARMAC Field Services, Inc."},{"Symbol":"SJM","Name":"JM Smucker","Sector":"Consumer Staples","Description":"Smucker J M Co, formerly The J. M. Smucker Company, operates principally in the manufacturing and marketing of branded food products. Majority of the Company's sales are in the United States. The Company's operations outside the United States are principally in Canada, although products are exported to other countries as well. The Company operates in three segments: U.S. Retail Coffee, U.S. Retail Consumer Foods, and International, Foodservice and Natural Foods. In August 2013, the Company completed an acquisition of Enray Inc."},{"Symbol":"JNJ","Name":"Johnson & Johnson","Sector":"Health Care","Description":"Johnson & Johnson is a holding company. The Company is engaged in the research and development, manufacture and sale of a broad range of products in the health care field. The business of Johnson & Johnson is conducted by more than 275 operating companies located in 60 countries, including the United States, which sell products in virtually all countries throughout the world. In March 2013, Johnson & Johnson's Cordis Corporation announced the acquisition of Flexible Stenting Solutions, Inc. In June 2013, Johnson & Johnson announced the opening of the Johnson & Johnson Innovation center in Boston. In August 2013, Johnson & Johnson announced it has completed its acquisition of Aragon Pharmaceuticals, Inc., a pharmaceutical discovery and development company focused on drugs to treat hormonally driven cancers."},{"Symbol":"JCI","Name":"Johnson Controls International","Sector":"Industrials","Description":"Johnson Controls, Inc. (Johnson Controls) manufactures, installs and services automatic temperature regulation systems for buildings. The Building Efficiency business is engaged in designing, producing, marketing and installing integrated heating, ventilating and air conditioning (HVAC) systems, building management systems, controls, security and mechanical equipment. The Power Solutions business is a global supplier of lead-acid automotive batteries for virtually every type of passenger car, light truck and utility vehicle. The Automotive Experience business is an automotive supplier, providing interior systems. In May 2013, Johnson Controls Inc acquired the remaining 50% interest in Tata Johnson Controls Automotive Ltd from its joint-venture partner Tata Autocomp Systems Ltd, a unit of Tata Sons Ltd. On September 27, 2013, the Company sold its HomeLink business to Gentex Corporation. Effective November 8, 2013, Johnson Controls Inc acquired a 90% interest in MAC SA."},{"Symbol":"JPM","Name":"JPMorgan Chase & Co.","Sector":"Financials","Description":"JPMorgan Chase & Co. (JPMorgan Chase) is a financial holding company. The Company is a global financial services firm and a banking institution in the United States, with global operations. The Company is engaged in investment banking, financial services for consumers and small businesses, commercial banking, financial transaction processing, asset management and private equity. JPMorgan Chase's principal bank subsidiaries are JPMorgan Chase Bank, National Association (JPMorgan Chase Bank, N.A.), and Chase Bank USA, National Association (Chase Bank USA, N.A.). JPMorgan Chase's activities are organized into four business segments, as well as Corporate/Private Equity. The Company's consumer business is the Consumer & Community Banking segment. The Company's wholesale businesses consists of Corporate & Investment Bank, Commercial Banking, and Asset Management segments."},{"Symbol":"JNPR","Name":"Juniper Networks","Sector":"Information Technology","Description":"Juniper Networks, Inc. (Juniper Networks) designs, develops, and sells products and services that together provide its customers with network infrastructure. It operates in two segments: Infrastructure and Service Layer Technologies (SLT). The Company's Infrastructure segment primarily offers routing and switching products that are used to control and direct network traffic from the core, through the edge, aggregation, and the customer premise equipment level. Infrastructure products include its Internet protocol (IP) routing, carrier Ethernet routing portfolio, and Ethernet switching portfolio. Its SLT segment offers solutions that meet a range of its customers' priorities, from protecting the users, applications and data on the network itself to providing network services across a distributed infrastructure. Effective September 13, 2013, Juniper Networks, Inc. acquired Contrail Networks Inc."},{"Symbol":"KSU","Name":"Kansas City Southern","Sector":"Industrials","Description":"Kansas City Southern (KCS) is a transportation holding company with domestic and international rail operations in North America that are strategically focused on the growing north/south freight corridor connecting key commercial and industrial markets in the central United States with industrial cities in Mexico. The KCS coordinated rail network (KCSR, KCSM and Tex-Mex) comprises approximately 6,300 route miles extending from the mid west and southeast portions of the United States south into Mexico and connects with all other Class I railroads, providing shippers with an effective alternative to other railroad routes and giving direct access to Mexico and the southeast and southwest United States through alternate interchange hubs."},{"Symbol":"K","Name":"Kellogg Co.","Sector":"Consumer Staples","Description":"Kellogg Company (Kellogg) is engaged in the manufacture and marketing of ready-to-eat cereal and convenience foods. The Company's principal products are ready-to-eat cereals and convenience foods, such as cookies, crackers, savory snacks, toaster pastries, cereal bars, fruit-flavored snacks, frozen waffles and veggie foods. As of February 26, 2013, these products were manufactured by the Company in 18 countries and marketed in more than 180 countries. Kellogg also market cookies, crackers, crisps, and other convenience foods, under brands, such as Kellogg's, Keebler, Cheez-It, Murray, Austin and Famous Amos, to supermarkets in the United States. The Company's cereal products are generally marketed under the Kellogg's name and are sold to the grocery trade through direct sales forces for resale to consumers. Effective June 1, 2012, Procter & Gamble Co announced that it has completed the sale of its Pringles business to Kellogg."},{"Symbol":"KEY","Name":"KeyCorp","Sector":"Financials","Description":"KeyCorp, is a bank holding company. It is a bank-based financial services company. KeyCorp is the parent holding company for KeyBank National Association (KeyBank), its principal subsidiary, through which most of its banking services are provided. Through KeyBank and certain other subsidiaries, the Company provides a range of retail and commercial banking, commercial leasing, investment management, consumer finance and investment banking products and services to individual, corporate and institutional clients through two major business segments: Key Community Bank and Key Corporate Bank. In August 2013, KeyCorp closed on its agreement to sell Victory Capital Management and its broker dealer affiliate Victory Capital Advisers."},{"Symbol":"KMB","Name":"Kimberly-Clark","Sector":"Consumer Staples","Description":"Kimberly-Clark Corporation (Kimberly Clark) is a global company focused on the world in essentials for a better life through product innovation and building its personal care, consumer tissue, K C professional and health care brands. The Company is principally engaged in the manufacturing and marketing of a wide ranges of products made from natural or synthetic fibers using advanced technologies in fibers, nonwovens and absorbency. Its operating segments include Personal Care , Consumer Tissue K C Professional and HealthCare. The Company operates and markets its products globally in Asia, Latin America, Eastern Europe, the Middle East and Africa, with a particular emphasis in China, Russia and Latin America. In April 2013, it announced the acquisition of the anesthesia business of Life-Tech, Inc."},{"Symbol":"KIM","Name":"Kimco Realty","Sector":"Real Estate","Description":"Kimco Realty Corp. is a real estate investment trust (REIT) that owns and operates North America's largest portfolio of neighborhood and community shopping centers. As of December 31, 2012, the Company owned interests in 896 shopping centers comprising 131 million square feet of leasable space across 44 states, Puerto Rico, Canada, Mexico and South America. The Company is specialized in shopping center acquisitions, development and management. In December 2012, the Company acquired City Heights Retail Village in San Diego. In June 2013, Skyway Development Group LLC announced that it has closed on the acquisition of 30 West 21st Street from Kimco Realty Corporation. In October 2013, Terrafina SA de CV completed the acquisition of the industrial portfolio of Kimco Realty Corp and its partner American Industries. In November 2013, the Company purchased a three-property, 189,000-square-foot grocery-anchored portfolio in the New York City metropolitan statistical area (MSA)."},{"Symbol":"KMI","Name":"Kinder Morgan","Sector":"Energy","Description":"Kinder Morgan, Inc. (KMI) owns and manages a diversified portfolio of energy transportation and storage assets. The Company operates in five business segments: Products Pipelines-KPM, Natural Gas Pipelines-KMP, CO2-KMP, Terminals-KMP and Kinder Morgan Canada-KMP. The Company through Kinder Morgan Energy Partners, L.P. (KMP) operates or owns an interest in approximately 37,000 miles of pipelines and approximately 180 terminals. These pipelines transport natural gas, refined petroleum products, crude oil, carbon dioxide and other products, and its terminals store petroleum products and chemicals, and handle such products as ethanol, coal, petroleum coke and steel. The Company is a provider of carbon dioxide (CO2), for enhanced oil recovery projects in North America. On May 25, 2012, KMI acquired El Paso Corporation. In August 2012, Kinder Morgan Energy Partners, L.P. acquired Tennessee Gas Pipeline (TGP) and a 50% interest in El Paso Natural Gas (EPNG) pipeline from KMI."},{"Symbol":"KLAC","Name":"KLA-Tencor Corp.","Sector":"Information Technology","Description":"KLA-Tencor Corporation (KLA-Tencor) is engaged in the design, manufacture and marketing of process control and yield management solutions for the semiconductor and related nanoelectronics industries. KLA-Tencor's offerings include the Chip Manufacturing, Wafer Manufacturing, Reticle Manufacturing, Complementary Metal-Oxide-Semiconductor (CMOS) Image Sensors Manufacturing, Solar Manufacturing, light emitting diode (LED) Manufacturing, Data Storage Media/Head Manufacturing, Microelectromechanical Systems (MEMS) Manufacturing, and General Purpose/Lab Applications. It also provides refurbished KLA-Tencor tools as part of its K-T Certified program for customers manufacturing larger design-rule devices, as well as service and support for its products. The Company's products are used in a number of other industries, including the LED, data storage and photovoltaic industries, as well as general materials research."},{"Symbol":"KSS","Name":"Kohl's Corp.","Sector":"Consumer Discretionary","Description":"Kohl's Corporation (Kohl's) operate family-oriented department stores that sell apparel, footwear and accessories for women, men and children; soft home products, such as sheets and pillows, and housewares. Kohl's apparel and home fashions appeal to classic, modern classic and contemporary customers. As of January 28, 2012, the Company operated 1,127 stores in 49 states. In addition, Kohl's offers on-line shopping on its Website at www.Kohls.com. Originally designed as an added service for customers who prefer to shop using the Internet, the Website include a selection of items and categories beyond what is available in stores, with a primary focus on sizes, product line extensions, and Web-exclusive product lines. Only at Kohl's is a brand of the Company. In March 2012, the Company opened eight new stores. In March 2013, it announced the openings of nine new stores, bringing the Company's store count to 1,155 stores."},{"Symbol":"KHC","Name":"Kraft Heinz Co","Sector":"Consumer Staples","Description":"The Kraft Heinz Company, through its subsidiaries, operates as a food and beverage company in North America and internationally. It primarily offers food and beverage products, including cheese, meats, refreshment beverages, coffee, packaged dinners, refrigerated meals, snack nuts, dressings, ketchup, condiments and sauces, frozen food, soups, beans and pasta meals, infant nutrition, and other food and grocery products. The company offers its products under the brands, including Kraft, Heinz, ABC, Capri Sun, Classico, Jell-O, Kool-Aid, Lunchables, Maxwell House, Ore-Ida, Oscar Mayer, Philadelphia, Planters, Plasmon, Quero, Weight Watchers Smart Ones, and Velveeta. The company was formerly known as H.J. Heinz Holding Corporation and changed its name to The Kraft Heinz Company in July 2015. The Kraft Heinz Company is based in Pittsburgh, Pennsylvania."},{"Symbol":"KR","Name":"Kroger Co.","Sector":"Consumer Staples","Description":"The Kroger Co. is a retailer in the United States. The Company also manufactures and processes some of the food for sale in its supermarkets. As of January 28, 2012, it operated, either directly or through its subsidiaries, 2,435 supermarkets and multi-department stores, 1,090 of which had fuel centers. As of January 28, 2012, it operated 2,435 supermarket and multi-department stores under banners, including Kroger, City Market, Dillons, Jay C, Food 4 Less, Fred Meyer, Fry's, King Soopers, QFC, Ralphs and Smith's. Of these stores, 1,090 have fuel centers. It also operates 791 convenience stores, either directly or through franchisees, and 348 jewelry stores. In addition to the supermarkets, as of January 28, 2012, it operated through subsidiaries 791 convenience stores and 348 jewelry stores. All of its jewelry stores located in malls is operated in leased locations. All of its jewelry stores located in malls is operated in leased locations."},{"Symbol":"LB","Name":"L Brands Inc.","Sector":"Consumer Discretionary","Description":"L Brands, Inc., formerly Limited Brands, Inc operates in the specialty retail business. The Company is a specialty retailer of women's intimate and other apparel, beauty and personal care products and accessories. It operates in two segments: Victoria's Secret and Bath & Body Works. It sells its merchandise through Company-owned specialty retail stores in the United States, Canada and the United Kingdom, which are primarily mall-based, and through Websites, catalogue and international franchise, license and wholesale partners. It operates in brands, such as Victoria's Secret, Victoria's Secret Pink, Bath & Body Works, La Senza, and Henri Bendel. Its business for both the Victoria's Secret and Bath & Body Works segments is principally conducted from office, distribution and shipping facilities located in the Columbus, Ohio area. As of February 2, 2013, it operated 2,619 retail stores located in leased facilities, primarily in malls and shopping centers, throughout United States."},{"Symbol":"LLL","Name":"L-3 Communications Holdings","Sector":"Industrials","Description":"L-3 Communications Holdings, Inc. (L-3 Holdings) is a prime contractor in Command, Control, Communications, Intelligence, Surveillance and Reconnaissance (C3ISR) systems, aircraft modernization and maintenance, and government services. It is also a provider of a range of electronic systems used on military and commercial platforms. Its customers include the United States Department of Defense and its prime contractors, United States Government intelligence agencies, the United States Department of Homeland Security, United States Department of State, United States Department of Justice, and allied foreign governments, domestic and foreign commercial customers. It operates in four segments: C3ISR, Government Services, Aircraft Modernization and Maintenance and Electronic Systems. On February 6, 2012, it acquired Kollmorgen Electro-Optical unit of Danaher Corporation. On August 6, 2012, it acquired Thales Training & Simulation Ltd's civil aircraft simulation and training business."},{"Symbol":"LH","Name":"Laboratory Corp. of America Holding","Sector":"Health Care","Description":"Laboratory Corporation of America Holdings is a clinical laboratory company in the United States. Through a national network of laboratories, the Company offers a range of testing services used by the medical profession in routine testing, patient diagnosis, and in the monitoring and treatment of disease. In addition, it has developed specialty and niche operations based on certain types of specialized testing capabilities and client requirements, such as oncology testing, human immunodeficiency virus (HIV) genotyping and phenotyping, diagnostic genetics and clinical research trials. It processes tests on approximately 470,000 patient specimens daily and provides clinical laboratory testing services in all 50 states, the District of Columbia, Puerto Rico, Belgium, Japan, the United Kingdom, China, Singapore and three provinces in Canada."},{"Symbol":"LRCX","Name":"Lam Research","Sector":"Information Technology","Description":"Lam Research Corporation (Lam Research), is a supplier of wafer fabrication equipment and services to the worldwide semiconductor industry. Lam Research designs, manufactures, markets, refurbish, and services semiconductor processing equipment used in the fabrication of integrated circuit. In addition, it offers a broad portfolio of single-wafer clean technologies. Its Customer Support Business Group (CSBG) provides products and services to maximize installed equipment performance and operational efficiency. It offers a range of services including customer service, spares, upgrades, refurbishment of its etch, deposition, photoresist strip, and clean products. The Company also develops manufactures, sells and supports equipment used in grinding, lapping and polishing precision parts used in a spectrum of industrial applications.In June 2012, Lam Research completed its merger with Novellus Systems, Inc."},{"Symbol":"LEG","Name":"Leggett & Platt","Sector":"Consumer Discretionary","Description":"Leggett & Platt, Incorporated is an international manufacturer that conceives, designs and produces a range of engineered components and products found in many homes, offices, retail stores, automobiles and commercial aircraft. The Company's operations are organized into 20 business units, which are divided into 10 groups under its four segments: Residential Furnishings; Commercial Fixturing & Components; Industrial Materials, and Specialized Products. On January 12, 2012, the Company completed the acquisition of Western Pneumatic Tube Holding, LLC. In Leggett & Platt Inc acquired Specitubes SAS from PFW Aerospace AG."},{"Symbol":"LEN","Name":"Lennar Corp.","Sector":"Consumer Discretionary","Description":"Lennar Corporation is a homebuilder and a provider of financial services and through its Rialto Investments (Rialto) segment, an investor, and manager of funds that invest in real estate assets. The Company's homebuilding operations include the construction and sale of single-family attached and detached homes, as well as the purchase, development and sale of residential land directly and through unconsolidated entities in which it has investments. Its homebuilding activities have five segments: Homebuilding East, Homebuilding Central, Homebuilding West, Homebuilding Southeast Florida, and Homebuilding Houston. It has two other segments: financial services and rialto. Its financial services reportable segment provides mortgage financing, title insurance and closing services for both buyers of its homes and others. Rialto segment focuses on real estate investments and asset management."},{"Symbol":"LUK","Name":"Leucadia National Corp.","Sector":"Financials","Description":"Leucadia National Corporation is a diversified holding company. Leucadia's largest business is Jefferies, a global, full service investment banking firm. Leucadia also owns and holds investments in a range of other businesses, including beef processing, manufacturing, telecommunications, gaming, real estate, and energy."},{"Symbol":"LVLT","Name":"Level 3 Communications","Sector":"Telecommunications Services","Description":"Level 3 Communications, Inc. (Level 3) is a facilities-based provider of a range of integrated communications services. It has created its communications network by constructing its own assets and through a combination of purchasing other companies and purchasing or leasing facilities from others. Its network is an international, facilities-based communications network. Level 3 provides communications services to a range of wholesale and enterprise customers. Wholesale customers may use its services as a component in their own offering of telecommunications services to their customers. Enterprise customers use its services for their own needs. It further subdivides enterprise customers into four subgroups: large enterprise customers, which consist of its roughly 1,000 largest enterprise customers other than content customers; government customers; mid-market customers, which are enterprise customers other than large enterprise customers, and content customers."},{"Symbol":"LLY","Name":"Lilly (Eli) & Co.","Sector":"Health Care","Description":"Eli Lilly and Company discovers, develops, manufactures, and sells products, in one business segment, pharmaceutical products. The Company also has an animal health business segment. It manufactures and distributes its products through facilities in the United States, Puerto Rico, and 15 other countries. Its products are sold in approximately 130 countries. The Company's products include neuroscience products, endocrinology products, oncology products, cardiovascular products, animal health products and other pharmaceuticals. The Company's new molecular entities (NMEs), which are in Phase III clinical trial testing include Dulaglutide, Edivoxetine, Ixekizumab, Necitumumab, New insulin glargine product, Novel basal insulin analog, Pomaglumetad Methionil, Ramucirumab, Solanezumab and Tabalumab. In February 2012, the Company acquired ChemGen Corp. On July 7, 2011, it acquired the animal health business of Janssen, a Johnson & Johnson company."},{"Symbol":"LNC","Name":"Lincoln National","Sector":"Financials","Description":"Lincoln National Corporation, through its subsidiaries, engages in multiple insurance and retirement businesses in the United States. It operates through Annuities, Retirement Plan Services, Life Insurance, and Group Protection segments. The company sells a range of wealth protection, accumulation, and retirement income products and solutions. Its products include fixed and indexed annuities, variable annuities, universal life insurance (UL), variable universal life insurance (VUL), linked-benefit UL, term life insurance, indexed universal life insurance, and employer-sponsored retirement plans and services, as well as group life, disability, and dental products. Lincoln National Corporation also provides various plan investment vehicles, including individual and group variable annuities, group fixed annuities, and mutual fund-based programs; single and survivorship versions of UL and VUL, including corporate-owned UL and VUL, as well as bank-owned UL and VUL insurance products; and group non-medical insurance products, principally term life, universal life, disability, dental, vision, accident, and critical illness insurance to the employer market place through various forms of contributory and non-contributory plans. The company distributes its products through consultants, brokers, planners, agents, financial advisors, third-party administrators, and other intermediaries. Lincoln National Corporation was founded in 1904 and is headquartered in Radnor, Pennsylvania."},{"Symbol":"LLTC","Name":"Linear Technology Corp.","Sector":"Information Technology","Description":"Linear Technology Corporation (Linear Technology) is designing, manufacturing and marketing a range of analog integrated circuits for companies globally. The Company's products provide a bridge between its analog world and the digital electronics in communications, networking, industrial, automotive, computer, medical, instrumentation, consumer, and military and aerospace systems. Linear Technology produces power management, data conversion, signal conditioning, radio frequency (RF) and interface integrated circuits (ICs), Module subsystems, and wireless sensor network products."},{"Symbol":"LKQ","Name":"LKQ Corporation","Sector":"Consumer Discretionary","Description":"LKQ Corporation (LKQ) provides replacement parts, components and systems needed to repair vehicles (cars and trucks). The Company operates in four segments: Wholesale-North America Wholesale-Europe, Self Service and Heavy-Duty Truck. Buyers of vehicle replacement products have the option to purchase from primarily five sources: new products produced by original equipment manufacturers (OEMs), which are known as OEM products; new products produced by companies other than the OEMs, which are sometimes referred to as aftermarket products; recycled products originally produced by OEMs, which it refers to as recycled products; used products that have been refurbished; and used products that have been remanufactured. Effective August 6, 2013, LKQ Corp acquired Premier Paints Ltd, a manufacturer of motor vehicle parts, form Iris Coatings Ltd, and concurrently, LKQ acquired Bee Bee Refinishing Supplies Halstead, JCA Coatings, Milton Keynes Paint & Equipment and Sinemaster Motor Factors Ltd."},{"Symbol":"LMT","Name":"Lockheed Martin Corp.","Sector":"Industrials","Description":"Lockheed Martin Corporation is a global security and aerospace company principally engaged in the research, design, development, manufacture, integration, and sustainment of technology systems and products. The Company also provides a range of management, engineering, technical, scientific, logistic, and information services. It serves both domestic and international customers with products and services that have defense, civil, and commercial applications, with its principal customers being agencies of the United States Government. It operates in five business segments: Aeronautics, Information Systems & Global Solutions (IS&GS), Missiles and Fire Control (MFC), Mission Systems and Training (MST), and Space Systems. In July 2013, Lockheed Martin Corporation launched Lockheed Martin International. In September 2013, Lockheed Martin Corporation announced the acquisition of Amor Group."},{"Symbol":"L","Name":"Loews Corp.","Sector":"Financials","Description":"Loews Corporation, through its subsidiaries, operates as a commercial property and casualty insurance company primarily in the United States. The company offers management and professional liability insurance and risk management services, and other specialized property and casualty coverages; commercial surety and fidelity bonds; and warranty and alternative risk services primarily for vehicles and portable electronic communication devices. Its commercials property insurance products include standard and excess property, marine, and boiler and machinery coverages; and casualty insurance products comprise workers compensation, general and product liability, commercial auto, and umbrella coverages. The company also provides loss-sensitive insurance programs; and risk management, information, warranty, and claims administration services, as well as underwrites primarily short-tail exposures in marine and aviation, non-marine property, property treaty reinsurance, and specialty lines products. It markets its insurance products and services primarily through independent agents, brokers, and managing general underwriters to various customers. In addition, the company owns and operates 38 offshore drilling rigs consisting of 27 semisubmersible rigs, 6 jack-ups, and 5 drillships for companies engaged in the exploration and production of hydrocarbons. Further, it is involved in the transportation and storage of natural gas and natural gas liquids (NGLs), and gathering and processing of natural gas. It owns and operates natural gas pipelines covering approximately 14,190 miles of interconnected pipelines transporting approximately 2.5 trillion cubic feet of natural gas and approximately 34.4 million barrels of NGLs; and underground storage fields with aggregate working gas capacity of approximately 208.0 billion cubic feet of natural gas. Additionally, it operates 21 upper and upscale hotels. The company was founded in 1954 and is headquartered in New York, New York."},{"Symbol":"LOW","Name":"Lowe's Cos.","Sector":"Consumer Discretionary","Description":"Lowe's Companies, Inc. (Lowe's) is a home improvement retailer. As of February 3, 2012, Lowe's operated 1,745 stores, consisted of 1,712 stores across 50 United States, 31 stores in Canada and two stores in Mexico. Its 1,745 stores represent approximately 197 million square feet of retail selling space. The Company serves homeowners, renters and commercial business customers. Individual homeowners and renters complete an array of projects and vary along the spectrum of do-it-yourself (DIY) and do-it-for-me (DIFM). Commercial business customers include those who work in construction, repair/remodel, commercial and residential property management, or business maintenance professions. During the fiscal year ended February 3, 2012 (fiscal 2011), it opened 25 stores, which included the relocation of two existing stores. Seven of the stores were opened in Canada. In August 2013, the Company acquired the majority of assets of Orchard Supply Hardware, including 72 Orchard stores."},{"Symbol":"LYB","Name":"LyondellBasell","Sector":"Materials","Description":"LyondellBasell Industries N.V. is an independent chemical company. The Company's chemicals businesses consist of processing plants that convert volumes of liquid and gaseous hydrocarbon feedstock into plastic resins and other chemicals. Its Houston refinery processes crude oil into fuels, such as gasoline, diesel and jet fuel. Its segments include Olefins and Polyolefins-Americas (O&P-Americas), Olefins and Polyolefins-Europe, Asia, International (O&P-EAI), Intermediates and Derivatives (I&D), Refining & Oxyfuels and Technology. It's O&P-Americas and O&P-EAI products include olefins, polyolefins and polypropylene (PP) compounds. I&D products include acetyls and ethylene derivatives. Refining & Oxyfuels include gasoline and alkylate. Technology include PP process technologies land polyolefin catalysts. On January 4, 2012, refinery operations were suspended at its Berre refinery in France."},{"Symbol":"MTB","Name":"M&T Bank Corp.","Sector":"Financials","Description":"M&T Bank Corporation (M&T) is a bank holding company. As of December 31, 2012, M&T had two wholly owned bank subsidiaries: M&T Bank and Wilmington Trust, National Association (Wilmington Trust, N.A.). The banks collectively offer a range of retail and commercial banking, trust, wealth management and investment services to their customers. The Company conducts lending activities in various states through other subsidiaries. M&T Bank and certain of its subsidiaries also offer commercial mortgage loans secured by income producing properties or properties used by borrowers in a trade or business. Additional financial services are provided through other operating subsidiaries of the Company. Its segments include Business Banking, Commercial Banking, Commercial Real Estate, Discretionary Portfolio, Residential Mortgage Banking and Retail Banking."},{"Symbol":"MAC","Name":"Macerich","Sector":"Real Estate","Description":"The Macerich Company is a fully integrated self-managed and self-administered real estate investment trust (REIT). The Company focuses on the acquisition, leasing, management, development and redevelopment of regional malls throughout the United States. Macerich focuses on retail properties in California, Arizona, Chicago, Greater New York Metro and Washington, DC. As of September 30, 2013, the Company owned approximately 61 million square feet of real estate consisted of primarily of interests in 58 regional shopping centers. The Company's properties include Queens Center, Kings Plaza Shopping Center and Green Acres Mall in New York City."},{"Symbol":"M","Name":"Macy's Inc.","Sector":"Consumer Discretionary","Description":"Macy's, Inc. (Macy's) is a retail organization operating stores and Internet Websites under two brands (Macy's and Bloomingdale's) that sell a range of merchandise, including apparel and accessories (men's, women's and children's), cosmetics, home furnishings and other consumer goods in 45 states, the District of Columbia, Guam and Puerto Rico. As of January 28, 2012, the Company's operations were conducted through Macy's, macys.com, Bloomingdale's, bloomingdales.com and Bloomingdale's Outlet. During the year ended December 31, 2011, the Company opened three new Bloomingdale's Outlet stores and re-opened one Macy's store. On February 25, 2011, the Company sold its investment in The Knot, Inc."},{"Symbol":"MNK","Name":"Mallinckrodt Plc","Sector":"Health Care","Description":"Mallinckrodt public limited company develops, manufactures, markets, and distributes specialty pharmaceutical products and medical imaging agents worldwide. The company operates through two segments, Specialty Pharmaceuticals and Global Medical Imaging. The Specialty Pharmaceuticals segment provides Acthar, an injectable biopharmaceutical drug for use in various indications, including neurology, rheumatology, nephrology, and pulmonology; Ofirmev and Xartemis XR for pain indications; EXALGO extended-release tablets to treat chronic pain; and GABLOFEN injections for use in the management of spasticity of cerebral or spinal origin. It also offers generics and active pharmaceutical ingredients (API), such as hydrocodone and hydrocodone-containing tablets, oxycodone and oxycodone-containing tablets, and methylphenidate HCl extended-release tablets, as well as biologics, medicinal opioids, synthetic controlled substances, acetaminophen products, and other active ingredients. The Global Medical Imaging segment provides contrast media and delivery systems comprising ioversol and gadoversetamide injections for diagnostic imaging applications under the Optiray and Optimark brand names with pre-filled syringes and power injectors to the hospitals and imaging centers. It also offers nuclear imaging products consisting of technetium-99m generators under the Ultra-Technekow DTE brand; cold kits; and Octreoscan kits for localization of tumors. The company markets its products to physicians, pharmacists, pharmacy buyers, specialty pharmacies, radiologists, and radiology technicians, as well as distributes its products to drug wholesalers, retail pharmacy chains, hospital networks, ambulatory surgical centers, and governmental agencies; sells and distributes APIs directly or through distributors to other pharmaceutical companies; and markets and distributes nuclear imaging products to radiopharmacies and hospitals. Mallinckrodt public limited company is based in Dublin, Ireland."},{"Symbol":"MRO","Name":"Marathon Oil Corp.","Sector":"Energy","Description":"Marathon Oil Corporation (Marathon Oil) is an international energy company engaged in exploration and production, oil sands mining and integrated gas with operations in the United States, Angola, Canada, Equatorial Guinea.(E.G.), Ethiopia, Gabon, Kenya, the Kurdistan Region of Iraq, Libya, Norway, Poland and the United Kingdom. The Company operates in three business segments: Exploration and Production Segment, explores for, produces and markets liquid hydrocarbons and natural gas on a worldwide basis; Oil sands mining segment, mines, extracts and transports bitumen from oil sands deposits in Alberta, Canada, and upgrades the bitumen to produce and market synthetic crude oil and vacuum gas oil, and integrated gas, produces and markets products manufactured from natural gas, such as LNG and methanol, in E.G. During the year ended December 31, 2012 , the Company Company acquired approximately 25,000 net acres in the core of the Eagle Ford shale."},{"Symbol":"MPC","Name":"Marathon Petroleum","Sector":"Energy","Description":"Marathon Petroleum Corporation (Marathon Petroleum) is engaged in refining, transporting and marketing petroleum products in the United States. The Company's refining, marketing and transportation operations are concentrated in the Midwest, Gulf Coast and Southeast regions of the United States. These include a six-plant refining network, a terminal and transportation system, and wholesale and retail marketing operations. This includes both the Marathon Brand and Marathon Petroleum's wholly owned retail marketing subsidiary, Speedway LLC and operated retail gasoline and convenience stores. As of December 31, 2012, the Company leased or owned approximately 1,944 leased and 27 owned railcars of various sizes and capacities for movement and storage of refined products."},{"Symbol":"MAR","Name":"Marriott Int'l.","Sector":"Consumer Discretionary","Description":"Marriott International, Inc. is a diversified hospitality company. It is a lodging company with more than 3,700 properties in 73 countries and territories. It operates and franchises hotels, including Marriott, The Ritz-Carlton, JW Marriott, Bulgari, EDITION, Renaissance, Autograph Collection, AC Hotels by Marriott, Courtyard, Fairfield Inn & Suites, SpringHill Suites, Residence Inn, TownePlace Suites, ExecuStay, and Marriott Executive Apartments brand names. It operates in four segments: North American Full-Service Lodging, which includes the Marriott Hotels & Resorts; North American Limited-Service Lodging, which includes the Courtyard; International Lodging, which includes the Marriott Hotels & Resorts, and Luxury Lodging, which includes The Ritz-Carlton. In September 2012, its JW Marriott hotel brand opened JW Marriott Essex House New York. In October 2012, Gaylord Entertainment Company sold the Gaylord Hotels brand and the rights to manage its four hotels to the Company."},{"Symbol":"MMC","Name":"Marsh & McLennan","Sector":"Financials","Description":"Marsh & McLennan Companies, Inc. is a global professional services firm providing advice and solutions in the areas of risk, strategy and human capital. It is the parent company of a number of risk experts and specialty consultants, including Marsh, the insurance broker, intermediary and risk advisor; Guy Carpenter, the risk and reinsurance specialist; Mercer, the provider of human resource (HR) and related financial advice and services, and Oliver Wyman Group, the management and economic consultancy. The Company provides analysis, advice and transactional capabilities to clients in more than 100 countries. In November 2013, the Company acquired Elsey & Associates. In December 2013, the Company announced that its Marsh & McLennan Agency LLC, a subsidiary of insurance broker Marsh Inc., announced the acquisition of Cambridge Property & Casualty, one of the property/casualty insurance agencies in Michigan."},{"Symbol":"MLM","Name":"Martin Marietta Materials","Sector":"Materials","Description":"Martin Marietta Materials, Inc. is the producer of aggregates products (crushed stone, sand, and gravel) for the construction industry, including infrastructure, nonresidential, residential, railroad ballast, agricultural, and chemical grade stone used in environmental applications. The Company operates in four business segments: the Mideast Group, Southeast Group, and West Group and the Specialty Products segment. The Specialty Products segment includes the magnesia-based chemicals and dolomitic lime businesses. The Company's aggregates business also includes asphalt products, ready mixed concrete, and road paving operations. The Company also has a Specialty Products segment that manufactures and markets magnesia-based chemical products used in industrial, agricultural, and environmental applications, and dolomitic lime sold primarily to customers in the steel industry."},{"Symbol":"MAS","Name":"Masco Corp.","Sector":"Industrials","Description":"Masco Corporation manufactures, distributes and installs home improvement and building products. The Company is a manufacturer of a number of home improvements and building products, including faucets, cabinets, architectural coatings and windows, and the installer of insulation for the new home construction market. The Company provides a range of product offerings and distribute products through multiple channels, including directly to homebuilders and wholesale and retail channels. Approximately 76% of the Company's sales were generated by its North American operations during the year ended December 31, 2011."},{"Symbol":"MA","Name":"Mastercard Inc.","Sector":"Information Technology","Description":"MasterCard Incorporated (MasterCard) is a global payments and technology company that connects consumers, financial institutions, merchants, Governments and businesses worldwide, enabling them to use electronic forms of payment instead of cash and checks. The Company offers a range of payment solutions that enable its customers and partners to develop and implement credit, debit, prepaid and related payment programs for their customers. Its customers and partners include financial institutions and other entities that act as issuers and acquirers, merchants, Government entities, telecommunications companies and other businesses. MasterCard manages a family of payment brands, including MasterCard, Maestro and Cirrus, which the Company license to its customers for use in their payment programs and solutions. On April 15, 2011, MasterCard acquired the prepaid card program management operations of Travelex Holdings Ltd., since renamed Access Prepaid Worldwide."},{"Symbol":"MAT","Name":"Mattel Inc.","Sector":"Consumer Discretionary","Description":"Mattel, Inc. (Mattel) designs, manufactures and markets a variety of toy products worldwide which are sold to its customers and directly to consumers. Mattel's portfolio of brands and products are grouped in categories, such as Mattel Girls & Boys Brands, including Barbie fashion dolls and accessories (Barbie), Polly Pocket, Little Mommy, Disney Classics, and Monster High (collectively Other Girls Brands), Hot Wheels, Matchbox, and Tyco R/C vehicles and play sets, and CARS, Radica, Toy Story, Max Steel, WWE Wrestling, and Batman, and games and puzzles; Fisher-Price Brands-including Fisher-Price, Little People, BabyGear, Imaginext, and View-Master, Dora the Explorer, Go Diego Go!, Thomas and Friends, Mickey Mouse Clubhouse, Sing-a-ma-jigs, and See 'N Say, and Power Wheels, and American Girl Brands, including My American Girl, the historical collection, and Bitty Baby. American Girl Brands products are sold directly to consumers via its catalogue, Website, and retail stores."},{"Symbol":"MKC","Name":"McCormick & Co.","Sector":"Consumer Staples","Description":"McCormick & Company, Incorporated manufactures, markets and distributes spices, seasoning mixes, condiments and other flavorful products to the entire food industry, retail outlets, food manufacturers and foodservice businesses. The Company operates in two business segments: consumer and industrial. The Company's sales, distribution and production facilities are located in North America and Europe. Additional facilities are based in China, Australia, Mexico, India, Singapore, Central America, Thailand and South Africa. The Company operates in two business segments: consumer and industrial. Its products are sold directly to customers and also through brokers, wholesalers and distributors. In May 2013, the Company has completed its purchase of China's Wuhan Asia-Pacific Condiments Co."},{"Symbol":"MCD","Name":"McDonald's Corp.","Sector":"Consumer Discretionary","Description":"McDonald's Corporation franchises and operates McDonald's restaurants in the global restaurant industry. These restaurants serve menu at various price points providing value in 119 countries globally. All restaurants are operated either by the Company or by franchisees, including conventional franchisees under franchise arrangements, and developmental licensees and foreign affiliated markets under license agreements. Under the conventional franchise arrangement, franchisees provide a portion of the capital required by initially investing in the equipment, signs, seating and decor of their restaurant businesses, and by reinvesting in the business over time."},{"Symbol":"MCK","Name":"McKesson Corp.","Sector":"Health Care","Description":"McKesson Corporation delivers pharmaceuticals, medical supplies and healthcare information technologies. The Company operates in two segments: McKesson Distribution Solutions segment and McKesson Technology Solutions segment. The McKesson Distribution Solutions segment distributes drugs, medical-surgical supplies and equipment and health and beauty care products throughout North America. The McKesson Technology Solutions segment delivers enterprise clinical, patient care, financial, supply chain, strategic management software solutions, pharmacy automation for hospitals, as well as connectivity, outsourcing and other services, including remote hosting and managed services, to healthcare organizations. In February 2013, the Company completed acquisition of Pss World Medical Inc."},{"Symbol":"MJN","Name":"Mead Johnson","Sector":"Consumer Staples","Description":"Mead Johnson Nutrition Company (Mead Johnson) is a global pediatric nutrition company. The Company manufactures, distributes and sells infant formulas, children's nutrition and other nutritional products. Its Enfa family of brands, including Enfamil infant formula, is the global brand franchise in pediatric nutrition. Its product portfolio addresses a range of nutritional needs for infants, children and expectant and nursing mothers. The Company markets its portfolio of more than 70 products to mothers, health care professionals and retailers in more than 50 countries in Asia, North America, Latin America and Europe. During the year ended December 31, 2011, it operated two segments: Asia/Latin America and North America/Europe, which consisted of 66% and 34%, respectively, of its net sales. In addition to the Enfa family of brands, the Company markets several other brands on a local, regional or global basis."},{"Symbol":"MDT","Name":"Medtronic plc","Sector":"Health Care","Description":"Medtronic, Inc. (Medtronic), is engaged in medical technology. As of April, 27, 2012, the Company functions in two operating segments that manufacture and sells device-based medical therapies. The Company's operating segments include Cardiac and Vascular Group, which consists of Cardiac Rhythm Disease Management (CRDM) and CardioVascular, and Restorative Therapies Group, which consists of Spinal, Neuromodulation, Diabetes and Surgical Technologies. Medtronic serves hospitals, physicians, clinicians, and patients in more than 120 countries worldwide. The Company's primary customers include hospitals, clinics, third-party health care providers, distributors, and other institutions, including governmental health care programs and group purchasing organizations. In August 2013, Medtronic, Inc. announced the closing of the acquisition of Cardiocom. Effective September 3, 2013, Medtronic Inc acquired a 30% stake in NGC Medical SpA."},{"Symbol":"MRK","Name":"Merck & Co.","Sector":"Health Care","Description":"Merck & Co., Inc. (Merck) is a global health care company that delivers health solutions through its prescription medicines, vaccines, biologic therapies, animal health, and consumer care products, which it markets directly and through its joint ventures. The Company consists of four operating segments, which are the Pharmaceutical, Animal Health, Consumer Care and Alliances segments, and one reportable segment, which is the Pharmaceutical segment. The Pharmaceutical segment includes human health pharmaceutical and vaccine products marketed either directly by the Company or through joint ventures. Human health pharmaceutical products consist of therapeutic and preventive agents, generally sold by prescription, for the treatment of human disorders. Effective February 25, 2013, Dashtag, a unit of Merck & Co Inc's Schering Plough Corp subsidiary acquired 17.95% interest in Fulford (India) Ltd."},{"Symbol":"MET","Name":"MetLife Inc.","Sector":"Financials","Description":"MetLife, Inc. (MetLife) is a provider of insurance, annuities and employee benefit programs, serving 90 million customers in over 50 countries. Through its subsidiaries and affiliates, MetLife operates in the United States, Japan, Latin America, Asia Pacific, Europe and the Middle East. It is organized into six segments: Insurance Products, Retirement Products, Corporate Benefit Funding and Auto & Home (collectively, U.S. Business), and Japan and Other International Regions (collectively, International). U.S. Business provides insurance and financial services products, including life, dental, disability, auto and homeowner insurance. In September 2013, MetLife Inc and Thayer Lodging Group acquired the 365-room Hilton Los Cabos Beach & Golf Resort in Cabo San Lucas, Mexico in a joint venture. In October 2013, Banco Bilbao Vizcaya Argentaria SA sold its entire 64.3% interest in Administradora de Fondos de Pensiones Provida SA (AFP Provida) to Metlife Inc's subsidiaries."},{"Symbol":"MTD","Name":"Mettler Toledo","Sector":"Health Care","Description":"Mettler-Toledo International Inc. supplies precision instruments and services worldwide. The company operates in five segments: U.S. Operations, Swiss Operations, Western European Operations, Chinese Operations, and Other. It offers weighing instruments for use in laboratory, industrial, packaging, logistics, and food retailing applications; analytical instruments for use in life science; reaction engineering and real-time analytic systems used in drug and chemical compound development; automated chemistry solutions used in drug and chemical compound discovery and development; and metal detection and other end-of-line inspection systems used in production and packaging for food, pharmaceutical, and other industries. Its laboratory instruments include laboratory balances, liquid pipetting solutions, titrators, thermal analysis and analyzers systems; and other analytical instruments comprising pH meters, density and refractometry instruments, and moisture analyzers, as well as laboratory software, automated chemistry solutions, and process analytic instruments. The companys industrial solutions comprise industrial weighing instruments and terminals, automatic identification and data capture solutions, vehicle scale systems, industrial software, and product inspection systems. Its retail solutions consist of multiple weighing and food labeling solutions, such as stand-alone scales, and networked scales and software for handling fresh goods, such as meats, vegetables, fruits, and cheeses. The company serves the life science industry covering pharmaceutical and biotechnology companies, and independent research organizations; food and beverage producers; food retailers; chemical, specialty chemical, and cosmetics companies; transportation and logistics, metals, and electronics industries; and the academic community. It markets its products through its direct sales force and indirect distribution channels. The company was founded in 1991 and is based in Columbus, Ohio."},{"Symbol":"KORS","Name":"Michael Kors Holdings","Sector":"Consumer Discretionary","Description":"Michael Kors Holdings Limited is a global lifestyle brand . The Company designs, materials and craftsmanship with a jet-set aesthetic that combines stylish elegance and a sporty attitude. The Company is an American sportswear house to a global accessories, footwear and apparel company with a presence in over 85 countries. Its segments include retail, wholesale and licensing. It is focused on retail stores, department stores, specialty stores and select licensing partners. As of March 30, 2013, its retail segment included 231 North American retail stores, including concessions, and 73 international retail stores, including concessions, in Europe and Japan. As of March 30, 2013, its wholesale segment included wholesale sales through approximately 2,215 department store and specialty store doors in North America and wholesale sales through approximately 1,034 department store and specialty store doors internationally."},{"Symbol":"MCHP","Name":"Microchip Technology","Sector":"Information Technology","Description":"Microchip Technology Incorporated develops and manufactures specialized semiconductor products used by its customers for a variety of embedded control applications. The Company's portfolio consists of specialized 8-bit, 16-bit, and 32-bit PIC microcontrollers and 16-bit dsPIC digital signal controllers, which feature on-board Flash (reprogrammable) memory technology. In addition, the Company offers a broad spectrum of high-performance linear, mixed-signal, power management, thermal management, RF, safety and security, and interface devices, as well as serial EEPROMs, Serial Flash memories and Parallel Flash memories. The Company also licenses Flash-IP solutions that are incorporated in a broad range of products. In November 2013, Microchip Technology Inc announced the acquisition of EqcoLogic."},{"Symbol":"MU","Name":"Micron Technology","Sector":"Information Technology","Description":"Micron Technology, Inc., is a global manufacturer and marketer of semiconductor devices, principally NAND Flash, DRAM and NOR Flash memory, as well as other memory technologies, packaging solutions and semiconductor systems for use in computing, consumer, networking, automotive, industrial, embedded and mobile products. In addition, the Company manufactures semiconductor components for CMOS images sensors and other semiconductor products. The Company operates in four segments: NAND Solutions Group (NSG), DRAM Solutions Group (DSG), Wireless Solutions Group (WSG) and Embedded Solutions Group (ESG). The Company's product include NAND Flash Memory, Dynamic Random Access Memory (DRAM) and NOR Flash Memory. The Company's manufacturing facilities are located in the United States, China, Israel, Italy, Malaysia, Puerto Rico and Singapore."},{"Symbol":"MSFT","Name":"Microsoft Corp.","Sector":"Information Technology","Description":"Microsoft Corporation is engaged in developing, licensing and supporting a range of software products and services. The Company operates in five segments: Windows & Windows Live Division (Windows Division), Server and Tools, Online Services Division (OSD), Microsoft Business Division (MBD), and Entertainment and Devices Division (EDD). The Company's products include operating systems for personal computers (PCs), servers, phones, and other intelligent devices; server applications for distributed computing environments; productivity applications; business solution applications; desktop and server management tools; software development tools; video games, and online advertising. It also designs and sells hardware, including the Xbox 360 gaming and entertainment console. In September 2013, Ericsson completed the acquisition of Microsoft's Mediaroom business and TV solution. Effective October 23, 2013, Microsoft Corp acquired Apiphany Inc."},{"Symbol":"MAA","Name":"Mid-America Apartments","Sector":"Real Estate","Description":"Mid-America Apartment Communities, Inc. (MAA) is a self-administered and self-managed real estate investment trust (REIT). It focuses on acquiring, owning and operating apartment communities in the Sunbelt region of the United States. As of December 31, 2011, it owned 100% of 160 properties representing 46,872 apartment units. Three properties include retail components with approximately 93,000 square feet of gross leasable area. It operates in three segments: large market same store communities, secondary market same store communities and non same store communities and other. In May 2013, the Company acquired Station Square at Cosner's Corner. In October 2013, Mid-America Apartment Communities Inc merged with Colonial Properties Trust. In October 2013, Mid America Apartment Communities Inc and Colonial Properties Trust announced the completion of the merger of the two companies."},{"Symbol":"MHK","Name":"Mohawk Industries","Sector":"Consumer Discretionary","Description":"Mohawk Industries, Inc. (Mohawk) is a producer of floor covering products for residential and commercial applications in the United States and residential applications in Europe. The Company is a carpet and rug manufacturer and manufacturers, marketers and distributors of ceramic tile, natural stone and hardwood flooring in the United States, as well as a producer of laminate flooring in the United States and Europe. The Company has three reporting segments: the Mohawk segment, the Dal-Tile segment and the Unilin segment. The Company has recently expanded its international presence through investments in Australia, Brazil, China, France, Mexico and Russia. In January 2013, the Company acquired Pergo. In April 2013, it completed the acquisition of the Marazzi Group. In May 2013, the Company acquired Spano Group."},{"Symbol":"TAP","Name":"Molson Coors Brewing Company","Sector":"Consumer Staples","Description":"Molson Coors Brewing Company (MCBC) is a holding company. The Company operates in five business segments: Canada, the U.S., Central Europe, the U.K., and MCI. The Company is a brewer and has a portfolio of owned and partner brands, including signature brands Coors Light, Molson Canadian, Carling and Staropramen, as well as craft and specialty beers such as Blue Moon, Creemore Springs, Cobra and Doom Bar. Its other subsidiaries include: Molson Coors Canada (MCC), operating in Canada; MillerCoors LLC (MillerCoors), which is accounted for by the Company under the equity method of accounting, operating in the United States (U.S.); Molson Coors Brewing Company (UK) Limited (MCBC-UK, operating in the United Kingdom (U.K.) and the Republic of Ireland; Molson Coors International (MCI) operating in various other countries; and its other non-operating subsidiaries. In June 15, 2012, the Company acquired StarBev. In January 2013, it acquired Ireland's Franciscan Well brewery."},{"Symbol":"MDLZ","Name":"Mondelez International","Sector":"Consumer Staples","Description":"Mondelez International, Inc. (Mondelez International), is a snack manufacturing company. The Company manufactures and markets food and beverage products for consumers in approximately 165 countries around the world. The Company produces biscuits, chocolate, candy and powdered beverages as well as gum and coffee. The Company's portfolio includes nine brands including Oreo, Nabisco and LU biscuits; Milka, Cadbury Dairy Milk and Cadbury chocolates; Trident gum; Jacobs coffee; and Tang powdered beverage. In addition, the Company's portfolio of snack foods and refreshments includes 52 brands. The Company manages its global business and through three geographic units: Developing Markets, Europe and North America. On October 1, 2012 the Company completed the spin-off of its North American grocery business, Kraft Foods Group, Inc. (Kraft Foods Group)."},{"Symbol":"MON","Name":"Monsanto Co.","Sector":"Materials","Description":"Monsanto Company (Monsanto) along with its subsidiaries, is a provider of agricultural products for farmers. The Company's seeds, biotechnology trait products, and herbicides provide farmers with solutions that improve productivity, reduce the costs of farming, and produce better foods for consumers and better feed for animals. It manages business in two segments: Seeds and Genomics, and Agricultural Productivity. In April 2010, the Company completed the acquisition of a corn and soybean processing plant located in Paine, Chile from Anasac, a company that provides seed processing services. In November 2013, the Company announced that it has completed the acquisition of The Climate Corporation."},{"Symbol":"MNST","Name":"Monster Beverage","Sector":"Consumer Staples","Description":"Monster Beverage Corporation is a holding company. The Company develops, markets, sells and distributes alternative beverage, such as non-carbonated ready-to-drink iced teas, lemonades, juice cocktails, single-serve juices and fruit beverages, ready-to-drink dairy and coffee drinks, energy drinks, sports drinks, and single-serve still water (flavored and unflavored) with beverages, including sodas that are considered natural, sparkling juices and flavored sparkling beverages. It has two reportable segments, namely Direct Store Delivery (DSD), whose principal products comprise energy drinks, and Warehouse (Warehouse), whose principal products comprise juice-based and soda beverages. The DSD segment develops, markets and sells products primarily through an exclusive distributor network. The Warehouse segment develops, markets and sells products directly to retailers."},{"Symbol":"MCO","Name":"Moody's Corp","Sector":"Financials","Description":"Moody's Corporation (Moody's) is a provider of credit ratings; credit and economic related research, data and analytical tools; software solutions and related risk management services; quantitative credit risk measures, credit portfolio management solutions, training and financial credentialing and certification services, and outsourced research and analytical services to institutional customers. The Company operates in two segments: Moody's Investors Service (MIS) and Moody's Analytics (MA). The MIS segment consists of all credit rating activity. All of Moody's other non-rating commercial activities are included within the MA segment. As of December 31, 2012, MIS had ratings relationships with approximately 10,000 corporate issuers and approximately 22,000 public finance issuers. During the year ended December 31, 2012, the Company had rated and monitored ratings on approximately 82,000 structured finance obligations (representing approximately 13,000 transactions)."},{"Symbol":"MS","Name":"Morgan Stanley","Sector":"Financials","Description":"Morgan Stanley is a global financial services company that, through its subsidiaries and affiliates, provides its products and services to a range of clients and customers, including corporations, governments, financial institutions and individuals. The Company is a financial holding company. The Company operates in three segments: Institutional Securities, Global Wealth Management Group and Asset Management. The Company provides financial advisory and capital-raising services to a group of corporate and other institutional clients worldwide. As of December 31, 2011, the Company's Global Wealth Management Group had $1,649 billion in client assets. The Company's Asset Management business segment offers clients an array of equity, fixed income and alternative investments and merchant banking services. On February 17, 2011, the Company completed the sale of Revel Entertainment Group, LLC (Revel). Effective March 14, 2013, Morgan Stanley acquired interest in Auerbach Grayson & Co."},{"Symbol":"MSI","Name":"Motorola Solutions Inc.","Sector":"Information Technology","Description":"Motorola Solutions, Inc. (Motorola Solutions) provides mission-critical communication infrastructure, devices, software and services. The Company provides these products and services for enterprise and government customers worldwide. The Company operates in two segments: Government and Enterprise. The Government segment includes sales of public safety mission-critical communications systems, commercial two-way radio systems and devices, software and services. The Enterprise segment includes sales of rugged and enterprise-grade mobile computers and tablets, laser/imaging/RFID-based data capture products, wireless local area network (WLAN) and integrated digital enhanced network (iDEN) infrastructure, software and services. In June 2013, AirWatch, provider of Enterprise Mobility Management (EMM) solutions announced that it has acquired Motorola Solutions Inc's Mobility Services Platform business."},{"Symbol":"MUR","Name":"Murphy Oil","Sector":"Energy","Description":"Murphy Oil Corporation is a worldwide oil and gas exploration and production company with retail and wholesale gasoline marketing operations in the United States and refining and marketing operations in the United Kingdom. In August 2013, the Company announced that it has completed the spin-off of its United States retail marketing business into an independent public company called Murphy USA Inc."},{"Symbol":"MYL","Name":"Mylan N.V.","Sector":"Health Care","Description":"Mylan Inc. (Mylan) is a fully integrated global pharmaceutical company that develops, licenses, manufactures, markets and distributes generic, branded generic and specialty pharmaceuticals. Mylan has two segments:Generics and Specialty. Its generic pharmaceutical business is conducted primarily in the United States and Canada (collectively, North America); Europe, the Middle East, and Africa (collectively, EMEA); and India, Australia, Japan and New Zealand (collectively, Asia Pacific). Its API business is conducted through Mylan Laboratories Limited (Mylan India), which is included within the Asia Pacific region in its Generics Segment. The Company's pharmaceutical business is conducted by Mylan Specialty L.P. (Mylan Specialty). Mylan provides products to customers in approximately 140 countries and territories. In December 2013, In December 2013, the Company announced that it has completed the acquisition of the Agila injectables businesses from Strides Arcolab Ltd."},{"Symbol":"NDAQ","Name":"NASDAQ OMX Group","Sector":"Financials","Description":"The NASDAQ OMX Group, Inc. (NASDAQ OMX) is a holding company. It is a global exchange group that delivers trading, clearing, exchange technology, regulatory, securities listing, and public company services across six continents. Its global offerings are diverse and include trading and clearing across multiple asset classes, market data products, financial indexes, capital formation solutions, financial services and market technology products and services. The Company operates in three segments: Market Services, Issuer Services and Market Technology. In June 2013, the Company announced the completion of its acquisition of Thomson Reuters Investor Relations, Public Relations and Multimedia Solutions businesses, which provide insight, analytics and communications solutions. In July 2013, BGC Partners Inc announced that it closed the sale of its on-the-run, 2-, 3-, 5-, 7-, 10-, and 30-year fully electronic trading platform for U.S. Treasury Notes and Bonds to NASDAQ OMX Group, Inc."},{"Symbol":"NOV","Name":"National Oilwell Varco Inc.","Sector":"Energy","Description":"National Oilwell Varco, Inc. is a provider of equipment and components used in oil and gas drilling and production operations, oilfield services, and supply chain integration services to the upstream oil and gas industry. The Company operates through three segments. Its Rig Technology segment designs, manufactures, sells and services complete systems for the drilling, completion, and servicing of oil and gas wells. Its Petroleum Services & Supplies segment provides a variety of consumable goods and services used to drill, complete, remediate and workover oil and gas wells and service drill pipe, tubing, casing, flowlines and other oilfield tubular goods. Its Distribution & Transmission segment provides maintenance, repair and operating supplies and spare parts to drill site and production locations worldwide. In July 2012, its subsidiary, NOV Distribution Services ULC, acquired CE Franklin Ltd. In February 2013, National Oilwell Varco Inc acquired Robbins & Myers Inc."},{"Symbol":"NAVI","Name":"Navient","Sector":"Financials","Description":"Navient Corporation holds the portfolio of education loans insured or guaranteed under the Federal Family Education Loan Program (FFELP Loans), as well as the portfolio of private education loans (Private Education Loans). FFELP Loans are insured or guaranteed by state or not-for-profit agencies and are also protected by contractual rights to recovery from the United States pursuant to guaranty agreements among the United States Department of Education(ED), and these agencies. The Company operates in four segments: Consumer Lending, Business Services, FFELP Loans and Other. At December 31, 2013, 84% of its FFELP Loan portfolio was funded to term with non-recourse, long-term securitization debt. The Company also sold its Campus Solution business and its 529 college-savings plan administration business in 2013 in connection with better aligning its core business. The results of both of these businesses are reported in discontinued operations for all periods presented."},{"Symbol":"NTAP","Name":"NetApp","Sector":"Information Technology","Description":"NetApp, Inc. (NetApp) is a provider of storage systems and data management solutions for information technology (IT) infrastructures. Its fabric-attached storage (FAS) platform offers the storage platform for business applications, shared infrastructures and cloud environments. NetApp is an original equipment manufacturer (OEM) storage provider and enables OEMs to offer IT and storage solutions that serve specific markets and customer requirements. It offers two storage platforms: FAS storage platform and E-Series platform. Its FAS storage systems offer integrated protection and data management for virtualized, shared infrastructures and business applications. NetApp offers storage solutions that incorporate its FAS platform and the functionality of its Data ONTAP data and storage resource management software. Its E-Series systems offer OEMs a modular storage platform."},{"Symbol":"NFLX","Name":"Netflix Inc.","Sector":"Information Technology","Description":"Netflix, Inc. is an Internet television network with more than 33 million members in over 40 countries. In the United States, the Company's subscribers can receive standard definition digital versatile disc (DVDs), and their high definition successor, Blu-ray discs (collectively DVD), delivered quickly to their homes. The Company operates in three segments: Domestic streaming, International streaming and Domestic DVD. Domestic and International streaming segments derive revenues from monthly subscription services consisting solely of streaming content. The Domestic DVD segment derives revenues from monthly subscription services consisting solely of DVD-by-mail."},{"Symbol":"NWL","Name":"Newell Brands","Sector":"Consumer Discretionary","Description":"Newell Rubbermaid Inc. is a global marketer of consumer and commercial products. The Company's products are marketed under a portfolio of brands, including Rubbermaid, Levolor, Goody, Calphalon, Sharpie, Paper Mate, Parker, Waterman, Irwin, Lenox, Graco, Aprica and Dymo. The Company's segments and the brands included in each of the six business segments includes Home Solutions, which includes Rubbermaid, Calphalon, Levolor, Kirsch and Goody; Writing, which includes Sharpie, Paper Mate, Expo, Prismacolor, Parker and Waterman; Tools, which include Irwin and Lenox tools, and Dymo Industrial; Commercial Products, which include Rubbermaid Commercial Products and Rubbermaid Healthcare; Baby & Parenting, which include Graco, Aprica and Teutonia, and Specialty, which include Dymo Office and Endicia. In September 2013, Newell Rubbermaid Inc closed the sale of its Hardware business, which includes the Amerock, Ashland, Bulldog and Shur-Line brands, to Nova Capital."},{"Symbol":"NFX","Name":"Newfield Exploration Co","Sector":"Energy","Description":"Newfield Exploration Company (Newfield) is an independent energy company engaged in the exploration, development and production of crude oil, natural gas and natural gas liquids. The Company's domestic areas of operation include the Mid-Continent, the Rocky Mountains and onshore Texas. Internationally, it focuses on offshore oil developments in Malaysia and China. As of December 31, 2011, it was in the process of drilling 16 gross (9.6 net) exploitation wells and 24 gross (19.7 net) development wells domestically. As of December 31, 2011, internationally, it was drilling one gross (0.6 net) exploratory well in Malaysia. In May 2011, the Company acquired assets in the Uinta Basin of Utah."},{"Symbol":"NEM","Name":"Newmont Mining Corp. (Hldg. Co.)","Sector":"Materials","Description":"Newmont Mining Corporation is primarily a gold producer with operations and/or assets in the United States, Australia, Peru, Indonesia, Ghana, New Zealand and Mexico. As of December 31, 2012, Newmont had attributable and probable gold reserves of 99.2 million ounces and an aggregate land position of approximately 29,000 square miles (75,000 square kilometers). Newmont is also engaged in the production of copper, principally through Batu Hijau in Indonesia and Boddington in Australia. Its operating segments include North America, South America, Asia Pacific and Africa. Its North America segment consists primarily of Nevada in the United States and La Herradura in Mexico. Its South America segment consists primarily of Yanacocha and Conga in Peru. Its Asia Pacific segment consists primarily of Boddington in Australia, Batu Hijau in Indonesia and other smaller operations in Australia and New Zealand. Its Africa segment consists primarily of Ahafo and Akyem in Ghana."},{"Symbol":"NWSA","Name":"News Corp. Class A","Sector":"Consumer Discretionary","Description":"News Corporation is a diversified media and information services company. The Company operates in five segments: News and Information Services, Cable Network Programming, Digital Real Estate Services, Book Publishing, and Other. The Company's business consists of range of media, including news and information services, sports programming in Australia, digital real estate services, book publishing, and pay-television (TV) distribution in Australia, that are distributed under the brands, including The Wall Street Journal, Dow Jones, Herald Sun, The Sun, The Times, HarperCollins Publishers, FOX SPORTS Australia and realestate.com.au. The Company is a developing provider of digital education content, assessment and delivery services. The Company's business component includes News and Information Services, Cable Network Programming, Digital Real Estate Services, Book Publishing, Amplify and Foxtel."},{"Symbol":"NWS","Name":"News Corp. Class B","Sector":"Consumer Discretionary","Description":"News Corporation is a diversified media and information services company. The Company operates in five segments: News and Information Services, Cable Network Programming, Digital Real Estate Services, Book Publishing, and Other. The Company's business consists of range of media, including news and information services, sports programming in Australia, digital real estate services, book publishing, and pay-television (TV) distribution in Australia, that are distributed under the brands, including The Wall Street Journal, Dow Jones, Herald Sun, The Sun, The Times, HarperCollins Publishers, FOX SPORTS Australia and realestate.com.au. The Company is a developing provider of digital education content, assessment and delivery services. The Company's business component includes News and Information Services, Cable Network Programming, Digital Real Estate Services, Book Publishing, Amplify and Foxtel."},{"Symbol":"NEE","Name":"NextEra Energy","Sector":"Utilities","Description":"NextEra Energy, Inc. (NEE) is an electric power company. The Company has over 42,000 megawatt of generating capacity in 26 states in the United States and four provinces in Canada. It also purchases electric power for resale to its customers and provides risk management services related to power and gas consumption for a limited number of customers. It is the generator in North America of renewable energy from the wind and sun. It owns and operates approximately 17% of the installed base of United States wind power production capacity and operates approximately 14% of the installed base of United States utility-scale solar power production capacity as of December 31, 2012. It also owns and operates one of the fleets of nuclear power stations in the United States, with eight reactors at five sites located in four states, representing approximately 6% of United States nuclear power electric generating capacity as of December 31, 2012."},{"Symbol":"NLSN","Name":"Nielsen Holdings","Sector":"Industrials","Description":"Nielsen Holdings NV is a Netherlands-based company engaged in information and measurement services. The Company, together with its subsidiaries, delivers media and marketing information, and analytics on a global and local basis. It operates in two segments: Buy segment and Watch segment. The Company's Buy segment provides retail transactional measurement data, consumer behavior information and analytics primarily to businesses in the consumer packaged goods industry. Its Watch segment provides viewership data and analytics primarily to the media and advertising industries across television, online and mobile screens. Nielsen Holdings NV operates in approximately 100 countries. In June 2013, the Company sold its Exposition's business segment. In September 2013, Nielsen Holdings NV completed its acquisition of Arbitron Inc."},{"Symbol":"NKE","Name":"Nike","Sector":"Consumer Discretionary","Description":"NIKE, Inc. (NIKE) is engaged in the design, development and worldwide marketing and selling of footwear, apparel, equipment, accessories and services. NIKE is a seller of athletic footwear and athletic apparel worldwide. The Company sells its products to retail accounts, through NIKE-owned retail stores and Internet sales, and through a mix of independent distributors and licensees, in approximately 190 countries around the world. The Company focuses its product offerings in seven key categories: Running, Basketball, Football (Soccer), Men's Training, Women's Training, NIKE Sportswear (its sports-inspired products) and Action Sports. It also markets products designed for kids, as well as for other athletic and recreational uses, such as baseball, cricket, golf, lacrosse, outdoor activities, football (American), tennis, volleyball, walking and wrestling. In February 2013, it sold its Cole Haan affiliate brand to APAX Partners LLP."},{"Symbol":"NI","Name":"NiSource Inc.","Sector":"Utilities","Description":"NiSource Inc. (NiSource) is an energy holding company whose subsidiaries provide natural gas, electricity and other products and services to approximately 3.8 million customers located within a corridor that runs from the Gulf Coast through the Midwest to New England. NiSource operates in three business segments: Gas Distribution Operations; Gas Transmission and Storage Operations, and Electric Operations. NiSource's principal subsidiaries include Columbia Energy Group (Columbia), a vertically-integrated natural gas distribution, transmission and storage holding company whose subsidiaries provide service to customers in the Midwest, the Mid-Atlantic and the Northeast; Northern Indiana Public Service Company (Northern Indiana), a vertically-integrated gas and electric company providing service to customers in northern Indiana, and Bay State Gas Company (Columbia of Massachusetts), a natural gas distribution company serving customers in Massachusetts."},{"Symbol":"NBL","Name":"Noble Energy Inc","Sector":"Energy","Description":"Noble Energy, Inc. (Noble Energy) is an independent energy company engaged in worldwide oil and gas exploration and production. Noble Energy has operations in five core areas: the DJ Basin (onshore United States), the Marcellus Shale (onshore United States), the deepwater Gulf of Mexico (offshore United States), offshore West Africa and offshore Eastern Mediterranean. The areas provide most of its crude oil and natural gas production; visible growth from development projects, and numerous exploration opportunities. In August 2012, it sold the Dumbarton and Lochranza properties in the North Sea to Maersk Oil North Sea Limited. In September 2012, it sold certain oil and natural gas properties in the Permian Basin to Sheridan Holding Company II, LLC. In September 2012, Unit Corporation's wholly owned subsidiary, Unit Petroleum Company, acquired certain oil and natural gas assets from the Company."},{"Symbol":"JWN","Name":"Nordstrom","Sector":"Consumer Discretionary","Description":"Nordstrom, Inc. is a fashion specialty retailer, with 225 the United States stores located in 30 states as of March 16, 2012. The Company operates in two segments: Retail and Credit. As of March 16, 2012, the Retail segment includes its 116 Nordstrom branded full-line stores and its online store at www.nordstrom.com, its 105 off-price Nordstrom Rack stores and its other retail channels, including its online private sale subsidiary HauteLook, its two Jeffrey boutiques, one philanthropic treasure&bond store and one clearance store that operates under the name Last Chance. The Company's Credit segment includes its wholly owned federal savings bank, Nordstrom fsb, through which it provides a private label credit card, two Nordstrom VISA credit cards and a debit card."},{"Symbol":"NSC","Name":"Norfolk Southern Corp.","Sector":"Industrials","Description":"Norfolk Southern Corporation (Norfolk Southern) is a Virginia based company that controls a railroad, Norfolk Southern Railway Company. Norfolk Southern Railway Company is primarily engaged in the rail transportation of raw materials, intermediate products, and finished goods primarily in the Southeast, East, and Midwest and, via interchange with rail carriers, to and from the rest of the United States. Norfolk Southern also transports overseas freight through several Atlantic and Gulf Coast ports. It provides logistics services and offers intermodal network in the eastern half of the United States."},{"Symbol":"NTRS","Name":"Northern Trust Corp.","Sector":"Financials","Description":"Northern Trust Corporation (Corporation) is a financial holding company that is a provider of asset servicing, fund administration, asset management, fiduciary and banking solutions for corporations, institutions, families and individuals worldwide. The Company conducts business through various United States and non-United States subsidiaries, including The Northern Trust Company (the Bank). The Company has a network of offices in 18 United States, Washington, District of Columbia, and 16 international locations in North America, Europe, the Middle East, and the Asia Pacific region. Northern Trust organizes its services globally around its two client-focused principal business units: Corporate and Institutional Services and Personal Financial Services (PFS). Two other business units provide services to the two principal business units: Northern Trust Global Investments, which provides investment management, and Operations and Technology, which provides operating and systems support."},{"Symbol":"NOC","Name":"Northrop Grumman Corp.","Sector":"Industrials","Description":"Northrop Grumman Corporation (Northrop Grumman) provides products, services, and integrated solutions in aerospace, electronics, information and services to its global customers. As of December 31, 2011, the Company operated in four segments: Aerospace Systems, Electronic Systems, Information Systems and Technical Services. The Company conducts most of its business with the United States Government, principally the Department of Defense (DoD) and intelligence community. It also conducts business with local, state, and foreign Governments and domestic and international commercial customers. Effective as of March 31, 2011, the company completed the spin-off of Huntington Ingalls Industries, Inc. (HII). HII operates the Company's former shipbuilding business. In September 2012, it acquired M5 Network Security Pty Ltd."},{"Symbol":"NRG","Name":"NRG Energy","Sector":"Utilities","Description":"NRG Energy, Inc. (NRG) is an integrated wholesale power generation and retail electricity company in the United States. NRG is a wholesale power generator engaged in the ownership and operation of power generation facilities; the trading of energy, capacity and related products; and the transacting in and trading of fuel and transportation services. NRG is engaged in the supply of energy, services, and sustainable products to retail customers in competitive markets through multiple channels and brands like Reliant Energy, Green Mountain Energy, and Energy Plus. Finally, NRG is focused on the deployment and commercialization of potentially disruptive technologies, like electric vehicles, Distributed Solar and smart meter technology, which have the potential to change the nature of the power supply industry. In August 2013, NRG acquired the Gregory cogeneration plant in Corpus Christi, Texas. Effective August 22, 2013, NRG Energy Inc acquired Energy Curtailment Specialists Inc."},{"Symbol":"NUE","Name":"Nucor Corp.","Sector":"Materials","Description":"Nucor Corporation and its affiliates (Nucor) manufactures steel and steel products. The Company also produces direct reduced iron (DRI) for use in the Company's steel mills. The Company's operations include several international trading companies that buy and sell steel and steel products manufactured by the Company and others. In 2012, it recycled approximately 19.2 million tons of scrap steel. In May 2012, ArcelorMittal announced the sale of its steel foundation distribution business in NAFTA, namely Skyline Steel and Astralloy (Skyline Steel) to Nucor. The transaction includes 100% of ArcelorMittal's interest in Skyline Steel's operations in the NAFTA countries and the Caribbean.In June 2012, the Company acquired Skyline Steel LLC and its subsidiaries. In August 2012, the Company completed the sale of the assets of their Nucor Wire Products Pennsylvania facility located in New Salem, Pennsylvania, to an affiliate of Wire Mesh Corporation."},{"Symbol":"NVDA","Name":"Nvidia Corporation","Sector":"Information Technology","Description":"NVIDIA Corporation (NVIDIA) is engaged in creating the graphics chips used in personal computers (PCs). The Company operates in three segments: graphics processing unit (GPU) Business, professional solutions business (PSB) and consumer products business (CPB). Its mobile processors are used in cell phones, tablets and auto infotainment systems. Designers use GPUs to create visual effects in movies and create everything from golf clubs to jumbo jets. NVIDIA solutions are based on two technologies: the GPU and the mobile processor. GPUs are the engines of visual computing, the science and art of using computers to understand, create and enhance images. It has three GPU product brands: GeForce, Quadro and Tesla. During the fiscal year ended January 29, 2012 (fiscal 2012), it acquired Icera Inc. In fiscal 2012, it launched Project Maximus. Effective July 29, 2013, NVIDIA Corp acquired Portland Group Inc, a wholesaler of parallel computing software, from STMicroelectronics NV."},{"Symbol":"ORLY","Name":"O'Reilly Automotive","Sector":"Consumer Discretionary","Description":"O'Reilly Automotive, Inc. (O'Reilly) is a specialty retailer of automotive aftermarket parts, tools, supplies, equipment and accessories in the United States, selling its products to both do-it-yourself (DIY) customers and professional service providers. As of December 31, 2012, O'Reilly operated 3,976 stores in 42 states. The Company's stores product line includes products, such as new and remanufactured automotive hard parts, such as alternators, starters, fuel pumps, water pumps, brake system components, batteries, belts, hoses, temperature control, chassis parts and engine parts; maintenance items, such as oil, antifreeze, fluids, filters, wiper blades, lighting, engine additives and appearance products, and accessories, such as floor mats, seat covers and truck accessories. Its stores offer a number of services and programs to its customers, such as used oil and battery recycling; battery diagnostic testing; professional paint shop mixing and related materials, and machine shops."},{"Symbol":"OXY","Name":"Occidental Petroleum","Sector":"Energy","Description":"Occidental Petroleum Corporation (Occidental) conducts its operations through various subsidiaries and affiliates. The Company operates in three segments: oil and gas segment; chemical segment, and midstream, marketing and other segment. The oil and gas segment explores for, develops and produces oil and condensate, natural gas liquids (NGLs) and natural gas. The chemical segment (OxyChem) mainly manufactures and markets basic chemicals and vinyls. The midstream, marketing and other segment (midstream and marketing) gathers, processes, transports, stores, purchases and markets oil, condensate, NGLs, natural gas, carbon dioxide (CO2) and power. It also trades around its assets, including transportation and storage capacity, and trades oil, NGLs, gas and other commodities. The segment also invests in entities that conduct similar activities. On December 31, 2012, it acquired Eagle Ford Shale."},{"Symbol":"OMC","Name":"Omnicom Group","Sector":"Consumer Discretionary","Description":"Omnicom Group Inc. (Omnicom) is a holding company, providing professional services to clients through multiple agencies. Omnicom is a global advertising, marketing and corporate communications company. Its agencies operate in markets worldwide and provide a range of services, which the Company group into four disciplines: advertising, customer relationship management (CRM), public relations and specialty communications. The services provided by the Company include advertising, brand consultancy, corporate social responsibility consulting, data analytics, entertainment marketing, healthcare communications and promotional marketing. In June 2013, Ketchum, a division of Omnicom Group Inc, announced its acquisition of Brandzeichen. In November 2013, the Company announced its acquisition of ICON International Communications, Singapore's independently owned public relations consultancy."},{"Symbol":"OKE","Name":"ONEOK","Sector":"Energy","Description":"ONEOK, Inc., is a diversified energy company. The Company's segments include ONEOK Partners, Natural Gas Distribution and Energy Services. As of December 31, 2012, the Company was the sole general partner and own 43.4 % of ONEOK Partners, L.P. ONEOK Partners is engaged in the gathering, processing, storage and transportation of natural gas in the United States. In addition, ONEOK Partners owns natural gas liquids systems, connecting natural gas liquids (NGL) supply in the Mid-Continent and Rocky Mountain regions with key market centers. The Company's distribution markets include Oklahoma City and Tulsa, Oklahoma; Kansas City, Wichita, and Topeka, Kansas, and Austin and El Paso, Texas. Its energy services operation is engaged in providing natural gas marketing services to its customers across the United States."},{"Symbol":"ORCL","Name":"Oracle Corp.","Sector":"Information Technology","Description":"Oracle Corporation is a provider of enterprise software and computer hardware products and services. The Company's software, hardware systems, and services businesses develops, manufactures, markets, hosts and supports database and middleware software, applications software, and hardware systems, with the latter consisting primarily of computer server and storage products. It is organized into three businesses: software, hardware systems and services. Its software business consists of two segments: new software licenses and software license updates and product support. Its hardware systems business consists of two segments: hardware systems products and hardware systems support. The Company's services business consists of the remainder of its segments and offers consulting services, managed cloud services, and education services. On April 5, 2012, the Company acquired Taleo Corporation (Taleo). Effective March 13, 2013, it acquired Nimbula Inc."},{"Symbol":"PCAR","Name":"PACCAR Inc.","Sector":"Industrials","Description":"PACCAR Inc (PACCAR) is engaged in the design, manufacture and customer support of light, medium- and heavy-duty trucks under the Kenworth, Peterbilt and DAF nameplates. The company also provides customized financial services, information technology and truck parts related to its principal business. The Company operates in three segments: design, manufacture and distribution of light-, medium- and heavy-duty trucks, distribution of parts for the truck aftermarket and related commercial vehicles, and finance and leasing products and services provided to customers and dealers. The Company's finance and leasing activities are principally related to Company products and associated equipment. The Company's other business is the manufacturing and marketing of industrial winches."},{"Symbol":"PH","Name":"Parker-Hannifin","Sector":"Industrials","Description":"Parker-Hannifin Corporation (Parker) is a full-line diversified manufacturer of motion and control technologies and systems, including fluid power systems, electromechanical controls and related components. The Company also is a worldwide producer of fluid purification, fluid and fuel control, process instrumentation, air conditioning, refrigeration, electromagnetic shielding and thermal management products and systems. The Company's motion and control technologies and systems are used in the products of its three business segments: Industrial, Aerospace, and Climate & Industrial Controls. In July 2012, the Company acquired Olaer Group. In July 2012, the Company acquired the filtration business of John Fowler (India) Private Limited. In July 2012, the Company acquired the hydraulics manufacturing division of PIX Transmissions Ltd. In July 2012, it acquired Kittiwake Developments Limited. In November 2012, The Sterling Group sold Velcon Filters, LLC to the Company."},{"Symbol":"PDCO","Name":"Patterson Companies","Sector":"Health Care","Description":"Patterson Companies, Inc. (Patterson) is a distributor serving three markets: the United States companion animal (dogs, cats and other common household pets) and equine veterinary supply, and the worldwide rehabilitation and assistive products supply market. The Company operates in three segments: dental supply, veterinary supply and rehabilitation supply. As Patterson's business, Patterson Dental is a distributor of dental products in North America. Webster Veterinary (Webster) is a distributor of veterinary supplies primarily to companion-pet (dogs, cats and other common household pets) and equine veterinary clinics across the United States. Effective August 2, 2013, it acquired Mercer Mastery, a developer of dental practice software, from Lovell Minnick Partners LLC's Mercer Advisors Inc subsidiary. In August 2013, Patterson Companies Inc completed acquisition of the stock of National Veterinary Services Limited, a wholly owned subsidiary of Dechra Pharmaceuticals PLC."},{"Symbol":"PAYX","Name":"Paychex Inc.","Sector":"Information Technology","Description":"Paychex, Inc. is a provider of payroll, human resource, and benefits outsourcing solutions for small to medium-sized businesses. As of May 31, 2012, the Company serviced approximately 567,000 clients, including approximately 2,000 clients through four offices in Germany. The Company offers a portfolio of services and products that allows its clients to meet their diverse payroll and human resource needs. These include payroll processing; payroll tax administration services; employee payment services; regulatory compliance services (new-hire reporting and garnishment processing); Paychex HR Solutions; retirement services administration; insurance services; eServices, and other human resource services and products. In January 2012, the Company acquired Icon Time Systems, Inc. In December 2012, the Company acquired ExpenseWire from Rearden Commerce Inc. In June 2013, Paychex Inc announced that it has acquired HR Services, Inc. and myStaffingPro."},{"Symbol":"PYPL","Name":"PayPal","Sector":"Information Technology","Description":"PayPal Holdings, Inc. operates as a technology platform company that enables digital and mobile payments on behalf of consumers and merchants worldwide. It enables businesses of various sizes to accept payments from merchant Websites, mobile devices, and applications, as well as at offline retail locations through a range of payment solutions across companys payments platform, including PayPal, PayPal Credit, Venmo, and Braintree products. The companys platform allows customers to pay and get paid, withdraw funds to their bank accounts, and hold balances in their PayPal accounts in various currencies. The company was founded in 1998 and is based in San Jose, California. PayPal Holdings, Inc. operates independently of eBay Inc. as of July 17, 2015."},{"Symbol":"PNR","Name":"Pentair Ltd.","Sector":"Industrials","Description":"No Description Available"},{"Symbol":"PBCT","Name":"People's United Financial","Sector":"Financials","Description":"People's United Financial, Inc. (People's United Financial) is a savings and loan holding company. The principal business of People's United Financial is to provide, through People's United Bank and its subsidiaries, commercial banking, retail and business banking, and wealth management services to individual, corporate and municipal customers. Traditional banking activities are conducted primarily within New England and southeastern New York, and include extending secured and unsecured commercial and consumer loans, originating mortgage loans secured by residential and commercial properties, and accepting consumer, commercial and municipal deposits. In addition to traditional banking activities, People's United Bank provides specialized financial services tailored to specific markets including: personal, institutional and employee benefit trust; cash management; and municipal banking and finance."},{"Symbol":"PEP","Name":"PepsiCo Inc.","Sector":"Consumer Staples","Description":"PepsiCo, Inc. (PepsiCo) is a global food and beverage company. Through the Company's bottlers, contract manufacturers and other partners, it makes, markets, sells and distributes a range of foods and beverages in more than 200 countries and territories. PepsiCo is organized into four business units: PepsiCo Americas Foods (PAF), which includes Frito-Lay North America (FLNA), Quaker Foods North America (QFNA) and all of its Latin American food and snack businesses (LAF); PepsiCo Americas Beverages (PAB), which includes all of its North American and Latin American beverage businesses; PepsiCo Europe, which includes all beverage, food and snack businesses in Europe and South Africa, and PepsiCo Asia, Middle East and Africa (AMEA), which includes all beverage, food and snack businesses in AMEA, excluding South Africa. It manufactures markets and sells a range of salty, sweet and grain-based snacks, carbonated and non-carbonated beverages, dairy products and other foods."},{"Symbol":"PKI","Name":"PerkinElmer","Sector":"Health Care","Description":"PerkinElmer, Inc. is a provider of technology, services and solutions to the diagnostics, research, environmental, industrial and laboratory services markets. Through the Company's advanced technologies, solutions, and services, it addresses issues related to health and safety of people and their environment. It operates in two segments: Human Health and Environmental Health. In February 2011, it acquired chemagen Biopolymer-Technologie AG (chemagen). In March 2011, it acquired ArtusLabs, Inc. (ArtusLabs). In March 2011, it acquired specified assets and assumed specified liabilities of ID Biological Systems, Inc. (IDB). In April 2011, it acquired CambridgeSoft Corporation (CambridgeSoft). In May 2011, it acquired Geospiza, Inc. (Geospiza). In May 2011, it acquired Labtronics, Inc. (Labtronics). In June 2011, it acquired Dexela Limited (Dexela). In November 2011, it acquired Caliper Life Sciences, Inc. (Caliper)."},{"Symbol":"PRGO","Name":"Perrigo","Sector":"Health Care","Description":"Perrigo Company is a global healthcare supplier that develops, manufactures and distributes over-the-counter (OTC) and generic prescription (Rx) pharmaceuticals, infant formulas, nutritional products and active pharmaceutical ingredients (API). The Company is the store brand manufacturer of OTC pharmaceutical products and infant formulas. The Company has four segments, aligned primarily by type of product: Consumer Healthcare, Nutritionals, Rx Pharmaceuticals and API. In addition, the Company has an Other category that consists of the Israel Pharmaceutical and Diagnostic Products operating segment. Its primary markets and locations of manufacturing and logistics operations are the United States, Israel, Mexico, the United Kingdom and Australia. In April 2013, it closed the acquisition of Velcera, Inc. In June 2013, Perrigo Company announced that it has completed the acquisition of an ophthalmic sterile ointment and solution product portfolio from Fera Pharmaceuticals, LLC."},{"Symbol":"PFE","Name":"Pfizer Inc.","Sector":"Health Care","Description":"Pfizer Inc. (Pfizer) is a research-based, global biopharmaceutical company. The Company manages its operations through five segments: Primary Care; Specialty Care and Oncology; Established Products and Emerging Markets; Animal Health, and Consumer Healthcare. The Company's diversified global healthcare portfolio includes human and animal biologic and small molecule medicines and vaccines, as well as nutritional products and consumer healthcare products. Its Animal Health business unit discovers, develops and sells products for the prevention and treatment of diseases in livestock and companion animals. Primary Care operating segment includes revenues from human prescription pharmaceutical products primarily prescribed by primary-care physicians. In November 2012, the Company acquired NextWave Pharmaceuticals, Inc. On November 30, 2012, the Company completed the sale of its Nutrition business."},{"Symbol":"PCG","Name":"PG&E Corp.","Sector":"Utilities","Description":"PG&E Corporation is a holding company that conducts its business through Pacific Gas and Electric Company (Utility). The Utility's revenues are generated mainly through the sale and delivery of electricity and natural gas to customers. The Utility served approximately 5.2 million electricity distribution customers and approximately 4.4 million natural gas distribution customers at December 31, 2012. The Utility's rates for electricity and natural gas utility services are based on its costs of providing service (cost-of-service ratemaking). The Utility is regulated primarily by the California Public Utilities Commission (CPUC) and the Federal Energy Regulatory Commission (FERC). In addition, the Nuclear Regulatory Commission (NRC) oversees the licensing, construction, operation, and decommissioning of the Utility's nuclear generation facilities. During the year ended December 31, 2012, the Company delivered 76,205 gigawatts per hour of Actual Electricity."},{"Symbol":"PM","Name":"Philip Morris International","Sector":"Consumer Staples","Description":"Philip Morris International Inc. (PMI) is a holding company. PMI's subsidiaries and affiliates and their licensees are engaged in the manufacture and sale of cigarettes and other tobacco products in markets outside of the United States of America. Its products are sold in approximately 180 countries. The Company divides its markets into four geographic segments: The European Union (EU) Region , The Eastern Europe, Middle East & Africa (EEMA) Region , The Asia Region and The Latin America & Canada Region. In June 2011, it completed the acquisition of a cigarette business in Jordan, consisting primarily of cigarette manufacturing assets and inventories. January 1, 2011, it established a business structure with Vietnam National Tobacco Corporation (Vinataba) in Vietnam, further developing its joint venture with Vinataba through the licensing of Marlboro and establishing a PMI-controlled branch for the building of its brands."},{"Symbol":"PSX","Name":"Phillips 66","Sector":"Energy","Description":"Phillips 66 is a holding company. The Company is engaged in producing natural gas liquids (NGL) and petrochemicals. The Company operates in three segments: the Refining and Marketing (R&M) segment, the Midstream segment and the Chemicals segment. The Refining and Marketing (R&M) segment purchases, refines, markets and transports crude oil and petroleum products, mainly in the United States, Europe and Asia, and also engages in power generation activities. The Midstream segment gathers, processes, transports and markets natural gas, and fractionates and markets NGL, predominantly in the United States. The Chemicals segment manufactures and markets petrochemicals and plastics on a worldwide basis. The Company's operations encompass 15 refineries with a gross crude oil capacity of 2.8 million barrels per day, 10,000 branded marketing outlets and 7.2 billion cubic feet per day of gross natural gas processing capacity."},{"Symbol":"PNW","Name":"Pinnacle West Capital","Sector":"Utilities","Description":"Pinnacle West Capital Corporation (Pinnacle West) is a holding company that conducts business through its subsidiaries. Pinnacle West derives its revenues and earnings from its wholly owned subsidiary, Arizona Public Service Company (APS). APS is a vertically-integrated electric utility that provides either retail or wholesale electric service to most of the State of Arizona. APS provides electric service to approximately 1.1 million customers. The Company owns or leases approximately 6,340 megawatts of regulated generation capacity and it holds a mix of both long-term and short-term purchased power agreements for additional capacity, including a variety of agreements for the purchase of renewable energy. The Company's subsidiaries include El Dorado Investment Company (El Dorado) and SunCor Development Company (SunCor). On August 19, 2011, the Company sold APS Energy Services Company, Inc., a subsidiary of the Company."},{"Symbol":"PXD","Name":"Pioneer Natural Resources","Sector":"Energy","Description":"Pioneer Natural Resources Company (Pioneer) is an independent oil and gas exploration and production company with operations in the United States and South Africa. Pioneer is a holding company whose assets consist of direct and indirect ownership interests in, and whose business is conducted substantially through, its subsidiaries. It sells homogenous oil, natural gas liquid (NGL) and gas units. The Company provides administrative, financial, legal and management support to United States and South Africa subsidiaries that explore for, develop and produce proved reserves. The Company's continuing operations are principally located in the United States in the states of Texas, Kansas, Colorado and Alaska. In April 2012, it acquired Carmeuse Industrial Sands (CIS). In August 2012, the Company sold its South Africa business to The Petroleum Oil and Gas Corporation of South Africa (SOC) Ltd. (PetroSA)."},{"Symbol":"PNC","Name":"PNC Financial Services","Sector":"Financials","Description":"The PNC Financial Services Group, Inc. (PNC) is a financial service company. The Company has businesses engaged in retail banking, corporate and institutional banking, asset management, and residential mortgage banking, providing its products and services nationally and others in its markets located in Pennsylvania, Ohio, New Jersey, Michigan, Illinois, Maryland, Indiana, Kentucky, Florida, Washington, D.C., Delaware, Virginia, Missouri, Wisconsin and Georgia. It also provides certain products and services internationally. As of December 31, 2011, its corporate legal structure consisted of one domestic subsidiary bank, including its subsidiaries, and approximately 141 active non-bank subsidiaries. On March 2, 2012, it acquired RBC Bank (USA). Effective October 26, 2012, PNC divested certain deposits and assets of the Smartstreet business unit, which was acquired by PNC as part of the RBC Bank (USA) acquisition, to Union Bank, N.A."},{"Symbol":"RL","Name":"Polo Ralph Lauren Corp.","Sector":"Consumer Discretionary","Description":"Ralph Lauren Corporation is engaged in the design, marketing and distribution of products, including men's, women's and children's apparel, accessories (including footwear), fragrances and home furnishings. The Company operates in three segments: Wholesale, Retail and Licensing. Its apparel products include a range of men's, women's and children's clothing. Accessories include a range of footwear, eyewear, watches, jewelry, hats, belts and leather goods, including handbags and luggage. The Company's coordinated home products include bedding and bath products, furniture, fabric and wallpaper, paint, tabletop and giftware. Fragrance products are sold under its Big Pony, Romance, Polo, Lauren, Safari, Ralph and Black Label brands, among others."},{"Symbol":"PPG","Name":"PPG Industries","Sector":"Materials","Description":"PPG Industries, Inc. (PPG) is a global supplier of protective and decorative coatings. PPG operates in six business segments. The Performance Coatings, Industrial Coatings and Architectural Coatings-EMEA segments supply protective and decorative finishes for customers in a range of end use markets, including industrial equipment, appliances and packaging; factory-finished aluminum extrusions and steel and aluminum coils; marine and aircraft equipment; automotive original equipment; and other industrial and consumer products. The Optical and Specialty Materials segment consist of the optical products and silicas businesses. It is a producer and supplier of basic chemicals. Glass segment produces flat glass and continuous-strand fiber glass. The Glass business segment consists of the flat glass and fiber glass businesses. In January 2013, the combined company formed by uniting Georgia Gulf with PPG's former commodity chemicals business is named Axiall Corporation."},{"Symbol":"PPL","Name":"PPL Corp.","Sector":"Utilities","Description":"PPL Corporation (PPL) is an energy and utility holding company. The Company operates in four segments: Kentucky Regulated, U.K. Regulated, Pennsylvania Regulated and Supply. Through its subsidiaries, PPL generates electricity from power plants in the northeastern, northwestern and southeastern United States; markets wholesale or retail energy primarily in the northeastern and northwestern portions of the United States; delivers electricity to customers in Pennsylvania, Kentucky, Virginia, Tennessee and the United Kingdom, and natural gas to customers in Kentucky. As of December 31, 2012, the Company's subsidiaries were PPL Energy Supply, LLC (PPL Energy Supply), PPL Electric Utilities Corporation (PPL Electric), LG&E and KU Energy LLC (LKE), PPL Global, LLC (PPL Global), PPL EnergyPlus LLC (PPL EnergyPlus), PPL Generation LLC (PPL Generation), Louisville Gas and Electric Company (LG&E) and Kentucky Utilities Company (KU)."},{"Symbol":"PX","Name":"Praxair Inc.","Sector":"Materials","Description":"Praxair, Inc. (Praxair) is an industrial gases supplier. The Company's products are oxygen, hydrogen, nitrogen, argon, carbon dioxide, helium, electronic gases and a range of specialty gases. Praxair Surface Technologies supplies coatings, which protect metal parts from wear, corrosion and heat. Praxair's industrial gas operations are managed on a geographical basis. During the year ended December 31, 2012, 94% of sales were generated in four geographic segments (North America, Europe, South America, and Asia). In November 2012, the Company had acquired Harlingen. In March 2013, Praxair Inc completed the acquisition of NuCO2 Inc. In April 2013, it announced the acquisition of Tri-State Carbonic's carbon dioxide (CO2) plant in Lawrenceburg, Indiana. In May 2013, the Company's subsidiary acquired Aberdeen, Scotland-based Dominion Technology Gases Investment Limited. In June 2013, Praxair Inc announced that it has acquired Volgograd Oxygen Factory (VOF)."},{"Symbol":"PCLN","Name":"Priceline.com Inc","Sector":"Consumer Discretionary","Description":"Priceline Com Incorporated, is an online travel company that offers its customers hotel room reservations at over 295,000 hotels worldwide through the Booking.com, priceline.com and Agoda brands. In the United States, it also offers its customers reservations for car rentals, airline tickets, vacation packages, destination services and cruises through the priceline.com brand. It offers car rental reservations worldwide through rentalcars.com. As of December 31, 2012, its international business (the majority of which is generated by Booking.com) represented approximately 82% of its gross bookings, and approximately 92% of its consolidated operating income. In 2012, the Company launched Express Deals, a merchant semi-opaque price-disclosed hotel reservation service at priceline.com, which allows customers to see the price of the reservation prior to purchase but not the identity of the hotel. On May 2013, the Company acquired Kayak Software Corp."},{"Symbol":"PFG","Name":"Principal Financial Group","Sector":"Financials","Description":"Principal Financial Group, Inc. (PFG) is a provider of retirement savings, investment and insurance products and services. As of December 31, 2011, the Company had approximately 18 million customers. PFG's United States and international operations concentrate primarily on asset accumulation and asset management. PFG offers a range of individual and group life insurance, individual and group disability insurance, and group dental and vision insurance. It focuses on small and medium-sized businesses providing an array of retirement and employee benefit solutions. The Company operates in four segments: retirement and investor services, principal global investors, principal international, and United States insurance solutions. PFG's corporate segment consists of the assets and activities that have not been allocated to any other segment. As of December 31, 2011, it owned 26 properties in Des Moines, Iowa, and in various other locations. In February 2013, it acquired AFP Cuprum S.A."},{"Symbol":"PG","Name":"Procter & Gamble","Sector":"Consumer Staples","Description":"The Procter & Gamble Company (P&G) is focused on providing consumer packaged goods. The Company's products are sold in more than 180 countries and territories worldwide primarily through mass merchandisers, grocery stores, membership club stores, drug stores, department stores, salons, e-commerce and high-frequency stores, and the neighborhood stores, which serve many consumers in developing markets. As of June 30, 2013, the Company had five reportable segments: Beauty; Grooming; Health Care; Fabric Care and Home Care, and Baby Care and Family Care. In beauty care, the Company offers a variety of products, ranging from deodorants to cosmetics to skin care, such as its Olay brand, which is the facial skin care brand in the world. The Company's grooming products include Shave Care (Blades and Razors, Pre- and Post-Shave Products); Braun and Appliances. The Baby Care and Family Care products include baby Wipes, diapers and pants, paper towels, tissues and toilet paper."},{"Symbol":"PGR","Name":"Progressive Corp.","Sector":"Financials","Description":"The Progressive Corporation is an insurance holding company. The Company's insurance subsidiaries and mutual insurance company affiliate provide personal and commercial automobile insurance and other specialty property-casualty insurance and related services. Its personal lines business includes insurance for personal autos and special lines products (such as motorcycles, all terrain vehicles (ATVs), recreational vehicles (RVs), mobile homes, watercraft and snowmobiles). Its Commercial Auto business offers products in 49 states. Its Claims business area is organized into four groups. Three of the groups are based on geographic region, and one is a nationwide group that provides various claims-related services, including catastrophe response and special investigations. Its non-insurance subsidiaries and limited partnership investment affiliate generally support its insurance and investment operations."},{"Symbol":"PLD","Name":"Prologis","Sector":"Real Estate","Description":"Prologis, Inc. (Prologis) is a real estate investment trust (REIT) and the general partner of the Prologis, L.P. As of December 31, 2012, Prologis, Inc. owned an approximate 99.59% common general partnership interest in the Prologis, L.P. and 100% of the preferred units in the Prologis, L.P. It operates the REIT and the Operating Partnership as one enterprise. As of December 31, 2012, it owned and managed properties and development projects totaling 554 million square feet (51.5 million square meters) in 21 countries. countries. These properties are leased to approximately 4,500 customers, including third-party logistics providers, manufacturers, retailers, transportation companies and other enterprises. The Company operates in two segments: segments: real estate operations and private capital. In May 2013, Prologis Inc acquired DocData distribution centre."},{"Symbol":"PRU","Name":"Prudential Financial","Sector":"Financials","Description":"Prudential Financial, Inc. (Prudential Financial) is a financial services company. Prudential Financial has operations in the United States, Asia, Europe and Latin America. Through its subsidiaries and affiliates, the Company offers an array of financial products and services, including life insurance, annuities, retirement-related services, mutual funds and investment management. Prudential Financial has two businesses: the Financial Services Businesses and the Closed Block Business. The Financial Services Businesses consists of its United States Retirement Solutions and Investment Management division, United States Individual Life and Group Insurance division, and International Insurance division, as well as its Corporate and Other operations. The Closed Block Business consists of the assets and related liabilities of the Closed Block described below and certain related assets and liabilities. On January 1, 2012, it merged with Gibraltar Life Insurance Company, Ltd (Gibraltar Life)."},{"Symbol":"PEG","Name":"Public Serv. Enterprise Inc.","Sector":"Utilities","Description":"Public Service Enterprise Group Incorporated, through its subsidiaries, operates as an energy company primarily in the northeastern and Mid Atlantic United States. The company operates nuclear, coal, gas, oil-fired, and renewable generation facilities with a generation capacity of approximately 13,146 megawatts. It sells electricity, natural gas, emissions credits, and a series of energy-related products that are used to optimize the operation of the energy grid. The company also transmits electricity; and distributes electricity and gas to residential, commercial, and industrial customers, as well as invests in solar generation projects, and implements energy efficiency and demand response programs. In addition, it offers appliance services and repairs to customers. As of December 31, 2014, it owned and operated 17,792 miles of gas mains; and owned 12 gas distribution headquarters and 2 sub-headquarters, as well as operated 62 natural gas metering and regulating stations. Public Service Enterprise Group Incorporated was founded in 1985 and is headquartered in Newark, New Jersey."},{"Symbol":"PSA","Name":"Public Storage","Sector":"Real Estate","Description":"Public Storage (the Trust) is a real estate investment trust (REIT). As of December 31, 2012, the Trust's principal business activities included: Domestic Self-Storage, European Self-Storage and Commercial. The Trust acquires, develops, owns, and operates self-storage facilities which offer storage spaces for lease, on a month-to-month basis, for personal and business use. It is a owner and operator of self-storage facilities in the United States. It has direct and indirect equity interests in 2,078 self-storage facilities (132 million net rentable square feet of space) located in 38 states within the United States operating under the Public Storage brand name. The Trust has a 49% equity interest in Shurgard Europe, with an institutional investor owning the remaining 51% interest. It has a 41% equity interest in PS Business Parks, Inc. (PSB), which owns and operates 28.3 million net rentable square feet of commercial space."},{"Symbol":"PHM","Name":"Pulte Homes Inc.","Sector":"Consumer Discretionary","Description":"PulteGroup, Inc. (PulteGroup) is a homebuilder in the United States. The Company's subsidiaries engage primarily in the homebuilding business. PulteGroup also has mortgage banking operations, conducted principally through Pulte Mortgage LLC (Pulte Mortgage), and title operations. Homebuilding, its core business, includes the acquisition and development of land primarily for residential purposes within the United States and the construction of housing on such land. Homebuilding offers a product line to meet the needs of home buyers in its targeted markets. The Company also has one segment for its financial services operations, which consist principally of mortgage banking and title operations. Its Financial Services segment operates generally in the same geographic markets as its Homebuilding segments. During the year ended December 31, 2011, it originated mortgage loans for 62%. Centex Corporation (Centex) and Pulte Mortgage are the wholly owned subsidiary of the Company."},{"Symbol":"PVH","Name":"PVH Corp.","Sector":"Consumer Discretionary","Description":"PVH Corp. operates as an apparel company in the United States and internationally. It designs, markets, and retails mens and womens apparel and accessories, branded dress shirts, neckwear, sportswear, jeanswear, intimate apparel, swim products, handbags, footwear, golf apparel, fragrances, cosmetics, eyewear, hosiery, socks, jewelry, watches, outerwear, small leather goods, and home furnishings, as well as other related products. The company offers its products under its own brands, such as Calvin Klein, Tommy Hilfiger, Van Heusen, IZOD, ARROW, Warners, Olga, and Eagle; licensed brands comprising Speedo, Geoffrey Beene, Kenneth Cole New York, Kenneth Cole Reaction, Sean John, MICHAEL Michael Kors, Michael Kors Collection, Chaps, Donald J. Trump Signature Collection, DKNY, Nautica, Ted Baker, J. Garcia, Claiborne, Ike Behar, Jones New York, and Ryan Seacrest; and trademarks, as well as various other licensed and private label brands. It also licenses its own brands over a range of products. The company distributes its products at wholesale in department, chain, specialty, mass market, club, off-price, and independent stores; and through company-operated full-price specialty and outlet stores, as well as through e-commerce Websites. PVH Corp. was founded in 1881 and is based in New York, New York."},{"Symbol":"QRVO","Name":"Qorvo","Sector":"Information Technology","Description":"Qorvo, Inc. provides technologies and radio frequency (RF) solutions for mobile, infrastructure, defense, and aerospace applications in the United States and internationally. The company operates through Mobile Products, and Infrastructure and Defense Products segments. The Mobile Products segment offers complete RF front end modules that combine high-performance filters, power amplifiers (PAs) and switches, PA modules, transmit modules, antenna control solutions, antenna switch modules, diversity receive modules, and envelope tracking power management devices. This segment supplies its RF solutions into various mobile devices, including smartphones, handsets, notebook computers, wearables, and tablets. The Infrastructure and Defense Products segment provides high power gallium arsenide and gallium nitride PAs, amplifiers, switches, fixed frequency and voltage-controlled oscillators, filters, attenuators, modulators, driver and transimpedance amplifiers, and various multichip and hybrid assemblies. This segment offers its RF solutions to wireless network infrastructure, defense, and aerospace markets; and connectivity applications for commercial, consumer, industrial, and automotive markets. The company sells its products directly, as well as through a network of sales representative firms and distributors to original equipment manufacturers and original design manufacturers of mobile products, and infrastructure and defense products. Qorvo, Inc. is headquartered in Greensboro, North Carolina."},{"Symbol":"QCOM","Name":"QUALCOMM Inc.","Sector":"Information Technology","Description":"QUALCOMM Incorporated (Qualcomm) is engaged in designing and manufacturing of digital communications products and services based on code division multiple access (CDMA), Orthogonal Frequency Division Multiplexing (OFDMA) and other technologies. The Company operates in four segments: Qualcomm CDMA Technologies (QCT); Qualcomm Technology Licensing (QTL); Qualcomm Wireless & Internet (QWI), and Qualcomm Strategic Initiatives (QSI). The Company develops and supply integrated circuits and system software based on CDMA, OFDMA and other technologies for uses in voice and data communications, networking, application processing, multimedia and global positioning system products. Effective July 4, 2013, Bharti Airtel Ltd raised its interest to 51% from 49% by acquiring a 2% interest in Qualcomm India Pvt Ltd, from Qualcomm Inc. In November 2013, the Company sold its subsidiary, Omnitracs, Inc to Vista Equity Partners."},{"Symbol":"PWR","Name":"Quanta Services Inc.","Sector":"Industrials","Description":"Quanta Services, Inc. (Quanta) is a provider of specialty contracting services, offering infrastructure solutions primarily to the electric power and natural gas and oil pipeline industries. The services the Company provides include the design, installation, upgrade, repair and maintenance of infrastructure within each of the industries the Company serves, such as electric power transmission and distribution networks, substation facilities, renewable energy facilities and pipelines transmission, gathering and distribution systems and facilities. It operates in three segments: electric power infrastructure services, natural gas and pipeline infrastructure services and fiber optic licensing and other. Effective August 14, 2013, Quanta Services Inc acquired JW Didado Electric Inc, an electric utility company. Effective July 29, 2013, Quanta Services Inc acquired Nacap Australia Pty Ltd."},{"Symbol":"DGX","Name":"Quest Diagnostics","Sector":"Health Care","Description":"Quest Diagnostics Incorporated (Quest Diagnostics), is a provider of diagnostic testing, information and services, providing insights that enable patients and physicians to make healthcare decisions. Quest Diagnostics offers United States patients and physicians the access to diagnostic testing services through its nationwide network of laboratories and Company-owned patient service centers. It provides interpretive consultation through the medical and scientific staff. It is a provider of clinical testing, including gene-based and esoteric testing and anatomic pathology services, and the provider of risk assessment services for the life insurance industry. On September 30, 2013, it sold its Enterix colorectal cancer screening test business to Clinical Genomics Technologies Pty Ltd. In October 2013, Quest Diagnostics Incorporated acquired ConVerge Diagnostic Services, LLC from Water Street Healthcare Partners."},{"Symbol":"RRC","Name":"Range Resources Corp.","Sector":"Energy","Description":"Range Resources Corporation (Range) is an independent natural gas, natural gas liquids and oil company, engaged in the exploration, development and acquisition of natural gas and oil properties, mostly in the Appalachian and Southwestern regions of the United States. Range operates in two regions: the Appalachian (which includes shale tight gas, coal bed methane and conventional natural gas, natural gas liquids, condensate and oil production in Pennsylvania, Virginia, and West Virginia), and Southwestern (which includes the Permian Basin of West Texas and the Delaware Basin of New Mexico, the Texas Panhandle, the Ardmore Basin in Southern Oklahoma, the Nemaha Uplift in Northern Oklahoma and the Anadarko Basin of Western Oklahoma). As of Decmbeer 31, 2011, the Company has approximately 8,600 proven and unproven drilling locations in inventory. During the year ended December 31, 2011, the Company discontinued its Barnett Shale assets that were sold in April 2011."},{"Symbol":"RTN","Name":"Raytheon Co.","Sector":"Industrials","Description":"Raytheon Company together with its subsidiaries, is a technology company specializing in defense, homeland security and other government markets worldwide. The Company provides electronics, mission systems integration and other capabilities in the areas of sensing, effects, and command, control, communications and intelligence systems (C3I), as well as a range of mission support services. It serves both domestic and international customers, principally as a prime contractor on a portfolio of defense and related programs for government customers. The Company operates in six business segments: Integrated Defense Systems (IDS); Intelligence and Information Systems (IIS); Missile Systems (MS); Network Centric Systems (NCS); Space and Airborne Systems (SAS), and Technical Services (TS). On January 31, 2011, the Company acquired Raytheon Applied Signal Technology, Inc. (RAST). Effective June 27, 2013, Raytheon Co acquired Visual Analytics Inc."},{"Symbol":"O","Name":"Realty Income Corporation","Sector":"Real Estate","Description":"Realty Income Corporation (Realty Income) is an equity real estate investment trust (REIT). The Company is engaged in acquiring and owning freestanding retail and other properties that generate rental revenue under long-term lease agreements (primarily 10 to 20 years). The Company has in-house acquisition, leasing, legal, credit research, real estate research, portfolio management and capital markets. At December 31, 2011, it owned a diversified portfolio of 2,634 properties with an occupancy rate of 96.7%, or 2,547 properties leased and only 87 properties available for lease. It leased properties to 136 different retail and other commercial enterprises doing business in 38 separate industries. It properties are located in 49 states, with over 27.3 million square feet of leasable space, and with an average leasable space per property of approximately 10,400 square feet. In January 2013, it acquired American Realty Capital Trust."},{"Symbol":"RHT","Name":"Red Hat Inc.","Sector":"Information Technology","Description":"Red Hat, Inc. (Red Hat) is a global provider of open source software solutions, using a community-powered approach to develop and offer operating system, middleware, virtualization, storage and cloud technologies. The Company offerings include Red Hat Enterprise Linux, Red Hat JBoss Middleware, Red Hat Enterprise Virtualization and Red Hat Storage Server. Red Hat Enterprise Linux is an operating system, which runs on a range of hardware, including mainframes, servers and work stations. Red Hat JBoss Middleware is a range of technologies used for developing, deploying and managing distributed applications. Red Hat Enterprise Virtualization includes standalone virtualization functionality and management tools for both server and desktop deployments. Red Hat Storage Server is software, which enables users to treat storage as a virtualized resource, and is available for deployment both on-premise and in public or hybrid clouds."},{"Symbol":"REG","Name":"Regency Centers Corporation","Sector":"Real Estate","Description":"Regency Centers Corporation is a real estate investment trust (REIT) and is the managing general partner in Regency Centers, L.P. It endeavors to be the preeminent national shopping center company distinguished by sustaining growth in shareholder value and compounding total shareholder return in excess of its peers. All of its operating, investing, and financing activities are performed through the Operating Partnership, its wholly-owned subsidiaries, and through its investments in real estate partnerships with third parties. In October 2013, Charter Realty & Development Corp. and Regency Centers Corporation closed on the acquisition of Fellsway Plaza, a 150,000-square-foot neighhood center anchored by a constructed Stop & Shop, located in Medford, Mass."},{"Symbol":"REGN","Name":"Regeneron","Sector":"Health Care","Description":"Regeneron Pharmaceuticals, Inc. (Regeneron) is an integrated biopharmaceutical company, which discovers, invents, develops, manufactures, and commercializes medicines for the treatment of serious medical conditions. As of December 31, 2011, the Company had two marketed products: EYLEA (aflibercept) Injection and ARCALYST (rilonacept) Injection for Subcutaneous Use. As of December 31, 2011, the Company had 13 product candidates in clinical development. Its Trap-based, late-stage programs are EYLEA, which is being developed for the treatment of additional serious eye diseases; ZALTRAP (aflibercept), known in the scientific literature as vascular endothelial growth factor (VEGF) trap, which is being developed in oncology in collaboration with Sanofi; and ARCALYST, which is being developed for the prevention of gout flares in patients initiating uric acid-lowering treatment."},{"Symbol":"RF","Name":"Regions Financial Corp.","Sector":"Financials","Description":"Regions Financial Corporation (Regions) is a financial holding company. Regions operate throughout the South, Midwest and Texas. The Company provides traditional commercial, retail and mortgage banking services, as well as other financial services in the fields of investment banking, asset management, trust, mutual funds, securities brokerage, insurance and other specialty financing. Regions conduct its banking operations through Regions Bank, a commercial bank. As of December 31, 2011, Regions operated approximately 2,100 automated teller machines (ATMs) and 1,726 banking offices in Alabama, Arkansas, Florida, Georgia, Illinois, Indiana, Iowa, Kentucky, Louisiana, Mississippi, Missouri, North Carolina, South Carolina, Tennessee, Texas and Virginia. It provides additional financial services through its subsidiaries, which includes Morgan Keegan & Company, Inc. and Regions Insurance Group, Inc. In April 2012, it sold Morgan Keegan & Company, Inc. and related affiliates to Raymond James"},{"Symbol":"RSG","Name":"Republic Services Inc","Sector":"Industrials","Description":"Republic Services, Inc. (Republic) is a provider of services in the domestic non-hazardous solid waste industry. Republic provides non-hazardous solid waste collection services for commercial, industrial, municipal and residential customers through 334 collection operations in 39 states and Puerto Rico. The Company owns or operates 194 transfer stations, 191 active solid waste landfills and 74 materials recovery facilities. It also operates 69 landfill gas and renewable energy projects. The Company operates in four geographical segments: Eastern, Midwestern, Southern and Western. Its residential collection operations involve the curbside collection of refuse from small containers into collection vehicles for transport to transfer stations or directly to landfills. Its commercial and industrial collection operations supply the customers with waste containers of varying sizes."},{"Symbol":"RAI","Name":"Reynolds American Inc.","Sector":"Consumer Staples","Description":"Reynolds American Inc. (RAI) is a holding company. The Company's operating subsidiaries include cigarette manufacturer in the United States, R. J. Reynolds Tobacco Company; the smokeless tobacco products manufacturer in the United States, American Snuff Company, LLC (American Snuff Co); the manufacturer of cigarette brand, Santa Fe Natural Tobacco Company, Inc. (SFNTC), and Niconovum AB. RAI operates in three segments: RJR Tobacco, American Snuff and Santa Fe. The RJR Tobacco segment consists of the primary operations of R. J. Reynolds Tobacco Company. The American Snuff segment consists of the primary operations of American Snuff Co. Santa Fe manufactures and markets cigarettes and other tobacco products under the NATURAL AMERICAN SPIRIT brand."},{"Symbol":"RHI","Name":"Robert Half International","Sector":"Industrials","Description":"Robert Half International Inc. provides specialized staffing and risk consulting services. The Company operates in three segments: temporary and consultant staffing, permanent placement staffing, and risk consulting and internal audit services. The Company, through its Accountemps, Robert Half Finance & Accounting, and Robert Half Management Resources divisions, is a specialized provider of temporary, full-time, and project professionals in the fields of accounting and finance. OfficeTeam specializes in skilled temporary administrative support personnel. Robert Half Technology provides information technology professionals. Robert Half Legal provides temporary, project, and full-time staffing of attorneys and specialized support personnel within law firms and corporate legal departments. The Creative Group provides project staffing in the advertising, marketing, and Web design fields. Protiviti is a global business consulting and internal audit firm."},{"Symbol":"ROK","Name":"Rockwell Automation Inc.","Sector":"Industrials","Description":"Rockwell Automation, Inc. (Rockwell Automation) is a provider of industrial automation power, control and information solutions that help manufacturers achieve a competitive advantage for their businesses. The Company operates in two segments: Architecture & Software and controls Products & Solutions. In the United States, Canada and certain other countries, the Company sells primarily through the independent distributors in conjunction with its direct sales force. In the remaining countries, the Company sells through a combination of its direct sales force. In March 2012, the Company purchased SoftSwitching Technologies."},{"Symbol":"COL","Name":"Rockwell Collins","Sector":"Industrials","Description":"Rockwell Collins, Inc. (Rockwell Collins) is engaged in design, production and support of communications and aviation electronics for commercial and military customers worldwide. The Company's products and systems are primarily focused on aviation applications, The integrated system solutions and products it provide to its served markets include communications, navigation, automated flight control, displays/surveillance, simulation and training, integrated electronics and information management systems. It also provides a range of services and support to its customers through a network of service centers, including equipment repair and overhaul, service parts, field service engineering, training, technical information services and aftermarket used equipment sales. It operates in two segments: Government Systems and Commercial Systems. In November 2013, Rockwell Collins, Inc completed the sale of its subsidiary, Kaiser Optical Systems, Inc. to Endress+Hauser (International) Holding AG."},{"Symbol":"ROP","Name":"Roper Industries","Sector":"Industrials","Description":"Roper Industries, Inc. (Roper) designs, manufactures and distributes radio frequency (RF) products, services and application software, industrial technology products, energy systems and controls and medical and scientific imaging products and software. The Company markets these products and services to a range of markets, including RF applications, medical, water, energy, research, education, software-as-a-service (SaaS)-based information networks, security and other niche markets. The Company operates in four segments: Medical and Scientific Imaging, Energy Systems and Controls, Industrial Technology and RF Technology. On August 22, 2012, the Company acquired Sunquest Information Systems, Inc. (Sunquest), a provider of diagnostic and laboratory software solutions to healthcare providers. In May 2013, Roper Industries Inc acquired Managed Health Care Associates Inc."},{"Symbol":"ROST","Name":"Ross Stores","Sector":"Consumer Discretionary","Description":"Ross Stores, Inc. is an off-price apparel and home fashion chain in the United States, with 1,091 locations in 33 states, the District of Columbia and Guam. The Company operates two brands of off-price retail apparel and home fashion stores: Ross Dress for Less (Ross) and dd's DISCOUNTS. It offers designer apparel, accessories, footwear, and home fashions for the entire family at everyday savings of 20% to 60% off department and specialty store regular prices. As of February 2, 2013, it operated 108 dd's DISCOUNTS stores in eight states. dd's DISCOUNTS features brand apparel, accessories, footwear, and home fashions for the entire family at everyday savings of 20% to 70% off moderate department and discount store regular prices."},{"Symbol":"RCL","Name":"Royal Caribbean Cruises Ltd","Sector":"Consumer Discretionary","Description":"Royal Caribbean Cruises Ltd. (Royal Caribbean) is a cruise company. As of December 31, 2012, the Company operated 41 ships in the cruise vacation industry across six brands with an aggregate capacity of approximately 98,650 berths. The Company's ships operate on a range of global itineraries, which call on approximately 455 destinations on all seven continents. Its global brands include Royal Caribbean International, Celebrity Cruises, Pullmantur, Azamara Club Cruises and CDF Croisieres de France. These brands are complemented by its Pullmantur brand, which has been custom tailored to serve the cruise markets in Spain, Portugal and Latin America, and its CDF Croisieres de France brand, which provides it with a custom tailored product targeted at the French market. In addition, it has a 50% investment in a joint venture, which operates the brand TUI Cruises, tailored for the German market."},{"Symbol":"R","Name":"Ryder System","Sector":"Industrials","Description":"Ryder System, Inc. (Ryder) is engaged in transportation and supply chain management solutions. Ryder operates in three business segments: Fleet Management Solutions (FMS), which provides full service leasing, contract maintenance, contract-related maintenance and commercial rental of trucks, tractors and trailers to customers principally in the United States, Canada and the United Kingdom; Supply Chain Solutions (SCS), which provides supply chain solutions, including distribution and transportation services throughout North America and Asia, and Dedicated Contract Carriage (DCC), which provides vehicles and drivers as part of a dedicated transportation solution in the United States. On June 8, 2011, it acquired all of the common stock of Hill Hire plc (Hill Hire). On August 1, 2012, it acquired Euroway Group Ltd. In September 2012, it opened its new Ryder Vehicle Sales center, offering used trucks for sale in the Greater Vancouver Area."},{"Symbol":"SPGI","Name":"S&P Global, Inc.","Sector":"Financials","Description":"S&P Global Inc. (prior to April 2016 McGraw Hill Financial, Inc., and prior to 2013 McGraw Hill Companies) is an American publicly traded corporation headquartered in New York City. Its primary areas of business are financial information and analytics. It is the parent company of S&P Global Ratings, S&P Global Market Intelligence, and S&P Global Platts, and is the majority owner of the S&P Dow Jones Indices joint venture."},{"Symbol":"CRM","Name":"Salesforce.com","Sector":"Information Technology","Description":"salesforce.com, inc. is a provider of enterprise cloud computing and social enterprise solutions. The Company provides a customer and collaboration relationship management (CRM), applications through the Internet or cloud. Cloud computing refers to the use of Internet-based computing, storage and connectivity technology to deliver a variety of different services. The Company delivers its service through Internet browsers and mobile devices. It markets its social enterprise applications and platforms to businesses on a subscription basis, primarily through its direct sales efforts and indirectly through partners. In May 2013, salesForce.com Inc acquired Clipboard Inc. In July 2013, salesforce.com, Inc. completed its acquisition of ExactTarget Inc."},{"Symbol":"SCG","Name":"SCANA Corp","Sector":"Utilities","Description":"SCANA Corporation, through its subsidiaries, is engaged in the generation, transmission, distribution, and sale of electricity to retail and wholesale customers in South Carolina. It owns nuclear, coal, hydro, natural gas and oil, and biomass generating facilities. The company also purchases, sells, and transports natural gas; offers energy-related risk management services; and provides service contracts on home appliances, and heating and air conditioning units. In addition, it owns two liquefied natural gas plants in Charleston and Salley, South Carolina; and offers tower site construction, management, and rental services, as well as sells towers in South Carolina and North Carolina. As of December 31, 2013, the company supplied electricity to approximately 678,000 customers; and provided natural gas to approximately 509,000 residential, commercial, and industrial customers in North Carolina and South Carolina, as well as markets natural gas to approximately 454,000 customers in Georgia. Further, SCANA Corporation owns and operates a 1,125 mile fiber optic telecommunications network and Ethernet network, as well as data center facilities in South Carolina. Additionally, it builds, manages, and leases communications towers with interests in 2,280 miles of fiber in South Carolina, North Carolina, and Georgia through a joint venture. The companys customers comprise municipalities, electric cooperatives, other investor-owned utilities, registered marketers, and federal and state electric agencies. It also serves chemical, educational service, paper product, food product, lumber and wood product, health service, textile manufacturing, rubber and miscellaneous plastic product, and fabricated metal product industries. The company was founded in 1924 and is based in Cayce, South Carolina."},{"Symbol":"SLB","Name":"Schlumberger Ltd.","Sector":"Energy","Description":"Schlumberger Limited (Schlumberger N.V.) is the supplier of technology, integrated project management and information solutions to the international oil and gas exploration and production industry. The Company's segments include Reservoir Characterization Group, which consists of the principal technologies involved in finding and defining hydrocarbon deposits; Drilling Group, which consists of the principal technologies involved in the drilling and positioning of oil and gas wells, and Production Group consists of the principal technologies involved in the lifetime production of oil and gas reservoirs and includes Well Services, Completions, Artificial Lift, Well Intervention, Subsea, Water Services, Carbon Services and the Schlumberger Production Management field production projects."},{"Symbol":"SNI","Name":"Scripps Networks Interactive Inc.","Sector":"Consumer Discretionary","Description":"Scripps Networks Interactive, Inc. is a developer of lifestyle-oriented content for television and the Internet with television and interactive brands. The Company operates through its Lifestyle Media segment. Lifestyle Media includes the Company's national television networks, Food Network, Home and Garden Television (HGTV), Travel Channel, DIY Network (DIY), Cooking Channel and Great American Country (GAC). Lifestyle Media also includes Websites that are associated with the aforementioned television brands and other Internet-based businesses serving food, home and travel related categories. The Company's lifestyle-oriented channels are available in the United Kingdom, other European markets, the Middle East, Africa and Asia-Pacific. In April 2012, it acquired Travel Channel International, Ltd. (TCI). In April 2013, it announced the acquisition of Asian Food Channel."},{"Symbol":"STX","Name":"Seagate Technology","Sector":"Information Technology","Description":"Seagate Technology plc (Seagate) is the provider of electronic data storage products. The Company's principal products are hard disk drives, commonly referred to as disk drives, hard drives or HDDs. The Company produces a range of disk drive products addressing enterprise applications, where its products are designed for enterprise servers, mainframes and workstations; client compute applications, desktop and notebook computers, and client non-compute applications, for a range of end user devices, such as digital video recorders (DVRs), personal data backup systems, portable external storage systems and digital media systems. In addition to manufacturing and selling disk drives, it provides data storage services for small- to medium-sized businesses, including online backup, data protection and recovery solutions. On December 19, 2011, it completed the acquisition of Samsung Electronics Co., Ltd's (Samsung) hard disk drive business. In August 3, 2012, it acquired 64.5% of LaCie S.A."},{"Symbol":"SEE","Name":"Sealed Air","Sector":"Materials","Description":"Sealed Air Corporation is engaged in food safety and security, facility hygiene and product protection business. The Company serves a range of end markets, including food and beverage processing, food service, retail, health care and industrial, commercial and consumer applications. The Company's brands include bubbles Wrap brand cushioning, Cryovac brand food packaging solutions and Diversey brand cleaning and hygiene solutions. The Company operates in four segments: food and beverage, institutional and laundry and protective packaging, and other category, which includes its medical applications and new venture businesses. On November 14, 2012, the Company completed the sale Diversey G.K. (Diversey Japan)."},{"Symbol":"SRE","Name":"Sempra Energy","Sector":"Utilities","Description":"Sempra Energy is a holding company. During the year ended December 31, 2011, Sempra Energy's business was organized in five segments consisting of San Diego Gas & Electric Company (SDG&E), Southern California Gas Company (SoCalGas), Sempra Generation, Sempra Pipelines & Storage and Sempra LNG (liquefied natural gas). Sempra Generation, Sempra Pipelines & Storage and Sempra LNG are subsidiaries of Sempra Global. Sempra Global is a holding company for most of its subsidiaries. In September 2013, Sempra Energy's Sempra United States Gas & Power acquired and will develop the Broken Bow 2 wind project in Nebraska."},{"Symbol":"SHW","Name":"Sherwin-Williams","Sector":"Materials","Description":"The Sherwin-Williams Company (Sherwin-Williams) is engaged in the development, manufacture, distribution and sale of paint, coatings and related products to professional, industrial, commercial and retail customers primarily in North and South America with additional operations in the Caribbean region, Europe and Asia. The Company has four segments: Paint Stores Group, Consumer Group, Global Finishes Group and Latin America Coatings Group. In September 2013, Sherwin-Williams Co announced that it has completed the acquisition of the United States/Canada business of Consorcio Comex, S.A. de C.V."},{"Symbol":"SIG","Name":"Signet Jewelers","Sector":"Consumer Discretionary","Description":"Signet Jewelers Limited is engaged in the retail sale of jewelry and watches in the United States, the United Kingdom, the Republic of Ireland, and the Channel Islands. The company operates through US and UK divisions. As of February 1, 2014, the US division operated a network of 1,471 stores in 50 states, including stores that trade nationally in malls and off-mall locations under the Kay Jewelers brand, and regionally under the mall-based brands; and destination superstores under the Jared The Galleria Of Jewelry brand, as well as Ultra stores. As of February 1, 2014, the UK division operated a network of 493 stores, including 14 stores in the Republic of Ireland and 3 in the Channel Islands under the H.Samuel, Ernest Jones, and Leslie Davis brands in regional shopping malls and high street locations. The company also operates a diamond polishing factory, which is involved in rough diamond sourcing and manufacturing activities. Signet Jewelers Limited was founded in 1950 and is based in Hamilton, Bermuda."},{"Symbol":"SPG","Name":"Simon Property Group Inc","Sector":"Real Estate","Description":"Simon Property Group, Inc. is an equity real estate investment trust. The firm invests in the real estate markets across the globe. It engages in investment, ownership, and management of properties. It primarily invests in regional malls, Premium Outlets, The Mills, and community/lifestyle centers to create its portfolio. Simon Property Group, Inc. was founded in 1960 and is based in Indianapolis, Indiana, with additional offices in Delaware, United States; and New York, New York."},{"Symbol":"SWKS","Name":"Skyworks Solutions","Sector":"Information Technology","Description":"Skyworks Solutions, Inc. (Skyworks) offers analog and mixed signal semiconductors. Skyworks offers custom linear products supporting automotive, broadband, cellular infrastructure, energy management, industrial, medical, military and cellular handset applications. The Company's portfolio includes amplifiers, attenuators, circulators, detectors, diodes, directional couplers, front-end modules, hybrids, infrastructure radio frequency (RF) subsystems, isolators, mixers/demodulators, optocouplers, optoisolators, phase shifters, phase locked loops (PLLs)/synthesizers/VCOs, power dividers/combiners, receivers, switches and technical ceramics. Its portfolio includes customized power amplifiers and front-end solutions. On June 10, 2011, the Company completed the acquisition of SiGe Semiconductor, Inc. (SiGe), a semiconductor provider. On April 27, 2011, the Company acquired 100% ownership of a private company engaged in the design and manufacturing of optical components."},{"Symbol":"SLG","Name":"SL Green Realty","Sector":"Real Estate","Description":"SL Green Realty Corp., incorporated on June, 10, 1997, is a self-managed real estate investment trust (REIT), with in-house capabilities in property management, acquisitions, financing, development, construction and leasing. As of December 31, 2012, its Manhattan office properties were comprised of 21 fee owned properties, including ownership in commercial condominium units, and six leasehold owned properties. In July 2012, the Company and its joint venture partners sold One Court Square in Long Island City, New York. On November 22, 2012, the Company and its partner Westcore Properties sold Jersey Property Unit Trust (JPUT). In December 2012, the Company acquired the non-performing mortgage loan secured by the property at 315 Park Avenue South, New York. During 2012, the Company acquired 12 properties. During 2012, the Company sold its fee interest at 292 Madison Avenue."},{"Symbol":"SNA","Name":"Snap-On Inc.","Sector":"Consumer Discretionary","Description":"Snap-on Incorporated (Snap-on) is a manufacturer and marketer of tools, equipment, diagnostics, repair information and systems solutions for professional users. Snap-on's products and services include hand and power tools, tool storage, diagnostics software, information and management systems, shop equipment and other solutions for vehicle dealerships and repair centers, as well as customers in industries, including aviation, aerospace, agriculture, construction, Government and military, mining, natural resources, power generation and technical education. Snap-on also derives income from various financing programs to facilitate the sales of its products. Snap-on markets its products and brands through a number of distribution sales channels in approximately 130 countries. Its business segments include the Commercial & Industrial Group; the Snap-on Tools Group; the Repair Systems & Information Group, and Financial Services. In May 2013, the Company acquired Challenger Lifts, Inc."},{"Symbol":"SO","Name":"Southern Co.","Sector":"Utilities","Description":"The Southern Company (Southern Company)is a holding company, which owns all of the common stock of the traditional operating companies, including Alabama Power Company (Alabama Power), Georgia Power Company (Georgia Power), Gulf Power Company (Gulf Power), and Mississippi Power Company (Mississippi Power) ,and Southern Power Company (Southern Power), and other direct and indirect subsidiaries (together, the Southern Company system). The primary business of the Southern Company system is electricity sales by the traditional operating companies and Southern Power. The four traditional operating companies are vertically integrated utilities providing electric service in four Southeastern states. Southern Power constructs, acquires, owns, and manages generation assets. In July 2012, the Company and Turner Renewable Energy acquired a 20 megawatts solar photovoltaic power plant in Nevada from SunEdison, a subsidiary of MEMC Electronic Materials, Inc."},{"Symbol":"LUV","Name":"Southwest Airlines","Sector":"Industrials","Description":"Southwest Airlines Co. operates Southwest Airlines, a passenger airline, which provides scheduled air transportation in the United States. As of December 31, 2011, the Company was serving 72 cities in 37 states throughout the United States. During the year ended December 31, 2011, the Company added addition services in two new states and three new cities: Charleston, South Carolina; Greenville-Spartanburg, South Carolina; and Newark, New Jersey. Southwest provides point-to-point. On May 2, 2011, the Company acquired AirTran Holdings, Inc. (AirTran). In December 2013, Southwest Airlines Co acquired 12 takeoff and landing slots at New York's LaGuardia Airport being divested by American Airlines as part of its merger with US Airways."},{"Symbol":"SWN","Name":"Southwestern Energy","Sector":"Energy","Description":"Southwestern Energy Company is an independent energy company engaged in natural gas and oil exploration, development and production. It is focused on creating and capturing additional value through its natural gas gathering and marketing businesses, which it refers to as Midstream Services. Its primary business is the exploration for and production of natural gas and oil, with operations being focused within the United States on development of an unconventional gas reservoir located on the Arkansas side of the Arkoma Basin, which it refers to as the Fayetteville Shale play. It is also engaged in exploration and production activities in Pennsylvania, and Texas and in Arkansas and Oklahoma in the Arkoma Basin. It conducts its exploration and production operations through its wholly owned subsidiaries, SEECO, Inc., and Southwestern Energy Production Company. In May 2011, it sold the producing rights to the Haynesville and Middle Bossier Shale intervals in approximately 9,717 net acres."},{"Symbol":"SWK","Name":"Stanley Black & Decker","Sector":"Consumer Discretionary","Description":"Stanley Black & Decker Inc. is a diversified global provider of power and hand tools, mechanical access solutions (automatic doors, commercial and residential locking systems), electronic security and monitoring systems and products and services for various industrial applications. The Company's operations are classified into three business segments: Construction & Do-It-Yourself (CDIY), Security, and Industrial. In September 2011, Stanley Black & Decker acquired Niscayah Group AB. In September 2011, the Company acquired Microtec Enterprises, Inc. In January 2011, the Company acquired InfoLogix, Inc. In December 2012, Spectrum Brands Holdings Inc acquired Hardware & Home Improvement Group (HHI) of the Company. In February 2013, the Company completed its acquisition of Infastech."},{"Symbol":"SPLS","Name":"Staples Inc.","Sector":"Consumer Discretionary","Description":"Staples, Inc. (Staples) is an office products company. The Company operates in three business segments: North American Stores & Online, North American Commercial and International Operations. As of February 02, 2013, the Company served businesses of all sizes and consumers in North America, Europe, Australia, South America and Asia. The Company's North American Commercial segment consists of the United States and Canadian businesses that sell and deliver office products and services directly to businesses and includes Staples Advantage and Quill.com. The Company's International Operations segment consists of businesses in 23 countries in Europe, Australia, South America and Asia. The Company offers more than 10,000 own brand products and services, including an assortment of products with various environmental attributes, which includes its Sustainable Earth brand products. In October 2013, Staples Inc completed the acquisition of Runa."},{"Symbol":"SBUX","Name":"Starbucks Corp.","Sector":"Consumer Discretionary","Description":"Starbucks Corporation is a roaster, marketer and retailer of coffee operating in 60 countries. The Company purchases and roasts coffees that it sells, along with handcrafted coffee, tea and other beverages and a variety of fresh food items, through Company-operated stores. It also sells a variety of coffee and tea products and licenses its trademarks through other channels, such as licensed stores and national foodservice accounts. As of September 30, 2012, it operated 9,405 Company-operated stores and 8,661 licensed stores. It has four segments: Americas; Europe, Middle East and Africa (EMEA); China/Asia Pacific (CAP), and Channel Development. In addition to its flagship Starbucks brand, the Company's portfolio also includes Tazo Tea, Seattle's Best Coffee, Starbucks VIA Ready Brew, Starbucks Refreshers beverages and the Verismo System by Starbucks. In February 2013, Tata Coffee Ltd and the Company inaugurated a roasting and packaging plant in Karnataka."},{"Symbol":"STT","Name":"State Street Corp.","Sector":"Financials","Description":"State Street Corporation (State Street) is a financial holding company. Through its subsidiaries, including its principal banking subsidiary, State Street Bank and Trust Company (State Street Bank), the Company provides a range of financial products and services to institutional investors worldwide. The Company has two lines of business: Investment Servicing and Investment Management. At December 31, 2011, it had consolidated total deposits of $157.29 billion. Its clients include mutual funds, collective investment funds and other investment pools, corporate and public retirement plans, insurance companies, foundations, endowments and investment managers. State Street operates in 29 countries and in more than 100 geographic markets worldwide. On November 3, 2011, the Company acquired Pulse Trading, Inc. On October 3, 2011, the Company acquired Complementa Investment-Controlling AG. On January 10, 2011, it acquired Bank of Ireland's asset management business (BIAM)."},{"Symbol":"SRCL","Name":"Stericycle Inc","Sector":"Industrials","Description":"Stericycle, Inc., is in the business of managing regulated waste and providing an array of related and complementary services. The Company operates in the United States, Argentina, Brazil, Canada, Chile, Ireland, Japan, Mexico, Portugal, Romania, Spain, and the United Kingdom. The regulated waste services the Company provides include medical waste disposal, its Steri-Safe medical waste and compliance program, its Clinical Services program, its Bio Systems reusable sharps disposal management services, pharmaceutical waste disposal, and hazardous waste disposal. In addition to the Company's regulated waste services, the Company offers regulated recall and returns management services, patient communication services, and medical safety products. In August 2013, Stericycle Inc acquired Eclipse Marketing."},{"Symbol":"SYK","Name":"Stryker Corp.","Sector":"Health Care","Description":"Stryker Corporation (Stryker) is a medical technology company. Stryker offers a diverse array of medical technologies, including reconstructive, medical and surgical, and neurotechnology and spine products. The Company operates in three business segments: Reconstructive, MedSurg, and Neurotechnology and Spine. Its products include implants used in joint replacement, trauma, and spinal surgeries; surgical equipment and surgical navigation systems; endoscopic and communications systems; patient handling and emergency medical equipment, as well as other medical device products used in a variety of medical specialties. Internationally, its products are sold in over 100 countries through Company-owned sales subsidiaries and branches, as well as third-party dealers and distributors. In 2012, the Company completed the acquisition of Surpass Medical, Ltd. (Surpass). Effective March 1, 2013, Stryker Corp acquired the share capital of Trauson Holdings Co Ltd."},{"Symbol":"STI","Name":"SunTrust Banks","Sector":"Financials","Description":"SunTrust Banks, Inc. (SunTrust) is a commercial banking organization. The Company is a diversified financial services holding company whose businesses provide a range of financial services to consumer and corporate clients. The Company operates in three segments: Consumer Banking and Private Wealth Management, Wholesale Banking, and Mortgage Banking. Through its principal subsidiary, SunTrust Bank, the Company offers a full line of financial services for consumers and businesses including deposit, credit, and trust and investment services. Additional subsidiaries provide mortgage banking, asset management, securities brokerage, and capital market services. SunTrust operates primarily within Florida, Georgia, Maryland, North Carolina, South Carolina, Tennessee, Virginia, and the District of Columbia. SunTrust provides clients with a selection of branch-based and technology-based banking channels, including the Internet, automatic teller machine (ATMs), and twenty-four hour telebanking."},{"Symbol":"SYMC","Name":"Symantec Corp.","Sector":"Information Technology","Description":"Symantec Corporation is a security, backup and availability solutions. The Company's products and services protect people and information in any digital environment from the smallest mobile device, to the enterprise data center, to cloud-based systems. The Company's software and services protect against advanced threats independent of the device and environment in which information is used or stored. The Company operated in five segments: Consumer, Security and Compliance, Storage and Server Management, Services, and Other. The Company's segment provides identity protection and digital consumer data, such as photos, music, and videos. The Security and Compliance solutions help businesses protect their information and systems. The Company delivers consulting, education and dedicated support services."},{"Symbol":"SYF","Name":"Synchrony Financial","Sector":"Financials","Description":"Synchrony Financial operates as a consumer financial services company in the United States. The company offers private label credit cards, dual cards, and small and medium-sized business credit products; and promotional financing for consumer purchases, such as private label credit cards and installment loans. It also provides promotional financing to consumers for elective healthcare procedures or services, such as dental, veterinary, cosmetic, vision, and audiology; debt cancellation products; and deposit products, including certificates of deposit, individual retirement accounts, money market accounts, and savings accounts under the Optimizer+Plus brand. The company offers its credit products through programs established with a group of national and regional retailers, local merchants, manufacturers, buying groups, industry associations, and healthcare service providers; and deposit products through multiple channels, including online, print, and radio advertising. Synchrony Financial was incorporated in 2003 and is headquartered in Stamford, Connecticut. Synchrony Financial operates as a subsidiary of GE Consumer Finance, Inc."},{"Symbol":"SYY","Name":"Sysco Corp.","Sector":"Consumer Staples","Description":"Sysco Corporation (Sysco), along with its subsidiaries and divisions, is a North American distributor of food and related products primarily to the foodservice or food-away-from-home industry. The Company provides products and related services to approximately 425,000 customers, including restaurants, healthcare and educational facilities, lodging establishments and other foodservice customers. Sysco provides food and related products to the foodservice or food-away-from-home industry. The Company has aggregated its operating companies into a number of segments, of which only Broadline and SYGMA are the main segments. Broadline operating companies distribute a line of food products and a variety of non-food products to their customers. SYGMA operating companies distribute a line of food products and a variety of non-food products to chain restaurant customer locations. On October 3, 2012, the Company acquired Keelings Foods."},{"Symbol":"TROW","Name":"T. Rowe Price Group","Sector":"Financials","Description":"T. Rowe Price Group, Inc. is a financial services holding company, which provides global investment management services through its subsidiaries to individual and institutional investors in the sponsored T. Rowe Price mutual funds and other investment portfolios, including separately managed accounts, sub-advised funds, and other sponsored investment funds offered to investors outside the United States and through variable annuity life insurance plans in the United States. The Company derives revenue from investment advisory services provided by its subsidiaries, such as T. Rowe Price Associates, Inc. and T. Rowe Price International Ltd. Its revenues depend on the total value and composition of assets under its management. Its assets under management are accumulated from a client base across four distribution channels. Effective August 20, 2013, T Rowe Price Group Inc acquired an undisclosed interest in GrubHub Inc, a provider of online food delivery services."},{"Symbol":"TGT","Name":"Target Corp.","Sector":"Consumer Discretionary","Description":"Target Corporation sells a range of assortment of general merchandise and food in its stores. The Company's general merchandise and CityTarget stores offer a food assortment on a smaller scale and its SuperTarget stores offer a line of food items comparable to traditional supermarkets. The Company operates in three segments: U.S. Retail, U.S. Credit Card and Canadian. The Company's U.S. Retail Segment includes all of its United States merchandising operations. The Company's U.S. Credit Card Segment offers credit to qualified guests through its credit cards: the Target Credit Card and the Target Visa. Its Canadian Segment includes costs incurred in the United States and Canada related to its Canadian retail market entry. As of February 2, 2013, the Company had 1,778 stores in 49 states and the District of Columbia. It also owns three distribution centers in Canada, with a total of 3,963 thousand square feet."},{"Symbol":"TEL","Name":"TE Connectivity Ltd.","Sector":"Information Technology","Description":"TE Connectivity Ltd., together with its subsidiaries, designs and manufactures connectivity and sensors solutions. It operates through four segments: Transportation Solutions, Industrial Solutions, Network Solutions, and Consumer Solutions. The Transportation Solutions segment offers electronic components, including terminals and connectors, relays, and sensors, as well as application tooling, wire and heat shrink tubing, and other custom-engineered solutions for the automotive market, such as industrial and commercial vehicle, and hybrid and electric vehicle markets. The Industrial Solutions segment supplies products that connect and distribute power and data, which consists of connectors, heat shrink tubing, relays, and wire and cable, as well as custom-engineered solutions used primarily in the industrial equipment; aerospace, defense, marine, oil, and gas; and energy markets. The Network Solutions segment supplies infrastructure components and systems for the telecommunications market and electronic components for the data communications market. This segments products include connectors, fiber optics, wire and cable, racks and panels, and wireless products. It also develops, manufactures, installs, and maintains subsea fiber optic communications systems. The Consumer Solutions segment supplies electronic components, including connectors, circuit protection devices, relays, antennas, and heat shrink tubing for the consumer devices and appliances markets. TE Connectivity Ltd. sells its products in approximately 150 countries primarily through direct sales to manufacturers, as well as indirectly via third-party distributors. The company was formerly known as Tyco Electronics Ltd. and changed its name to TE Connectivity Ltd. in March 2011. TE Connectivity Ltd. is based in Schaffhausen, Switzerland."},{"Symbol":"TGNA","Name":"Tegna, Inc.","Sector":"Consumer Discretionary","Description":"TEGNA Inc. engages in media and digital businesses in the United States. The company operates 46 television stations that produce local programming, such as news, sports, and entertainment; and affiliated online sites. It also operates Cars.com that provides information about car shopping, selling, and servicing; CareerBuilder, which provides human capital solutions, such as labor market intelligence, talent management software, and other recruitment solutions; and G/O Digital that acts as a one-stop shop for local businesses looking to connect with consumers through digital marketing. In addition, the company offers advertising and marketing solutions comprising digital and print options; and news, information, and marketing solutions in the military, defense, federal technology, and C4ISR markets. As of June 29, 2015, it served 90 million customers. The company was formerly known as Gannett Co., Inc. and changed its name to TEGNA Inc. in June 2015. TEGNA Inc. was founded in 1906 and is headquartered in McLean, Virginia."},{"Symbol":"TDC","Name":"Teradata Corp.","Sector":"Information Technology","Description":"Teradata Corporation (Teradata) is engaged in analytic data solutions, including integrated data warehousing, big data analytics and business applications. The Company's data data warehousing solutions are consisted of software, hardware, and related business consulting and support services. Its solutions integrate an organization's departmental and enterprise-wide data about customers, financials and operations into a single integrated data warehouse. Its analytic technologies then transform that data into actionable information. The Company serves customers across a range of industries globally ranging from small departmental and corporate implementations to data warehouses and marketing applications. Teradata operates from four main locations in the United States: Dayton, Ohio; Johns Creek (Atlanta), Georgia; Rancho Bernardo (San Diego), California; and Indianapolis, Indiana. In June 2012, the Company acquired eCircle."},{"Symbol":"TSO","Name":"Tesoro Petroleum Co.","Sector":"Energy","Description":"Tesoro Corporation (Tesoro), is an independent petroleum refiners and marketers in the United States. The Company's subsidiaries, operating through two business segments: manufacture and sell transportation fuels. Its refining operating segment (refining), which operates seven refineries in the western United States, refines crude oil and other feedstocks into transportation fuels, such as gasoline, gasoline blendstocks, jet fuel and diesel fuel, as well as other products, including heavy fuel oils, liquefied petroleum gas, petroleum coke and asphalt. Its retail operating segment (retail) sells transportation fuels and convenience products In September 2013, the Company announced that it has completed the sale of all of its interests in Tesoro Hawaii, LLC to Hawaii Pacific Energy, LLC. In December 2013, Tesoro Corp closed the acquisition of the majority of the remaining Los Angeles logistics assets."},{"Symbol":"TXN","Name":"Texas Instruments","Sector":"Information Technology","Description":"Texas Instruments Inc. (TI) designs and makes semiconductors that the Company sells to electronics designers and manufacturers all over the world. The Company has four segments: Analog, Embedded Processing, Wireless and Other. The Company's products, more than 100,000 orderable parts, are integrated circuits that are used to accomplish many different things, such as converting and amplifying signals, interfacing with other devices, managing and distributing power, processing data, canceling noise and improving signal resolution. The Company sells catalog and, to a lesser extent, custom semiconductor products. The life cycles of catalog products generally span multiple years, with some products continuing to sell for decades after their initial release. The life cycles of custom products are generally determined by end-equipment upgrade cycles and can be as short as 12 to 24 months. In December 2013, TI acquired a building in the Chengdu Hi-Tech Zone from UTAC Chengdu Ltd."},{"Symbol":"TXT","Name":"Textron Inc.","Sector":"Industrials","Description":"Textron Inc. is a multi-industry company engaged in aircraft, defense, industrial and finance businesses to provide customers with products and services worldwide. It operates in five segments: Cessna, Bell, Textron Systems and Industrial, which represent its manufacturing businesses, and Finance, which represents its finance business. Cessna is a general aviation company with two principal lines of business: Aircraft sales and aftermarket services. Bell Helicopter is a supplier of military and commercial helicopters, tiltrotor aircraft, and related spare parts and services in the world. Textron Systems is a supplier to the defense, aerospace, homeland security and general aviation markets. Industrial segment designs and manufactures a range of products under three principal product lines. The Company's Finance segment, or the Finance group, is a commercial finance business that consists of Textron Financial Corporation (TFC) and its consolidated subsidiaries."},{"Symbol":"BK","Name":"The Bank of New York Mellon Corp.","Sector":"Financials","Description":"The Bank of New York Mellon Corporation provides various financial products and services in the United States and internationally. Its Investment Management segment provides institutional, intermediary, retirement and retail investment management, distribution, and related services. This segment offers various equity, fixed income, cash, and alternative/overlay products; and investment management, wealth and estate planning, and private banking solutions to high-net-worth individuals and families, family offices, charitable gift programs, endowments, and foundations. The companys Investment Services segment provides custody and related services, broker-dealer services, collateral services, corporate trust and depositary receipt, and clearing services, as well as payment/working capital solutions to financial institutional clients. This segments financial solutions include custody, fund services, securities lending, investment manager outsourcing, performance and risk analytics, alternative investment services, securities clearance, collateral management, corporate trust, depositary receipt programs, cash management solutions, payment services, liquidity services and other linked revenues, foreign exchange, clearing and execution, managed account services, and prime brokerage solutions. This segment serves corporations, public funds and government agencies, foundations, and endowments; financial institutions, including banks, broker-dealers, asset managers, insurance companies, and central banks; financial intermediaries and independent registered investment advisors; and hedge fund managers. The company is also involved in credit-related activities, business exits, leasing operations, and corporate treasury activities; and the provision of global markets and institutional banking services. The Bank of New York Mellon Corporation was founded in 1784 and is headquartered in New York, New York."},{"Symbol":"CLX","Name":"The Clorox Company","Sector":"Consumer Staples","Description":"The Clorox Company (Clorox) is a manufacturer and marketer of consumer and professional products. Clorox operates in four segments: Cleaning, Household, Lifestyle and International. Clorox sells its products primarily through merchandisers, grocery stores, other retail outlets, distributors and medical supply providers. Clorox markets some of the brand names, including its namesake bleach and cleaning products, Clorox Healthcare, HealthLink, Aplicare and Dispatch products, Green Works naturally derived home care products, Pine-Sol cleaners, Poett home care products, Fresh Step cat litter, Glad bags, wraps and containers, Kingsford charcoal, Hidden Valley and K C Masterpiece dressings and sauces, Brita water-filtration products, and Burt's Bees and gud natural personal care products. During the fiscal year ended June 30, 2012 (fiscal 2012), Clorox acquired HealthLink, Aplicare, Inc., and Soy Vay Enterprises, Inc."},{"Symbol":"COO","Name":"The Cooper Companies","Sector":"Health Care","Description":"The Cooper Companies, Inc. (Cooper) is a global medical products company that serves the specialty healthcare market through its two business units, CooperVision, Inc. (CVI) and CooperSurgical, Inc. (CSI). CVI develops, manufactures and markets a range of contact lenses for the global vision correction market. CVI specializes in lenses for astigmatism, presbyopia and ocular dryness. CVI is a manufacturer of toric lenses, which correct astigmatism, multifocal lenses for presbyopia (blurring near vision due to advancing age) and spherical lenses that correct the common visual defects. CVI's products are manufactured at its facilities located in the United Kingdom, Puerto Rico and New York. Effective October 31, 2013, the Company completed the sale of Aime, its rigid gas permeable contact lens and solutions business in Japan, to Nippon Contact Lens Inc."},{"Symbol":"HSY","Name":"The Hershey Company","Sector":"Consumer Staples","Description":"The Hershey Company is a producer of chocolate in North America and a provider in chocolate and sugar confectionery. The Company's principal product groups include chocolate and sugar confectionery products; pantry items, such as baking ingredients, toppings and beverages, and gum and mint refreshment products. It manufactures, markets, sells and distributes its products under more than 80 brand names. The Company markets its products in approximately 70 countries worldwide. The Company operates in United States, the Americas, and Asia, Europe, the Middle East and Africa (AEMEA). The Company's key regions include the United States; the Americas, including Canada, Mexico, Brazil, Central America, Puerto Rico and global exports and AEMEA, including Asia, Europe, the Middle East, Africa and exports to these geographical areas In January 2012, the Company acquired Brookside Foods Ltd. (Brookside)."},{"Symbol":"MOS","Name":"The Mosaic Company","Sector":"Materials","Description":"The Mosaic Company (Mosaic) is a producer and marketer of combined concentrated phosphate and potash crop nutrients. It is a single source supplier of phosphate-and potash-based crops nutrients and animal feed ingredients. It operates in two segments: Phosphates and Potash. Its Phosphates segment owns and operates mines and production facilities in Florida. Its Potash segment owns and operates potash mines and production facilities in Canada and the United States, which produce potash-based crop nutrients, animal feed ingredients and industrial products. The Company mines phosphate rock in Florida and processes rock into finished phosphate products at facilities in Florida and Louisiana. It mines potash in Saskatchewan, New Mexico and Michigan. The Company has other production, blending or distribution operations in Brazil, China, India, Argentina, and Chile, and an equity investment in a new phosphate rock mine in the Bayovar region in Peru."},{"Symbol":"TRV","Name":"The Travelers Companies Inc.","Sector":"Financials","Description":"The Travelers Companies, Inc. (TRV) is a holding company. The Company, through its subsidiaries, is engaged in providing a range of commercial and personal property and casualty insurance products and services to businesses, Government units, associations and individuals. The Company is organized into three business segments: Business Insurance; Financial, Professional and International Insurance, and Personal Insurance. The Business Insurance segment offers an array of property and casualty insurance and insurance-related services to its clients primarily in the United States. The Financial, Professional and International Insurance segment includes surety and financial liability coverage's, which primarily use credit-based underwriting processes, as well as property and casualty products that are marketed on a domestic basis. In November 2013, the Company completed the sale of its wholly owned subsidiary The Dominion of Canada General Insurance Company to The Travelers Companies, Inc."},{"Symbol":"DIS","Name":"The Walt Disney Company","Sector":"Consumer Discretionary","Description":"The Walt Disney Company, together with its subsidiaries, is a diversified worldwide entertainment company. The Company operates in five business segments: Media Networks, Parks and Resorts, Studio Entertainment, Consumer Products and Interactive. The Company has a 51% effective ownership interest in Disneyland Paris, a 5,510-acre development located in Marne-la-Vallee, approximately 20 miles east of Paris, France. The Company manages and has a 40% equity interest in Euro Disney S.C.A. The Company owns a 48% interest in Hong Kong Disneyland Resort through Hongkong International Theme Parks Limited. On November 7, 2012, the Company sold its 50% equity interest in ESPN STAR Sports (ESS). On December 21, 2012, the Company acquired Lucasfilm Ltd. LLC."},{"Symbol":"TMO","Name":"Thermo Fisher Scientific","Sector":"Health Care","Description":"Thermo Fisher Scientific Inc. (Thermo Fisher) is engaged in serving science. It operates in three segments: Analytical Technologies, Specialty Diagnostics and Laboratory Products and Services. It serves its customers through three brands: Thermo Scientific, Fisher Scientific and Unity Lab Services. Thermo Scientific is its technology brand. Fisher Scientific is its channels brand. Its Unity Lab Services offers portfolio of enterprise services for instruments and laboratory equipment. Unity Lab Services offers a network of service and support personnel. In April 2012, it opened a new demonstration laboratory and training center in Seoul, South Korea. In May 2012, it acquired Doe & Ingalls Management, LLC. In September 2012, it acquired One Lambda, Inc. In September 2012, Thermo Fisher opened its Molecular Biology Center of Excellence in Vilnius, Lithuania. In October 2012, OpenGate Capital, LLC acquired the Laboratory (Lab) Workstations business from Thermo Fisher."},{"Symbol":"TIF","Name":"Tiffany & Co.","Sector":"Consumer Discretionary","Description":"Tiffany & Co. (Tiffany) is a holding company that operates through its subsidiary companies. The Company operates in five segments: Americas, Asia-Pacific, Japan, Europe and other. The Company's principal product category is jewelry, which represented 90% of worldwide net sales during the fiscal year ended January 31, 2013 (fiscal 2012). The Company offers a selection of TIFFANY & CO. brand jewelry. The Company also sells timepieces, leather goods, sterling silver goods (other than jewelry), china, crystal, stationery, fragrances and accessories, which represented, in total, 8% of worldwide net sales in 2012. The Company operates e-commerce enabled Websites in 13 countries. The Company produces jewelry in New York, Rhode Island and Kentucky, and silver hollowware in New Jersey."},{"Symbol":"TWX","Name":"Time Warner Inc.","Sector":"Consumer Discretionary","Description":"Time Warner Inc. (Time Warner) is a media and entertainment company. The Company operates in three reporting segments: Networks, Film and TV Entertainment and Publishing. Networks consist of television networks and premium pay and basic tier television services and digital media properties. Film and TV Entertainment consists of feature film, television, home video and videogame production and distribution. Publishing consists of magazine publishing. In August 2012, Turner Broadcasting System, Inc. acquired Bleacher Report."},{"Symbol":"TJX","Name":"TJX Companies Inc.","Sector":"Consumer Discretionary","Description":"The TJX Companies, Inc. (TJX) is the off-price apparel and home fashions retailer in the United States and worldwide. As of January 28, 2012, the Company operated in four business segments. It has two segments in the United States, Marmaxx (T.J. Maxx and Marshalls) and HomeGoods; one in Canada, TJX Canada (Winners, Marshalls and HomeSense) and one in Europe, TJX Europe (T.K. Maxx and HomeSense). As a result of the consolidation of the A.J. Wright chain, all A.J. Wright stores ceased operations by the end of February 2011. It completed the consolidation of A.J. Wright, converting 90 of the A.J. Wright stores to T.J. Maxx, Marshalls or HomeGoods banners and closed the remaining 72 stores, two distribution centers and home office. In December 2012, the Company acquired Sierra Trading Post, an off-price Internet retailer."},{"Symbol":"TMK","Name":"Torchmark Corp.","Sector":"Financials","Description":"Torchmark Corporation is an insurance holding company. The Company is financial services holding company whose affiliate Companies market life insurance and supplemental health insurance to middle-income Americans. The Company operates in life insurance, health insurance, and annuities segment. The Company's primary subsidiaries are American Income Life Insurance Company (American Income), Liberty National Life Insurance Company (Liberty), Globe Life And Accident Insurance Company (Globe), United American Insurance Company (United American), and Family Heritage Life Insurance Company of America (Family Heritage)."},{"Symbol":"TSS","Name":"Total System Services","Sector":"Information Technology","Description":"Total System Services, Inc. (TSYS), is a global payment solutions provider that provides services to financial and nonfinancial institutions. The Company operates in three segments: North America Services, International Services and Merchant Services. Through the Company's North America Services and International Services segments, TSYS processes information through its cardholder systems to financial institutions throughout the United States and internationally. Its North America Services segment provides these services to clients in the United States, Canada, Mexico and the Caribbean. The Company's International Services segment provides services to clients in Europe, India, Middle East, Africa, Asia Pacific and Brazil. In July 2013, Total System Services Inc completed its acquisition of NetSpend."},{"Symbol":"TSCO","Name":"Tractor Supply Company","Sector":"Consumer Discretionary","Description":"Tractor Supply Company is an operator of retail farm and ranch stores in the United States. The Company operates retail stores under the names Tractor Supply Company and Del's Farm Supply and operate a Website under the name TractorSupply.com. The Company's stores are located in towns outlying metropolitan markets and in rural communities, and offer a selection of merchandise, which include equine, pet and small animal products, including items necessary for their health, care, growth and containment; hardware, truck, towing and tool products; seasonal products, including lawn and garden items, power equipment, gifts and toys; maintenance products for agricultural and rural use, and work/recreational clothing and footwear. The Company operates at farm and ranch retail sales segment, both at its retail locations and online."},{"Symbol":"TDG","Name":"TransDigm Group","Sector":"Industrials","Description":"TransDigm Group Incorporated (TD Group), is a global designer, producer and supplier of aircraft components for use on commercial and military aircraft in service. Once its parts are designed into and sold on a new aircraft, it generates sales from aftermarket consumption over the life of that aircraft. On December 6, 2010, the Company's wholly owned subsidiary, TransDigm Inc., completed the acquisition of McKechnie Aerospace Holdings Inc. On December 31, 2010, the Company acquired the actuation business of Telair International, Inc. On March 9, 2011, the Company divested its fastener business. On April 7, 2011, the Company completed the divestiture of Aero Quality Sales (AQS) to Satair A/S. On August 31, 2011, the Company acquired Schneller Holdings LLC. In July 2013, TransDigm Group Inc acquired the assets of GE Aviation's Electromechanical Actuation Division."},{"Symbol":"RIG","Name":"Transocean","Sector":"Energy","Description":"Transocean Ltd. (Transocean) is an international provider of offshore contract drilling services for oil and gas wells. The Company operates in two segments: contract drilling services and drilling management services. Contract drilling services, the Company's primary business, involves contracting its mobile offshore drilling fleet, related equipment and work crews primarily on a dayrate basis to drill oil and gas wells. Its drilling management services segment provides oil and gas drilling management services on either a dayrate basis or a completed-project, fixed-price (or turnkey) basis, as well as drilling engineering and drilling project management services. As of February 14, 2012, it owned or had partial ownership interests in and operated 134 mobile offshore drilling units. On October 4, 2011, the Company acquired Aker Drilling ASA (Aker Drilling). In February 2011, it sold the subsidiary that owns the High-Specification Jackup Trident 20."},{"Symbol":"TRIP","Name":"TripAdvisor","Sector":"Consumer Discretionary","Description":"TripAdvisor, Inc. (TripAdvisor) is an online travel research company, enabling users to plan and have a trip. TripAdvisor features reviews and advice on hotels, resorts, flights, vacation rentals, vacation packages and travel guides. TripAdvisor's travel research platform features reviews and opinions from its community of travelers about destinations, accommodations, restaurants and activities worldwide, through its TripAdvisor brand. TripAdvisor Websites include tripadvisor.com in the United States and versions of the Website in 30 countries, including in China under the brand daodao.com. TripAdvisor Websites also include links to the Websites of its travel advertisers allowing travelers to directly book their travel arrangements. In May 2013, TripAdvisor Inc acquired key technology and talent from CruiseWise Inc. In May 2013, TripAdvisor Inc acquired Guia de Apartamentos Niumba SL. In June 2013, the Company announced that it has acquired GateGuru."},{"Symbol":"FOXA","Name":"Twenty-First Century Fox Class A","Sector":"Consumer Discretionary","Description":"Twenty-First Century Fox, Inc., formerly News Corporation, has a portfolio of cable, broadcast, film, pay television and satellite assets spanning six continents across the globe. The Company is home to a global portfolio of cable and broadcasting networks and properties, including FOX, FX, FXX, FS1, Fox News Channel, Fox Business Network, Fox Sports, Fox Sports Network, National Geographic Channels, Fox Pan American Sports, MundoFox, STAR and 28 local television stations; film studio Twentieth Century Fox Film; and television production studios Twentieth Century Fox Television and Shine Group. The Company also provides content to millions of subscribers through its pay-television services in Europe and Asia, including Sky Deutschland, Sky Italia and its equity interests in BSkyB and Tata Sky"},{"Symbol":"FOX","Name":"Twenty-First Century Fox Class B","Sector":"Consumer Discretionary","Description":"Twenty-First Century Fox, Inc., formerly News Corporation, has a portfolio of cable, broadcast, film, pay television and satellite assets spanning six continents across the globe. The Company is home to a global portfolio of cable and broadcasting networks and properties, including FOX, FX, FXX, FS1, Fox News Channel, Fox Business Network, Fox Sports, Fox Sports Network, National Geographic Channels, Fox Pan American Sports, MundoFox, STAR and 28 local television stations; film studio Twentieth Century Fox Film; and television production studios Twentieth Century Fox Television and Shine Group. The Company also provides content to millions of subscribers through its pay-television services in Europe and Asia, including Sky Deutschland, Sky Italia and its equity interests in BSkyB and Tata Sky"},{"Symbol":"TSN","Name":"Tyson Foods","Sector":"Consumer Staples","Description":"Tyson Foods, Inc. is a food production company. The Company produces, distributes and markets chicken, beef, pork, prepared foods and related allied products. The Company's operates in four segments: Chicken, Beef, Pork and Prepared Foods. The Company operates an integrated poultry production process. The Company's integrated operations consist of breeding stock, contract growers, feed production, processing, further-processing, marketing and transportation of chicken and related allied products, including animal and pet food ingredients. Through Cobb-Vantress, Inc. (Cobb), the Company supplies poultry breeding stock. The Company also processes live fed cattle and hogs and fabricate dressed beef and pork carcasses into primal and sub-primal meat cuts, case ready beef and pork and fully-cooked meats. In June 2013, Tyson Foods, Inc. announced the acquisition of assets of Circle Foods, LLC."},{"Symbol":"USB","Name":"U.S. Bancorp","Sector":"Financials","Description":"U.S. Bancorp (U.S. Bancorp) is a multi-state financial services holding company. It provides a range of financial services, including lending and depository services, cash management, foreign exchange and trust and investment management services. It also engages in credit card services, merchant and automated teller machine (ATM) processing, mortgage banking, insurance, brokerage and leasing. The Company operates in five segments: Wholesale Banking and Commercial Real Estate, Consumer and Small Business Banking, Wealth Management and Securities Services, Payment Services and Treasury and Corporate Support.In January 2012, the Company's bank, U.S. Bank National Association, acquired the banking operations of BankEast. In February 2013, Elavon, global payments provider and wholly owned subsidiary of the Company acquired Collective Point of Sale Solutions Ltd."},{"Symbol":"UDR","Name":"UDR Inc","Sector":"Real Estate","Description":"UDR, Inc. (UDR) is a self administered real estate investment trust (REIT), which owns, operates, acquires, renovates, develops, redevelops, and manages multifamily apartment communities located throughout the United States. UDR holds interests in other operating partnerships, subsidiaries and joint ventures, owns and operates properties, issues securities from time to time and guarantees debt of certain of its subsidiaries. As of December 31, 2012, UDR owned approximately 94.9% of the limited partnership interests of the Operating Partnership (the OP Units). During the year ended December 31, 2012, the Company acquired the remaining 80% ownership interests in two apartment communities (633 homes) in Austin, Texas. As of December 31, 2012, the Company is developing eight wholly owned communities with 2,622 apartment homes, 76 of which have been completed."},{"Symbol":"ULTA","Name":"Ulta Salon Cosmetics & Fragrance Inc","Sector":"Consumer Discretionary","Description":"Ulta Salon, Cosmetics & Fragrance, Inc. (Ulta) is a beauty retailer, which provides one-stop shopping for prestige, mass and salon products and salon services in the United States. During the year ended January 28, 2012 (fiscal 2011), the Company opened 61 new stores. It operates full-service salons in all of its stores. Its Ulta store format includes an open and modern salon area with approximately eight to 10 stations. The entire salon area is approximately 950 square feet with a concierge desk, skin treatment room, semi-private shampoo and hair color processing areas. Each salon is a full-service salon offering hair cuts, hair coloring and permanent texture, with salons also providing facials and waxing."},{"Symbol":"UA","Name":"Under Armour","Sector":"Consumer Discretionary","Description":"Under Armour, Inc. (Under Armour) is engaged in the development, marketing and distribution of apparel, footwear and accessories for men, women and youth. The Company's products are sold worldwide and are worn by athletes at all levels, from youth to professional, on playing fields worldwide. Most of its products are sold in North America. Internationally, Under Armour sells its products in certain countries in Europe, a third party licensee sells its products in Japan, and distributors sell the Company's products in other foreign countries. In addition, it opened it store in China during the year ended December 31, 2012. The Company's product offerings consist of apparel, footwear and accessories for men, women and youth. In November 2013, the Company acquired MapMyFitness."},{"Symbol":"UAA","Name":"Under Armour","Sector":"Consumer Discretionary","Description":""},{"Symbol":"UNP","Name":"Union Pacific","Sector":"Industrials","Description":"Union Pacific Corporation (UPC) owns transportation companies. Its principal operating company, Union Pacific Railroad Company, links 23 states in the western 66% of the country. Union Pacific Railroad Company's business mix includes agricultural products, automotive, chemicals, energy, industrial products and intermodal. Union Pacific Railroad Company connects with Canada's rail systems and is the railroad serving six gateways to Mexico. Union Pacific Railroad Company (UPRR) is a Class I railroad operating in the United States. In June 2012, the Company's wholly owned subsidiary, PS Technology (PST), acquired the Yard Control Systems division of Ansaldo STS USA."},{"Symbol":"UAL","Name":"United Continental Holdings","Sector":"Industrials","Description":"United Continental Holdings, Inc., together with its subsidiaries, provides air transportation services in North America, the Asia-Pacific, Europe, the Middle East, Africa, and Latin America. It transports people and cargo through its mainline operations, which use jet aircraft with at least 118 seats, and its regional operations. As of December 31, 2014, the company operated a fleet of 1,257 aircraft. It also sells fuel; and provides maintenance, ground handling, and catering services for third parties. The company was formerly known as UAL Corporation and changed its name to United Continental Holdings, Inc. in October 2010. United Continental Holdings, Inc. was founded in 1934 and is headquartered in Chicago, Illinois."},{"Symbol":"UNH","Name":"United Health Group Inc.","Sector":"Health Care","Description":"UnitedHealth Group Incorporated (UnitedHealth Group) diversified health and well-being company. The Company operates in four segments: UnitedHealthcare, which includes UnitedHealthcare Employer & Individual, UnitedHealthcare Medicare & Retirement and UnitedHealthcare Community & State; UnitedHealthcare Community & State; OptumHealth; OptumInsight, and OptumRx. The Company operates these businesses across eight markets: integrated care delivery, care management, consumer engagement and support, distribution of benefits and services, health financial services, operational services and support, health care information technology and pharmacy."},{"Symbol":"UPS","Name":"United Parcel Service","Sector":"Industrials","Description":"United Parcel Service, Inc. (UPS) is a package delivery company. The Company delivers packages each business day for 1.1 million shipping customers to 7.7 million consignees in over 220 countries and territories. During the year ended December 31, 2012, it delivered an average of 16.3 million pieces per day worldwide, or a total of 4.1 billion packages. It serves the global market for logistics services, which include transportation, distribution, forwarding, ground, ocean and air freight, brokerage and financing. The Company has three segments: U.S. Domestic Package, International Package and Supply Chain & Freight. In February 2012, it acquired Kiala S.A."},{"Symbol":"URI","Name":"United Rentals, Inc.","Sector":"Industrials","Description":"United Rentals, Inc. is a holding company. The Company primarily conducts its operations through its wholly owned subsidiary, United Rentals (North America), Inc., and its subsidiaries. The Company is an equipment rental company and its network consists of 836 rental locations in the United States and Canada. The Company operates in two segments: general rentals and trench safety, power and HVAC. The general rentals segment includes the rental of construction, aerial and industrial equipment, general tools and light equipment, and related services and activities. The trench safety, power and HVAC segment includes the rental of specialty construction products and related services. The trench safety, power and HVAC segment's customers include construction companies involved in infrastructure projects, municipalities and industrial companies. This segment operates throughout the United States and in Canada. On April 30, 2012, the Company acquired RSC Holdings Inc."},{"Symbol":"UTX","Name":"United Technologies","Sector":"Industrials","Description":"United Technologies Corporation (UTC) provides high technology products and services to the building systems and aerospace industries worldwide. The Company operates in six segments: Otis, Carrier, UTC Fire & Security, Pratt & Whitney, Hamilton Sundstrand and Sikorsky. Otis, Carrier and UTC Fire & Security serve customers in the commercial, government infrastructure and residential property sectors worldwide. Carrier also serves commercial, industrial, transport refrigeration and food service equipment customers. Pratt & Whitney, Hamilton Sundstrand and Sikorsky primarily serves commercial and government customers. In May 2013, the Company announced that it has completed the divestiture of its Pratt & Whitney Power Systems unit to Mitsubishi Heavy Industries Ltd (MHI). In June 2013, United Technologies Corp announced it has closed on the sale of substantially all operations of its Pratt & Whitney Rocketdyne unit to GenCorp."},{"Symbol":"UHS","Name":"Universal Health Services, Inc.","Sector":"Health Care","Description":"Universal Health Services, Inc., through its subsidiaries, owns and operates acute care hospitals, behavioral health centers, surgical hospitals, ambulatory surgery centers, and radiation oncology centers. The companys hospitals offer various services, including general and specialty surgery, internal medicine, obstetrics, emergency room care, radiology, oncology, diagnostic care, coronary care, pediatric services, pharmacy services, and/or behavioral health services. As of February 26, 2015, it owned and/or operated 24 acute care hospitals and 216 behavioral health centers located in 37 states, Washington, D.C.; the United Kingdom; Puerto Rico; and the U.S. Virgin Islands. Universal Health Services, Inc. was founded in 1978 and is headquartered in King of Prussia, Pennsylvania."},{"Symbol":"UNM","Name":"Unum Group","Sector":"Financials","Description":"Unum Group is a provider of disability insurance products in the United States and the United Kingdom. The Company also provides a portfolio of other insurance products, including employer- and employee-paid group benefits, life insurance, and other related services. The Company operates in five segments: Unum US, Unum UK, Colonial Life, Closed Block and Corporate. The principal operating subsidiaries in the United States are Unum Life Insurance Company of America (Unum America), Provident Life and Accident Insurance Company (Provident), The Paul Revere Life Insurance Company (Paul Revere Life), and Colonial Life & Accident Insurance Company, and in the United Kingdom, Unum Limited."},{"Symbol":"URBN","Name":"Urban Outfitters","Sector":"Consumer Discretionary","Description":"Urban Outfitters, Inc. (Urban Outfitters) is a lifestyle specialty retail company, which operates under the Urban Outfitters, Anthropologie, Free People, Terrain and BHLDN brands. The Company also operates a wholesale segment under the Free People brand. In addition to its retail stores, it offers its products and markets its brands directly to the consumer through its e-commerce Websites, www.urbanoutfitters.com, www.anthropologie.com,www.freepeople.com, www.urbanoutfitters.co.uk,www.urbanoutfitters.de, www.urbanoutfitters.fr, www.anthropologie.eu, www.shopterrain.com and www.bhldn.com and also through its Urban Outfitters, Anthropologie and Free People catalogs. Its Urban Outfitters stores, Websites and catalogs offer a range of eclectic merchandise, including women's and men's fashion apparel, footwear and accessories and an eclectic mix of apartment wares and gifts."},{"Symbol":"VFC","Name":"V.F. Corp.","Sector":"Consumer Discretionary","Description":"V.F. Corporation (VF) is a global apparel company based in the United States. The Company designs and manufactures or sources from independent contractors a variety of apparel and footwear for all ages, including jeanswear, outerwear, footwear, packs, luggage, sportswear, and occupational and performance apparel. Products are marketed primarily under VF-owned brand names. It is a diversified apparel company across brands, product categories, channels of distribution and geographies. These products are marketed to consumers shopping in specialty stores, upscale and traditional department stores, national chains and mass merchants. These groupings of businesses are called coalitions and consist of the following: Outdoor & Action Sports, Jeanswear, Imagewear, Sportswear and Contemporary Brands. On April 30, 2012, the Company sold its 80% ownership in John Varvatos Enterprises, Inc."},{"Symbol":"VLO","Name":"Valero Energy","Sector":"Energy","Description":"Valero Energy Corporation (Valero) is an independent petroleum refining and marketing company. Valero's refineries can produce conventional gasoline's, distillates, jet fuel, asphalt, petrochemicals, lubricants, and other refined products, as well as a slate of premium products, including conventional blendstock for oxygenate blending and reformulated gasoline blendstock for oxygenate blending, gasoline meeting the specifications of the California Air Resources Board, a diesel fuel, and low-sulfur and ultra-low-sulfur diesel fuel. It also owns 10 ethanol plants in the central plains region of the United States with a combined ethanol nameplate production capacity of about 1.1 billion gallons per year. It operates in three business segments: refining, ethanol, and retail. In May 2013, CST Brands Inc announced that the Company which includes Corner Store and Depanneur du Coin, spun off from Valero Energy Corporation."},{"Symbol":"VAR","Name":"Varian Medical Systems","Sector":"Health Care","Description":"Varian Medical Systems, Inc. is engaged in the design, manufacture, sale and service of equipment and software products for treating cancer with radiotherapy, stereotactic radiotherapy, stereotactic body radiotherapy (SBRT), stereotactic radiosurgery (SRS) and brachytherapy. The Company also designs, manufactures, sells and services x-ray tubes for original equipment manufacturers (OEMs); replacement x-ray tubes, and flat panel digital image detectors for filmless x-ray imaging (flat panel detectors or digital image detectors) in medical, dental, veterinary, scientific and industrial applications. It designs, manufactures, sells and services linear accelerators, digital image detectors, image processing software and image detection products for security and inspection purposes. In March 2011, it acquired a privately-held supplier of devices for delivery of brachytherapy treatment of cancer. In August 2012, the Company established a subsidiary, Varian Medical Systems Korea."},{"Symbol":"VTR","Name":"Ventas Inc","Sector":"Real Estate","Description":"Ventas, Inc. is a real estate investment trust (REIT) with a geographically diverse portfolio of seniors housing and healthcare properties throughout the United States and Canada. As of December 31, 2011, the Company operated through three business segments: triple-net leased properties, senior living operations and MOB operations. As of December 31, 2011, the Company owned 1,378 properties located in 46 states, the District of Columbia and two Canadian provinces, consisting of 678 seniors housing communities; 396 skilled nursing facilities; 47 hospitals; 249 medical office buildings (MOBs), and eight personal care facilities. The Company was in the process of developing three properties as of December 31, 2011. In July 2011, it acquired Nationwide Health Properties, Inc. (NHP). In June 2012, the Company sold 12 seniors housing communities to Assisted Living Concepts, Inc."},{"Symbol":"VRSN","Name":"Verisign Inc.","Sector":"Information Technology","Description":"VeriSign, Inc. (VeriSign) is a provider of Internet infrastructure services. The Company provides network confidence and availability for mission-critical Internet services, such as domain name registry services and infrastructure assurance services. Its service capabilities enable real-time name resolution for a number of global top level domains (TLDs), enable domain name registration through registrars, and provide security intelligence and cloud-based network availability services to enterprise customers. It has one reportable segment is Naming Services, which consists of Registry Services and Network Intelligence and Availability (NIA) Services. It has operations inside as well as outside the United States (U.S.). Registry Services operates the authoritative directory of all .com, .net, .cc, .tv, and .name domain names and the back-end systems for all .gov, .jobs and .edu domain names."},{"Symbol":"VRSK","Name":"Verisk Analytics","Sector":"Industrials","Description":"Verisk Analytics, Inc. (Verisk) is a provider of information about risk to professionals in insurance, healthcare, mortgage, government, supply chain, and risk management. Verisk enable risk-bearing businesses to understand and manage their risks. The Company provides its customers by supplying data that, combined with its analytic methods, creates embedded decision support solutions. Verisk organizes its business in two segments: Risk Assessment and Decision Analytics. Its Risk Assessment segment provides statistical, actuarial and underwriting data for the United States property and casualty (P&C) insurance industry. Its Decision Analytics segment provides solutions its customers use to analyze the processes of the Verisk Risk Analysis Framework: Prediction of Loss, Detection and Prevention of Fraud, and Quantification of Loss. On June 17, 2011, it acquired the net assets of Health Risk Partners, LLC. On April 27, 2011, it acquired 100% interest of Bloodhound Technologies, Inc."},{"Symbol":"VZ","Name":"Verizon Communications","Sector":"Telecommunications Services","Description":"Verizon Communications Inc. (Verizon) is a holding company. The Company is a provider of communications, information and entertainment products and services to consumers, businesses and governmental agencies. It operates in two primary segments: Verizon Wireless and Wireline. Verizon Wireless' communications products and services include wireless voice and data services and equipment sales, which are provided to consumer, business and government customers across the United States. Wireline's communications products and services include voice, Internet access, broadband video and data, Internet protocol network services, network access, long distance and other services. In November 2013, Verizon Communications Inc's Verizon Digital Media Services acquired upLynk, a technology and television cloud company."},{"Symbol":"VRTX","Name":"Vertex Pharmaceuticals Inc","Sector":"Health Care","Description":"Vertex Pharmaceuticals Incorporated (Vertex) is in the business of discovering, developing, manufacturing and commercializing small molecule drugs for the treatment of serious diseases. The Company's two products are INCIVEK (telaprevir), which is approved for the treatment of patients with genotype 1 hepatitis C virus (HCV), infection, and KALYDECO (ivacaftor), which is approved in the United States for the treatment of patients six years of age and older with cystic fibrosis (CF), who have at least one copy of the G551D mutation in the cystic fibrosis transmembrane conductance regulator (CFTR), gene. The Company has ongoing clinical programs involving drug candidates intended for the treatment of HCV infection, CF, rheumatoid arthritis, influenza and epilepsy. INCIVEK (telaprevir) achieved initial commercial acceptance following its approval in May 2011. It started marketing KALYDECO in the United States in January 2012."},{"Symbol":"VIAB","Name":"Viacom Inc.","Sector":"Consumer Discretionary","Description":"Viacom Inc. (Viacom) is an entertainment content company that connects with audiences through compelling content across television, motion picture, online and mobile platforms in over 160 countries and territories. The Company operates in two segments: Media Networks and Filmed Entertainment. The Company's Media Networks segment provides entertainment content and related branded products for consumers in targeted demographics attractive to advertisers, content distributors and retailers. The Company's Filmed Entertainment segment produces finances and distributes motion pictures and other entertainment content under the Paramount Pictures, Paramount Vantage, Paramount Classics, Insurge Pictures, MTV Films and Nickelodeon Movies brands. In September 2013, Telecom Italia Media SpA announced that the sale of the 51% stake of MTV Italia Srl to Viacom Inc. has been completed."},{"Symbol":"V","Name":"Visa Inc.","Sector":"Information Technology","Description":"Visa Inc. (Visa) is a global payments technology company that connects consumers, businesses, banks and governments in more than 200 countries and territories to fast, secure and reliable electronic payments. The Company operates processing networks, VisaNet, offering fraud protection for consumers and assured payment for merchants. The Company operates an open-loop payments network, a multi-party system in which Visa connects issuing financial institutions, or issuers, that issue cards to cardholders, and acquiring financial institutions, or acquirers, that have the banking relationship with merchants-and manage the exchange of information and value between them. The Company derives revenues primarily from fees paid by its clients based on payments volume, transactions that it processes and other related services the Company provides. Its clients deliver Visa products and payment services to consumers and merchants based on product platforms it defines and manages."},{"Symbol":"VNO","Name":"Vornado Realty Trust","Sector":"Real Estate","Description":"Vornado Realty Trust (Vornado) is an integrated real estate investment trust (REIT). Vornado conducts its business through, and substantially all of its interests in properties are held by, Vornado Realty L.P., a limited partnership (the Operating Partnership). As of December 31, 2011, the Company was the sole general partner of, and owned approximately 93.5% of the common limited partnership interest in the Operating Partnership. Vornado operates in five business segments: New York Office Properties, Washington, DC Office Properties, Retail Properties, Merchandise Mart Properties and Toys R Us (Toys). In April 2013, it completed the sale of The Plant, a power strip shopping center in San Jose, California. In April 2013, the Company announced that the sale of a site located in the Downtown Crossing district of Boston. In October 2013, the Company sold a parcel of land known as Harlem Park located at 1800 Park Avenue (at 125th Street) in New York City."},{"Symbol":"VMC","Name":"Vulcan Materials","Sector":"Materials","Description":"Vulcan Materials Company is a producer of construction aggregates primarily crushed stone, sand, and gravel. The Company is a producer of aggregates-based construction materials including asphalt and ready-mixed concrete. The Company produces these materials from natural deposits such as granite, limestone and trap rock. The Company operates in four segments: Aggregates, Concrete, Asphalt Mix, And Cement. The Company has 341 active aggregates facilities. The Company also is a producer of asphalt mix and ready-mixed concrete, as well as a producer of cement in Florida. The Company is a construction materials business in the country with aggregates operations. Its construction aggregates are used in water filtration, purification and erosion control, as base material underneath highways, walkways, airport runways, parking lots and railroads."},{"Symbol":"WMT","Name":"Wal-Mart Stores","Sector":"Consumer Staples","Description":"Wal-Mart Stores, Inc. (Walmart) operates retail stores in various formats globally. The Company operates in three segments: the Walmart U.S., the Walmart International and the Sam's Club. During the fiscal year ended January 31, 2013 (fiscal 2013), its Walmart U.S. segment accounted for approximately 59% of its net sales and operated retail stores in various formats in all 50 states in the United States and Puerto Rico, as well as Walmart's online retail operations, walmart.com. Its Walmart International segment consists of retail operations in 26 countries. In fiscal 2013, this segment generated approximately 29% of its net sales. The Walmart International segment includes a range of formats of retail stores, restaurants, Sam's Clubs and online retail operations, which operate outside the United States. Sam's Club segment consists of membership warehouse clubs operated in 47 states in the United States and Puerto Rico, as well as the segment's online retail operations, samsclub.com."},{"Symbol":"WBA","Name":"Walgreens Boots Alliance","Sector":"Consumer Staples","Description":"Walgreens Boots Alliance, Inc., together with its subsidiaries, operates a network of drugstores in the United States. It provides consumer goods and services, pharmacy, and health and wellness services through drugstores, as well as through mail, and by telephone and online. The company sells prescription and non-prescription drugs; and general merchandise, including convenience and fresh foods, household items, personal care, photofinishing and candy, and beauty care. It also provides specialty pharmacy services for managing complex and chronic health conditions; customers infusion therapy services consisting of administration of intravenous medications for cancer treatments, chronic pain, heart failure, and other infections and disorders; and clinical services, such as laboratory monitoring, medication profile review, nutritional assessments, and patient and caregiver education. In addition, the company manages in-store convenient care clinics (Healthcare Clinics). As of October 20, 2014, it operated 8,207 locations in 50 states, the District of Columbia, Puerto Rico, and the U.S. Virgin Islands. The company also managed approximately 400 Healthcare Clinics and provider practice locations. The company was founded in 1901 and is based in Deerfield, Illinois."},{"Symbol":"WM","Name":"Waste Management Inc.","Sector":"Industrials","Description":"Waste Management, Inc. (WM) is a provider of waste management services in North America. WM's subsidiaries provide collection, transfer, recycling, and disposal services. WM is a developer, operator and owner of waste-to-energy and landfill gas-to-energy facilities in the United States. Its customers include residential, commercial, industrial and municipal customers throughout North America. Its segments include Eastern, Midwest, Southern, Western and Wheelabrator Groups. The Oakleaf operations are included in other. The services the Company provides include collection, landfill, transfer, waste-to-energy facilities and independent power production plants, recycling and other services. In January 2013, its subsidiary, WM Recycle America, L.L.C., acquired Greenstar, LLC. Effective August 1, 2013, Waste Management Inc acquired Summit Energy Services, and concurrently, WM acquired Liquid Logistics. Effective August 1, 2013, Waste Management Inc acquired Oak Grove Disposal Co."},{"Symbol":"WAT","Name":"Waters Corporation","Sector":"Health Care","Description":"Waters Corporation (Waters) is an analytical instrument manufacturer that primarily designs, manufactures, sells and services, through its Waters Division, high performance liquid chromatography (HPLC), ultra performance liquid chromatography (UPLC and together with HPLC, referred to as LC) and mass spectrometry (MS) technology systems and support products, including chromatography columns, other consumable products and post-warranty service plans. Through its TA Division, the Company primarily designs, manufactures, sells and services thermal analysis, rheometry and calorimetry instruments. The Company is also a developer and supplier of software-based products that interface with the Company's instruments and are typically purchased by customers as part of the instrument system. The Company operates in two segments: Waters Division and TA Division. In January 2012, the Company acquired Baehr Thermoanalyse GmbH. In July 2012, the Company acquired Blue Reference, Inc."},{"Symbol":"WEC","Name":"Wec Energy Group Inc","Sector":"Utilities","Description":"Wisconsin Energy Corporation is a diversified holding company. The Company operates primarily through two segments: a utility energy segment and a non-utility energy segment. Its primary subsidiaries are Wisconsin Electric Power Company (Wisconsin Electric), Wisconsin Gas LLC (Wisconsin Gas) and W.E. Power, LLC (We Power). Its utility energy segment consists of Wisconsin Electric and Wisconsin Gas, operating together under the trade name of We Energies. Its non-utility energy segment derives its revenues primarily from the ownership of electric power generating facilities for long-term lease to Wisconsin Electric. Its non-utility energy segment derives its revenues primarily from the ownership of electric power generating facilities for long-term lease to Wisconsin Electric. As of December 31, 2012, the Company have a 26.2% interest in ATC."},{"Symbol":"WFC","Name":"Wells Fargo","Sector":"Financials","Description":"Wells Fargo & Company is a bank holding company. It has three operating segments: Community Banking, Wholesale Banking and Wealth, and Brokerage and Retirement. It provides retail, commercial and corporate banking services through banking stores and offices, the Internet and other distribution channels to individuals, businesses and institutions in all 50 states, the District of Columbia and in other countries. The Company provides other financial services through subsidiaries engaged in various businesses, principally wholesale banking, mortgage banking, consumer finance, equipment leasing, agricultural finance, commercial finance, securities brokerage and investment banking, insurance agency and brokerage services, computer and data processing services, trust services, investment advisory services, mortgage-backed securities servicing and venture capital investment. Effective November 21, 2013, Wells Fargo & Co acquired an undisclosed minority interest in Grameen America Inc."},{"Symbol":"HCN","Name":"Welltower Inc.","Sector":"Real Estate","Description":"Health Care REIT, Inc. is a real estate investment trust (REIT). The Company's portfolio spans the full spectrum of seniors housing and health care real estate, including seniors housing communities, skilled nursing/post-acute facilities, medical office buildings, inpatient and outpatient medical centers and life science facilities. The Company operates in three segments: seniors housing triple-net, seniors housing operating and medical facilities. Its properties are primarily comprised of land, building, improvements and related rights. The Company's hospitals and seniors housing triple-net properties are leased to operators under long-term operating leases. Its medical office building portfolio is primarily self-managed and consists principally of multi-tenant properties leased to health care providers. In October 2013, the Company announced the acquisition of the Willows at Hamburg from Health Care REIT, Inc."},{"Symbol":"WDC","Name":"Western Digital","Sector":"Information Technology","Description":"Western Digital Corporation (Western Digital) is a developer and manufacturer of storage solutions, which enables people to create, manage, experience and preserve digital content. The Company designs and makes storage devices and home entertainment products under the HGST, WD and G-Technology brands. The Company's principal products are hard drives that use one or more rotating magnetic disks (magnetic media) to store and allow fast access to data. Hard drives are primary storage medium for digital content. The Company operates its global business through two independent subsidiaries: Hitachi Global Storage Technologies Holdings Pte. Ltd. (HGST) and Western Digital Corporation (WD). In September 2013, the Company announced that it has completed the acquisition of sTec, Inc. In October 2013, Western Digital Corporation completed the acquisition of Virident Systems, Inc."},{"Symbol":"WU","Name":"Western Union Co","Sector":"Information Technology","Description":"The Western Union Company (Western Union) is engaged in money movement and payment services. The Company's business payments service provides consumers and businesses with options for making one-time or recurring bill payments, including business-to-business payment transactions, which are primarily cross-border, cross currency transactions. Its segments are consumer-to-consumer and global business payments. Its other businesses not included in these segments primarily consist of Western Union money order and prepaid services available through a network of third-party agents. In April 2011, the Company acquired Angelo Costa, S.r.l. In August 2011, the Company formed Western Union Ventures. In October 2011, the Company acquired Finint S.r.l. In November 2011, the Company acquired the business-to-business payment business of Travelex Holdings Limited, known as Travelex Global Business Payments."},{"Symbol":"WRK","Name":"WestRock Company","Sector":"Materials","Description":"WestRock Company manufactures and sells paper and packaging solutions for consumer and corrugated markets in North America, South America, Europe, and the Asia-Pacific. It offers folding cartons for use in various food and non-food applications; superior pumps, sprayers, and dispensing closures for home cleaning, healthcare, and beauty and personal care products; and corrugated containers for home appliances, electric motors, small machinery, produce, books, furniture, and other products. The company also provides beverage multipacks for beer, carbonated soft drink, and other beverage markets; merchandising displays; chipboard partitions and other protective packaging, such as custom partitions, book cover board, single ply chip, and gypsum; and coated white top linerboard, white top linerboard, virgin and recycled linerboard, corrugated medium, kraft paper, and pulp products. In addition, it offers various paperboard products, including solid bleached sulfate, coated and uncoated recycled board, and coated and uncoated unbleached kraft. Further, the company is involved in recovering, processing, and selling recycled paperboard; developing master planned communities, and industrial and business campuses, as well as recreational/investment land sales in South Carolina and Georgia; and supplying specialty chemicals products and distinctive technologies in asphalt innovations, oilfield and pine chemicals, and activated carbon. WestRock Company is based in Richmond, Virginia."},{"Symbol":"WY","Name":"Weyerhaeuser Corp.","Sector":"Real Estate","Description":"Weyerhaeuser Company, formerly Weyerhaeuser Timber Company, is a forest products company. The Company is engaged in growing and harvesting trees, builds homes and making a range of forest products. As of December 31, 2010, it had offices or operations in 11 countries and have customers worldwide. It manages 20.3 million acres of forests, of which it owns 5.7 million acres, lease 0.7 million acres and has renewable, long-term licenses on 13.9 million acres. The Company operates in five business segments: Timberlands, Wood Products, Cellulose Fibers, Real Estate and Corporate and Other. In August 1, 2011, the Company sold its hardwoods business to American Industrial Partners. On September 30, 2011, the Company sold its Westwood Shipping Lines. Effective July 23, 2013, Weyerhaeuser Co acquired the entire share capital of Longview Timber LLC, a Longview-based owner and operator of timberlands, from Brookfield Asset Management Inc."},{"Symbol":"WHR","Name":"Whirlpool Corp.","Sector":"Consumer Discretionary","Description":"Whirlpool Corporation (Whirlpool) is a manufacturer and marketer of home appliances. Whirlpool operates in four segments: North America, Latin America, EMEA (Europe, Middle East and Africa) and Asia. Whirlpool manufactures and markets a line of home appliances and related products. Its principal products are laundry appliances, refrigerators and freezers, cooking appliances, dishwashers, mixers and other portable household appliances. The Company also produces hermetic compressors for refrigeration systems. It is a producer of home appliances in North America and Latin America and has a presence in markets throughout Europe and India. Its portfolio of brands includes Whirlpool, Maytag, KitchenAid, Brastemp and Consul."},{"Symbol":"WFM","Name":"Whole Foods Market","Sector":"Consumer Staples","Description":"Whole Foods Market, Inc. (Whole Foods Market) is a natural and organic foods supermarkets. As of September 25, 2011, Whole Foods Market operated 311 stores in the United States, Canada, and the United Kingdom. The Company's stores average 38,000 square feet in size and 10 years in age, and are supported by its Austin headquarters, regional offices, distribution centers, bakehouse facilities, commissary kitchens, seafood-processing facilities, meat and produce procurement centers, and a specialty coffee, tea procurement and roasting operation. As of September 25, 2011, it operated 311 stores, of which 299 stores operated in 38 United States and the District of Columbia; seven stores in Canada; and five stores in the United Kingdom. It owns 12 stores, two distribution facilities and land for one store in development, including the adjacent property. It also owns a building on leased land, which is leased to third parties, and has one store in development on leased land."},{"Symbol":"WMB","Name":"Williams Cos.","Sector":"Energy","Description":"The Williams Companies, Inc. (Williams) is an energy infrastructure company focused on connecting North America's hydrocarbon resource plays to markets for natural gas, natural gas liquids (NGLs), and olefins. Its operations span from the deepwater Gulf of Mexico to the Canadian oil sands. It operates in three segments: Williams Partners, Midstream Canada & Olefins and Other. Its interstate gas pipeline and domestic midstream interests are held through its investment in Williams Partners L.P. (WPZ). It owns the general-partner interest and a 70% limited-partner interest in WPZ. Williams also owns a Canadian midstream and domestic olefins production business, which processes oil sands off-gas and produces olefins for petrochemical feedstocks. In November 2012, Williams Partners LP acquired Williams Companies Inc's approximately 83% interest in the Geismar olefins production facility In December 2012, it acquired Access Midstream Partners GP, L.L.C. and Access Midstream Partners LP."},{"Symbol":"WLTW","Name":"Willis Towers Watson","Sector":"Financials","Description":"Willis Towers Watson (NASDAQ:WLTW ) is a leading global advisory, broking and solutions company that helps clients around the world turn risk into a path for growth. With roots dating to 1828, Willis Towers Watson has 39,000 employees in more than 120 territories. We design and deliver solutions that manage risk, optimize benefits, cultivate talent, and expand the power of capital to protect and strengthen institutions and individuals. Our unique perspective allows us to see the critical intersections between talent, assets and ideas  the dynamic formula that drives business performance. Together, we unlock potential. Learn more at http://www.willistowerswatson.com."},{"Symbol":"WYN","Name":"Wyndham Worldwide","Sector":"Consumer Discretionary","Description":"Wyndham Worldwide Corporation is a hospitality company. The Company offers individual consumers and business customers an array of hospitality services and products across various accommodation alternatives and price ranges through its portfolio of brands. It operates in three segments of the hospitality industry: lodging, vacation exchange and rentals and vacation ownership. Its brands include Wyndham Hotels and Resorts, Tryp by Wyndham, Ramada, Days Inn, Super 8, Landal GreenParks, Novasol, Hoseasons, cottages4you, James Villa Holidays, ResortQuest by Wyndham Vacation Rentals, The Resort Company by Wyndham Vacation Rentals, Wyndham Vacation Resorts and WorldMark by Wyndham. In September 2012, its subsidiary, Wyndham Vacation Ownership, acquired Shell Vacations LLC (Shell) and its subsidiaries. In October 2012, the Company's Wyndham Hotel Group announced the opening of the newly constructed, 250-room TRYP by Wyndham Bogota Embajada, the newest TRYP by Wyndham(SM) hotel in Colombia."},{"Symbol":"WYNN","Name":"Wynn Resorts Ltd","Sector":"Consumer Discretionary","Description":"Wynn Resorts, Limited is a developer, owner and operator of destination casino resorts. The Company owns and operates two destination casino resorts. In Las Vegas, Nevada, the Company owns and operates Wynn Las Vegas, which includes Encore at Wynn Las Vegas. In the Macau Special Administrative Region of the People's Republic of China (Macau) the Company owns and operates Wynn Macau, which includes Encore at Wynn Macau. The Company has two segments: Las Vegas Operations and Macau Operations. The Company's Macau Operations feature approximately 1,008 hotel rooms and suites, 495 table games, 835 slot machines. Company's Las Vegas Operations feature approximately 4,750 hotel rooms and suites, 240 table games, 2,195 slot machines."},{"Symbol":"XEL","Name":"Xcel Energy Inc","Sector":"Utilities","Description":"Xcel Energy Inc. is a holding company with subsidiaries engaged primarily in the utility business. During the year ended December 31, 2012, the Company's operations included the activity of four wholly owned utility subsidiaries that serve electric and natural gas customers in eight states. These utility subsidiaries are NSP-Minnesota, NSP-Wisconsin, PSCo and SPS, and serve customers in portions of Colorado, Michigan, Minnesota, New Mexico, North Dakota, South Dakota, Texas and Wisconsin. Along with WYCO, a joint venture formed with CIG to develop and lease natural gas pipelines, storage, and compression facilities, and WGI, an interstate natural gas pipeline company, these companies comprise the continuing regulated utility operations."},{"Symbol":"XRX","Name":"Xerox Corp.","Sector":"Information Technology","Description":"Xerox Corporation provides services and technology to enable its customers from small businesses to large global enterprises to focus on their core business. The Company is a diversified business process outsourcing company managing transaction-intensive processes. This includes services which support all enterprises through offerings, such as customer care, finance and accounting and human resources, as well as vertically focused offerings in areas, such as healthcare, transportation, retail and telecommunications, among others. It is engaged in designing, developing and delivering information technology (IT) solutions that leverage its secure data centers, help desks and managed storage facilities around the world to provide IT infrastructure. The Company operates in three segments: Services, Document Technology and Other. Effective August 13, 2013, Xerox Corp acquired CPAS Systems Inc."},{"Symbol":"XLNX","Name":"Xilinx Inc","Sector":"Information Technology","Description":"Xilinx, Inc. (Xilinx) designs and develops programmable devices and associated technologies. The associated technologies, the Company develops includes integrated circuits (ICs) in the form of programmable logic devices (PLDs), including programmable System on Chips (SoCs) and three dimensional ICs (3D) ICs; software design tools to program the PLDs; targeted reference designs; printed circuit boards, and intellectual property (IP), which consists of Xilinx and various third-party verification and IP cores. In addition to its programmable platforms, the Company provides design services, customer training, field engineering and technical support. The Company sells its products and services through independent domestic and foreign distributors and through direct sales to original equipment manufacturers (OEMs) and electronic manufacturing service providers (EMS)."},{"Symbol":"XL","Name":"XL Capital","Sector":"Financials","Description":"XL Group plc, through its subsidiaries, is a global insurance and reinsurance company engaged in providing property, casualty and specialty products to industrial, commercial and professional firms, insurance companies and other enterprises on a worldwide basis. The Company is organized into three operating segments: Insurance, Reinsurance and Life operations."},{"Symbol":"XYL","Name":"Xylem Inc.","Sector":"Industrials","Description":"Xylem Inc. (Xylem), formerly ITT WCO, Inc., is a provider of equipment and service for water and wastewater applications with a portfolio of products and services addressing the full cycle of water, from collection, distribution and use to the return of water to the environment. It operates in two segments: Water Infrastructure and Applied Water. The Water Infrastructure segment focuses on the transportation, treatment and testing of water, offering a range of products, including water and wastewater pumps, treatment and testing equipment, and controls and systems. The Applied Water segment encompasses the uses of water and focuses on the residential, commercial, industrial and agricultural markets. The segment's products include pumps, valves, heat exchangers, controls and dispensing equipment. In March 2013, it acquired MultiTrode Pty Ltd."},{"Symbol":"YHOO","Name":"Yahoo Inc.","Sector":"Information Technology","Description":"Yahoo! Inc. (Yahoo!) is a digital media company. Through the Company's technology and insights, Yahoo! delivers digital content and experiences, across devices and globally. The Company provides online properties and services (Yahoo! Properties) to users, as well as a range of marketing services designed to reach and connect with those users on Yahoo! and through a distribution network of third-party entities (Affiliates). These Affiliates integrate its advertising offerings into their Websites or other offerings (those Websites and other offerings, Affiliate sites). Its offerings to users on Yahoo! Properties fall into three categories: Communications and Communities, Search and Marketplaces, and Media. In October 2013, Yahoo! Inc acquired Hitpost Inc. In October 2013, Yahoo! Inc acquired Bread Labs Inc."},{"Symbol":"YUM","Name":"Yum! Brands Inc","Sector":"Consumer Discretionary","Description":"YUM! Brands, Inc. (YUM) is a quick service restaurant company based on number of system units, with over 39,000 units in more than 125 countries and territories. The Company, through three concepts of KFC, Pizza Hut and Taco Bell (Concepts) develops, operates, franchises and licenses a worldwide system of restaurants, which prepare, package and sell a menu of priced food items. The Company operates in six segments: YUM Restaurants China (China or China Division), YUM Restaurants International (YRI or International Division), Taco Bell U.S., KFC U.S., Pizza Hut U.S. and YUM Restaurants India (India or India Division). The China Division includes mainland China, and the India Division includes India, Bangladesh, Mauritius, Nepal and Sri Lanka. YRI includes the remainder of its international operations."},{"Symbol":"ZBH","Name":"Zimmer Biomet Holdings","Sector":"Health Care","Description":"Zimmer Biomet Holdings, Inc., together with its subsidiaries, designs, develops, manufactures, and markets orthopaedic reconstructive devices, spinal and trauma devices, biologics, dental implants, and related surgical products in the Americas, Europe, and the Asia Pacific. It offers orthopaedic reconstructive devices that restore function lost due to disease or trauma in joints comprising knees, hips, shoulders, and elbows; dental reconstructive implants, which restore function and aesthetics in patients who have lost teeth due to trauma or disease; spinal devices that are utilized by orthopedic surgeons and neurosurgeons for the treatment of degenerative diseases, deformities, and trauma in various regions of the spine; and trauma devices used primarily to reattach or stabilize damaged bone and tissue to support the bodys natural healing process. The company also provides related surgical products, such as surgical supplies and instruments to aid in orthopedic surgical procedures and post-operation rehabilitation; and other healthcare related services. It primarily serves orthopedic surgeons, neurosurgeons, oral surgeons, dentists, hospitals, stocking distributors, and healthcare dealers, as well as agents, healthcare purchasing organizations, or buying groups. The company was formerly known as Zimmer Holdings, Inc. and changed its name to Zimmer Biomet Holdings, Inc. in June 2015. Zimmer Biomet Holdings, Inc. was founded in 1927 and is headquartered in Warsaw, Indiana."},{"Symbol":"ZION","Name":"Zions Bancorp","Sector":"Financials","Description":"Zions Bancorporation is a financial holding company. The Company focuses on providing community banking services by continuously strengthening its core business lines of small and medium-sized business and corporate banking; commercial and residential development, construction and term lending; retail banking; treasury cash management and related products and services; residential mortgage; trust and wealth management, and investment activities. The Company is a bank holding company. The Company owns and operates eight commercial banks with a total of 480 domestic branches in ten western and southwestern states as of December 31, 2012. The Company provides a range of banking and related services through its banking and other subsidiaries, primarily in Utah, California, Texas, Arizona, Nevada, Colorado, Idaho, Washington, and Oregon. As of December 31, 2012, net loans and leases accounted for 67.9% of total assets."},{"Symbol":"ZTS","Name":"Zoetis","Sector":"Health Care","Description":"Zoetis Inc, is engaged in the discovery, development, manufacture and commercialization of animal health medicines and vaccines, with a focus on both livestock and companion animals. The primary livestock species are cattle (both beef and dairy), swine, poultry, sheep and fish, and the primary companion animal species are dogs, cats and horses. The Company's more than 300 product lines include vaccines, parasiticides, anti-infectives, medicated feed additives and other pharmaceutical products. The Company's product portfolio also includes businesses, such as diagnostics, genetics, devices and services, such as dairy data management, e-learning and professional consulting. The Company operates in North America, Europe, Africa, Asia, Australia and Latin America."}];

